<template lang="html">
  <div class="layout-content-main">
    <div class="">
      <Card :bordered="false">
        <p slot="title">Seminar Hasil</p>
        <span slot="extra">
          <router-link v-if="bisa_seminar_hasil" to="/Seminar-Hasil/Pengajuan">
            <Button type="primary" icon="plus">
              Ajukan Seminar Hasil
            </Button>
          </router-link>
        </span>
        <Spin v-if="ajax_fetch_data"></Spin>
        <template v-else>
          <div v-if="tesis.length == 0" class="tidak-ada-data">
            <div style="text-align:center">
              <Icon type="document-text"></Icon>
              <br>
              <span>Data Tesis tidak ditemukan, <br>Seminar Hasil Belum Bisa Diajukan</span>
            </div>
          </div>
          <template v-else>
            <div v-if="seminar_hasil.length == 0" class="tidak-ada-data">
              <div style="text-align:center">
                <Icon type="document-text"></Icon>
                <br>
                <span>Data Seminar Hasil tidak ditemukan</span>
              </div>
            </div>
            <card-tesis
            v-else
            v-for="(item,index) in tesis"
            key="index"
            :prop_data="item"
            :prop_ujian="seminar_hasil"
            prop_tipe="seminar-hasil">
            </card-tesis>
          </template>
        </template>
        </Card>
    </div>
  </div>
</template>

<script>
// import SeminarHasilCard from './Seminar_Hasil_Card.vue';
import CardTesis from './Card_Tesis.vue';
export default {
  name: 'SeminarHasilList',
  props: {

  },
  //variable
  data: function(){
    return{
      ajax_fetch_data: '',
      seminar_hasil: '',
      tesis: '',
      status: this.$store.state.profile.status_seminar_hasil,
      // tipe_progres_seminar_hasil: '',
    }
  },
  computed: {
    bisa_seminar_hasil: function(){
      if(this.status == 'pengajuan baru'){
        return true;
      }
      else{
        return false;
      }
    },
  },
  components: {
    // SeminarHasilCard,
    CardTesis
  },
  //methods
  watch: {

  },
  methods: {
    get_tesis_by_nim: function(){
      console.log('get tesis from ajax');
      let nim = this.$store.state.profile.mahasiswa.nim_mahasiswa;
      this.ajax_fetch_data = true;
      var self = this;
      this.$store.dispatch('get_tesis_by_nim',nim).then((tesis) => {
        this.tesis = tesis;
        if(this.tesis.length != 0){
          this.get_seminar_hasil();
        }
        else{
          this.ajax_fetch_data = false;
        }
      });
    },
    get_seminar_hasil: function(){
      var self = this;
      this.tesis.forEach(function(val,index,item){
        var args = {};
        args.id_tesis = item[index].id_tesis;
        args.tipe_ujian = 'SH'
        self.$store.dispatch('get_ujian_by_id_tesis',args).then((seminar_hasil) => {
          self.seminar_hasil = seminar_hasil;
        });
      });
      this.ajax_fetch_data = false;
    }
  },
  //components life cycle hook
  created: function(){
    this.get_tesis_by_nim();
  }
}
</script>

<style lang="css">
</style>



