<script>
import { Doughnut } from 'vue-chartjs'
export default {
  name: 'DosenChart',
  extends: Doughnut,
  props: {
    bidang_minat: ''
  },
  //variable
  data: function(){
    return{

    }
  },
  computed: {

  },
  components: {

  },
  //methods
  watch: {

  },
  methods: {

  },
  //components life cycle hook
  created: function(){

  },
  mounted () {
    // Overwriting base render method with actual data.
    this.renderChart({
      // labels: ['Keuangan', 'Manajemen Sumber Daya Manusia', 'Pariwisata', 'Pemasaran', 'Sistem Informasi Manajemen'],
      labels: ['Keuangan', 'Manajemen Sumber Daya Manusia', 'Pariwisata', 'Pemasaran', 'Sistem Informasi Manajemen'],
      datasets: [
        {
          label: 'GitHub Commits',
          backgroundColor: [
            '#ff6384','#ff9f40','#ffcd56','#4bc0c0','#36a2eb'
          ],
          data: [this.bidang_minat['keuangan'], this.bidang_minat['msdm'], this.bidang_minat['pariwisata'], this.bidang_minat['pemasaran'], this.bidang_minat['sim']]
        }
      ],
    })
  }
}
</script>

