<template lang="html">
  <div class="layout-content-main">
    <div class="">
      <Card :bordered="false">
        <p slot="title">Persetujuan Ujian Proposal Tesis</p>
        <ujian-proposal-card v-for="(item,index) in progress" :key="item.id_progress" :prop_data_progress="item"></ujian-proposal-card>
      </Card>
    </div>
  </div>
</template>

<script>
import UjianProposalCard from './Ujian_Proposal_Card.vue';
export default {
  name: 'PersetujuanUjianProposal',
  prop: {

  },
  data: function(){
    return {
      progress: ''
    }
  },
  computed: {

  },
  components: {
    UjianProposalCard
  },
  watch: {

  },
  methods: {
    get_persetujuan_ujian_proposal: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ujian/get_progress_ujian",
        data: {token: self.$store.state.token,id_tipe_progress: 'PRG02'},
        success: function(response){
          self.progress = response.progress;
          console.log(response);
        }
      })
    },
  },
  created: function(){
    this.get_persetujuan_ujian_proposal();
  }
}
</script>

<style lang="css">

</style>



