<template lang="html">
  <Row class-name="batas">
    <Spin v-if="ajax_fetch_data" fix size="large"></Spin>
    <template v-else>
      <Col span="24" class="title-section">
        <Col span="4">
          <img src="./../assets/logo-ub.png" alt="" class="img-fluid">
        </Col>
        <Col span="20">
          <p class="top-title">KEMENTERIAN RISET, TEKNOLOGI DAN PENDIDIKAN TINGGI</p>
          <p class="top-title">UNIVERSITAS BRAWIJAYA</p>
          <p class="top-title">FAKULTAS ILMU ADMINISTRASI</p>
          <p class="sub-title">Jl. MT. Haryono 163, Malang 65145, Indonesia</p>
          <p class="sub-title">Telp. : +62-341-553737, 568914, 558226   Fax : +62-341-558227</p>
          <p class="sub-title">http://fia.ub.ac.id                  E-mail: fia@ub.ac.id</p>
        </Col>
      </Col>
      <Col span="24">
        <p class="berita-acara">BERITA ACARA Ujian Tesis</p>
        <p class="section-separator">Penyelenggaraan Ujian Tesis dari mahasiswa yang tersebut :</p>
        <table>
          <tr>
            <td style="width:250px;">Nama</td>
            <td>:</td>
            <td>{{ tesis.nama_mahasiswa }}</td>
          </tr>
          <tr>
            <td style="width:250px;">Nomor Induk Mahasiswa</td>
            <td>:</td>
            <td>{{tesis.nim_mahasiswa }}</td>
          </tr>
          <tr>
            <td style="width:250px;">Program Studi</td>
            <td>:</td>
            <td>Magister Administrasi Bisnis</td>
          </tr>
          <tr>
            <td style="width:250px;">Kekhususan</td>
            <td>:</td>
            <td>-</td>
          </tr>
          <tr>
            <td style="width:250px;">Judul</td>
            <td>:</td>
            <td>{{ tesis.judul_tesis }}</td>
          </tr>
        </table>
        <p class="section-separator">Yang diselenggarakan pada :</p>
        <table>
          <tr>
            <td style="width:250px;">Hari</td>
            <td>:</td>
            <td>{{ format_hari(ujian_tesis.tgl_ujian) }}</td>
          </tr>
          <tr>
            <td style="width:250px;">Tanggal</td>
            <td>:</td>
            <td>{{ format_tanggal(ujian_tesis.tgl_ujian) }}</td>
          </tr>
          <tr>
            <td style="width:250px;">Jam</td>
            <td>:</td>
            <td>{{ ujian_tesis.waktu_mulai_ujian }}</td>
          </tr>
          <tr>
            <td style="width:250px;">Tempat</td>
            <td>:</td>
            <td>Gedung C (Magister) Ruang C.2.10 FIA UB</td>
          </tr>
          <tr>
            <td style="width:250px;">Nilai</td>
            <td>:</td>
            <td></td>
          </tr>
        </table>
        <table class="table-penilaian">
          <tr>
            <th style="width:60%;text-align:center">Penilaian oleh dosen</th>
            <th style="width:20%;text-align:center">Nilai<br>(Angka)</th>
            <th style="width:20%;text-align:center">Nilai<br>(Huruf)</th>
          </tr>
          <tr>
            <td>Ketua : {{ tesis.nama_dospem_1 }}</td>
            <td>{{ nilai_angka(hasil_ujian_tesis[0].nilai_1,hasil_ujian_tesis[0].nilai_2,hasil_ujian_tesis[0].nilai_3) }}</td>
            <td>{{ nilai_huruf(hasil_ujian_tesis[0].nilai_1,hasil_ujian_tesis[0].nilai_2,hasil_ujian_tesis[0].nilai_3) }}</td>
          </tr>
          <tr>
            <td>Anggota : {{ tesis.nama_dospem_2 }}</td>
            <td>{{ nilai_angka(hasil_ujian_tesis[1].nilai_1,hasil_ujian_tesis[1].nilai_2,hasil_ujian_tesis[1].nilai_3) }}</td>
            <td>{{ nilai_huruf(hasil_ujian_tesis[1].nilai_1,hasil_ujian_tesis[1].nilai_2,hasil_ujian_tesis[1].nilai_3) }}</td>
          </tr>
          <tr>
            <td>Anggota : {{ ujian_tesis.penguji_1 }}</td>
            <td>{{ nilai_angka(hasil_ujian_tesis[2].nilai_1,hasil_ujian_tesis[2].nilai_2,hasil_ujian_tesis[2].nilai_3) }}</td>
            <td>{{ nilai_huruf(hasil_ujian_tesis[2].nilai_1,hasil_ujian_tesis[2].nilai_2,hasil_ujian_tesis[2].nilai_3) }}</td>
          </tr>
          <tr>
            <td>Anggota : {{ ujian_tesis.penguji_2 }}</td>
            <td>{{ nilai_angka(hasil_ujian_tesis[3].nilai_1,hasil_ujian_tesis[3].nilai_2,hasil_ujian_tesis[3].nilai_3) }}</td>
            <td>{{ nilai_huruf(hasil_ujian_tesis[3].nilai_1,hasil_ujian_tesis[3].nilai_2,hasil_ujian_tesis[3].nilai_3) }}</td>
          </tr>
          <tr>
            <th style="text-align:center">Rerata</th>
            <th>{{ ujian_tesis.nilai }}</th>
            <th>{{ nilai_huruf(ujian_tesis.nilai) }}</th>
          </tr>
        </table>
        <br><br>
        <Col span="8" offset="16">
          <p>Malang,  {{ format_hari(ujian_tesis.tgl_ujian)+' '+format_tanggal(ujian_tesis.tgl_ujian) }}</p>
          <p>dosen Pembimbing</p>
          <p>Ketua</p>
          <br>
          <br>
          <br>
          <p>{{ tesis.nama_dospem_1 }}</p>
          <p>NIP. {{ tesis.NIP_dospem_1 }}</p>
        </Col>
      </Col>
    </template>
  </Row>
</template>

<script>
export default {
  name: "DokumenBAUjianProposal",
  props: {

  },
  //variable
  data: function(){
    return{
      tesis: '',
      ujian_tesis: '',
      hasil_ujian_tesis: [],
      ajax_fetch_data: ''

    }
  },
  computed: {

  },
  components: {

  },
  //methods
  watch: {

  },
  methods: {
    get_tesis_by_id_tesis: function(){
      console.log('get tesis from ajax');
      this.ajax_fetch_data = true;
      var self = this;
      this.$store.dispatch('get_tesis_by_id_tesis',this.$route.params.id_tesis).then((tesis) => {
        this.tesis = tesis[0];
        this.get_ujian_tesis();
        // this.ajax_fetch.tesis = false;
      });
    },
    get_ujian_tesis: function(){
      console.log('get ujian tesis from ajax');
      var self = this;
      var args = [];
      args['id_tesis'] = this.$route.params.id_tesis;
      args['tipe_ujian'] = 'UT';
      this.$store.dispatch('get_ujian_by_id_tesis',args).then((ujian) => {
        this.ujian_tesis = ujian[0];
        this.get_hasil_ujian();
        this.ujian_tesis.penguji_1 = this.ujian_tesis.penguji_1[0].nama_pegawai;
        this.ujian_tesis.penguji_2 = this.ujian_tesis.penguji_2[0].nama_pegawai;
        // this.ajax_fetch.ujian_tesis = false;
      });
    },
    get_hasil_ujian: function(){
      console.log('get hasil ujian tesis from ajax');
      var self = this;
      var args = [];
      args['id_ujian'] = this.ujian_tesis.id_ujian;
      this.$store.dispatch('get_hasil_ujian',args).then((hasil_ujian_tesis) => {
        hasil_ujian_tesis.forEach(function(val,index,item){
          self.hasil_ujian_tesis.push(item[index]);
        });
        this.ajax_fetch_data = false;
        setTimeout(function(){
          window.print();
          // if(this.hasil_ujian_tesis){
          //
          // }
        },200);
      });
    },
    format_hari: function(tanggal){
      var days = ['Minggu', 'Senin', 'Selasa', 'Rabu', 'Kamis', 'Jumat', 'Sabtu'];
      var date = new Date(tanggal);
      var day = date.getDate();
      var nama_hari = days[date.getDay()]
      return nama_hari;
    },
    format_tanggal: function(tanggal){
      var date = new Date(tanggal);
      var month = date.getMonth() + 1;
      var day = date.getDate();
      var year = date.getFullYear();
      return day+"-"+month+"-"+year;
    },
    nilai_angka: function(nilai_1,nilai_2,nilai_3){
      return (parseInt(nilai_1)+parseInt(nilai_2)+parseInt(nilai_3))/3;
    },
    nilai_huruf: function(nilai_1,nilai_2,nilai_3){
      var nilai = (parseInt(nilai_1)+parseInt(nilai_2)+parseInt(nilai_3))/3;
      if(nilai > 80){
        return 'A';
      }
      else if(nilai <= 80 && nilai > 75){
        return 'B+';
      }
      else if(nilai <= 75 && nilai > 69){
        return 'B';
      }
      else if(nilai <= 69 && nilai > 60){
        return 'C+';
      }
      else if(nilai <= 60 && nilai > 55){
        return 'C';
      }
      else if(nilai <= 55 && nilai > 50){
        return 'D+';
      }
      else if(nilai <= 50 && nilai > 44){
        return 'D';
      }
      else{
        return 'E';
      }
    }
  },
  //components life cycle hook
  created: function(){
    this.get_tesis_by_id_tesis();
  }
}
</script>

<style lang="css" scoped>
*{
  color: black;
  font-size: 14px;
  border: none;
}
.batas{
  width: 210mm;
  margin-left: auto;
  margin-right: auto;
}
@media print {
    html, body {
    width: 210mm;
  }
}
.title-section{
  border-bottom: 5px solid black;
}
.top-title{
  font-size: 20px;
  line-height: 1;
  text-align: center;
  color: black;
}
.sub-title{
  font-size: 14px;
  text-align: center;
  color: black;
}
.berita-acara{
  text-align: center;
  text-decoration: underline;
  font-size: 16px;
  font-weight: bold;
  margin-top: 20px;
  margin-bottom: 40px;
  letter-spacing: 0;
}
.section-separator{
  margin-top: 15px;
  margin-bottom: 15px;
}
.table-penilaian{
  width: 100%;
}
.table-penilaian td,th{
  border: 1px solid black;
}
img{
  width: 100%;
  height: auto;
}
</style>



