<template lang="html">
  <Card :bordered="false" :dis-hover="true">
    <p slot="title">Pengajuan Judul dan dosen Pembimbing
      <ButtonGroup>
        <Button @click="back" :disabled="prev_button_disabled" type="default"><Icon type="chevron-left"></Icon> Prev</Button>
        <Button @click="next" :disabled="next_button_disabled" type="default">Next <Icon type="chevron-right"></Icon></Button>
      </ButtonGroup>
    </p>
    <Steps :current="pengajuan_proposal_step_copy">
      <Step title="Tahap 1" content="Isi Form pengajuan proposal" style="width:20%"></Step>
      <Step title="Tahap 2" content="Verifikasi Akademik & Kaprodi" style="width:20%"></Step>
      <Step title="Tahap 3" content="Verifikasi Dospem" style="width:20%"></Step>
      <Step title="Tahap 4" content="Persetujuan Kaprodi" style="width:20%"></Step>
      <Step title="Tahap 5" content="Selesai" style="width:20%"></Step>
    </Steps>
    <hr>
    <div v-if="pengajuan_proposal_step_copy == 0" class="step-content-wrap">
      <h5 class="form-title">
        Form Pengajuan Judul dan dosen Pembimbing
      </h5>
      <p class="text-center form-sub-title">Diajukan pada : {{ pengajuan_proposal.tgl_progress }}</p>
      <div class="row justify-content-center">
        <div class="col-sm-10">
          <Spin v-if="ajax_fetch_data"></Spin>
          <div  v-else class="card-block">
            <form @submit.prevent="submit_pengajuan">
              <div class="form-group">
                <label for="judul_tesis">Judul Tesis</label>
                <input
                  v-model="pengajuan_proposal.judul_tesis"
                  type="text"
                  class="form-control"
                  disabled>
                <div v-show="errors.has('judul_tesis')" class="invalid-feedback">
                  {{ errors.first('judul_tesis') }}
                </div>
              </div>
              <div class="form-group">
                <label for="tipe_penelitian">Tipe Penelitian</label>
                <input
                  v-model="pengajuan_proposal.nama_bidang_minat"
                  type="text"
                  class="form-control"
                  disabled>
                <div v-show="errors.has('tipe_penelitian')" class="invalid-feedback">
                  {{ errors.first('tipe_penelitian') }}
                </div>
              </div>
              <div class="form-row">
                <div class="col-md-12">
                  <label for="dosen_1">Calon Dosen Pembimbing 1</label>
                </div>
                <div :class="form_size">
                  <input
                    v-model="pengajuan_proposal.calon_dospem[0].nama_pegawai"
                    type="text"
                    class="form-control"
                    disabled>
                    <div v-show="errors.has('dosen_pembimbing_1')" class="invalid-feedback">
                      {{ errors.first('dosen_pembimbing_1') }}
                    </div>
                </div>
              </div>
              <div class="form-row">
                <div class="col-md-12">
                  <label for="dosen_2">Calon Dosen Pembimbing 2</label>
                </div>
                <div :class="form_size">
                  <input
                    v-model="pengajuan_proposal.calon_dospem[1].nama_pegawai"
                    type="text"
                    class="form-control"
                    disabled>
                    <div v-show="errors.has('dosen_pembimbing_2')" class="invalid-feedback">
                      {{ errors.first('dosen_pembimbing_2') }}
                    </div>
                </div>
              </div>
              <div class="form-row">
                <div class="col-md-12">
                  <label for="dosen_3">Calon Dosen Pembimbing</label>
                </div>
                <div :class="form_size">
                  <input
                    v-if="pengajuan_proposal.calon_dospem[2]"
                    v-model="pengajuan_proposal.calon_dospem[2].nama_pegawai"
                    type="text"
                    class="form-control"
                    disabled>
                    <input
                      v-else
                      type="text"
                      class="form-control"
                      disabled>
                  <small id="emailHelp" class="form-text text-muted">Opsional</small>
                </div>
              </div>
              <div class="form-row">
                <div class="col-md-12">
                  <label for="dosen_4">Calon Dosen Pembimbing</label>
                </div>
                <div :class="form_size">
                  <input
                    v-if="pengajuan_proposal.calon_dospem[3]"
                    v-model="pengajuan_proposal.calon_dospem[3].nama_pegawai"
                    type="text"
                    class="form-control"
                    disabled>
                    <input
                      v-else
                      type="text"
                      class="form-control"
                      disabled>
                  <small id="emailHelp" class="form-text text-muted">Opsional</small>
                </div>
              </div>
              <div class="form-row">
                <div class="col-md-12">
                  <label for="dosen_5">Calon Dosen Pembimbing</label>
                </div>
                <div :class="form_size">
                  <input
                    v-if="pengajuan_proposal.calon_dospem[4]"
                    v-model="pengajuan_proposal.calon_dospem[4].nama_pegawai"
                    type="text"
                    class="form-control"
                    disabled>
                    <input
                      v-else
                      type="text"
                      class="form-control"
                      disabled>
                  <small id="emailHelp" class="form-text text-muted">Opsional</small>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
    <div v-if="pengajuan_proposal_step_copy == 1" class="step-content-wrap">
      <h5 class="form-title">Verifikasi Akademik & Kaprodi</h5>
      <div class="row justify-content-center">
        <div class="col-sm-8 status-wrap">
          <div class="row justify-content-center">
            <div class="col-sm-10 status-inner">
              <Spin v-if="!ajax_fetch_status_selesai"></Spin>
              <template v-else>
                <p class="text-wrap"><span class="text-label">Kaprodi : </span></p>
                <Alert show-icon :type="pengajuan_proposal_persetujuan.persetujuan_kaprodi.alert">
                  <span v-if="pengajuan_proposal_persetujuan.persetujuan_kaprodi.status">{{ pengajuan_proposal_persetujuan.persetujuan_kaprodi.status }}</span>
                  <span v-else>-</span>
                </Alert>
                <p class="text-wrap"><span class="text-label">Akademik : </span></p>
                <Alert show-icon :type="pengajuan_proposal_persetujuan.persetujuan_akademik.alert">
                  <span v-if="pengajuan_proposal_persetujuan.persetujuan_akademik.status">{{ pengajuan_proposal_persetujuan.persetujuan_akademik.status }}</span>
                  <span v-else>-</span>
                </Alert>
              </template>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="pengajuan_proposal_step_copy == 2">
      <h5 class="form-title">Verifikasi Dospem 1 & Dospem 2</h5>
      <div class="row justify-content-center">
        <div class="col-sm-8 status-wrap">
          <div class="row justify-content-center">
            <div class="col-sm-10 status-inner">
              <Spin v-if="!ajax_fetch_status_selesai"></Spin>
              <template v-else>
                <p class="text-wrap"><span class="text-label">Dospem 1 : </span></p>
                <Alert show-icon :type="pengajuan_proposal_persetujuan.persetujuan_dosen_1.alert">
                  <span v-if="pengajuan_proposal_persetujuan.persetujuan_dosen_1.status">{{ pengajuan_proposal_persetujuan.persetujuan_dosen_1.status }}</span>
                  <span v-else>-</span>
                </Alert>
                <p class="text-wrap"><span class="text-label">Dospem 2 : </span></p>
                <Alert show-icon :type="pengajuan_proposal_persetujuan.persetujuan_dosen_2.alert">
                  <span v-if="pengajuan_proposal_persetujuan.persetujuan_dosen_2.status">{{ pengajuan_proposal_persetujuan.persetujuan_dosen_2.status }}</span>
                  <span v-else>-</span>
                </Alert>
              </template>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="pengajuan_proposal_step_copy == 3">
      <h5 class="form-title">Persetujuan Kaprodi</h5>
      <div class="row justify-content-center">
        <div class="col-sm-8 status-wrap">
          <div class="row justify-content-center">
            <div class="col-sm-10 status-inner">
              <Spin v-if="!ajax_fetch_status_selesai"></Spin>
              <template v-else>
                <p class="text-wrap"><span class="text-label">Kaprodi : </span></p>
                <Alert show-icon :type="pengajuan_proposal_persetujuan.persetujuan_akhir.alert">
                  <span v-if="pengajuan_proposal_persetujuan.persetujuan_akhir.status">{{ pengajuan_proposal_persetujuan.persetujuan_akhir.status }}</span>
                  <span v-else>-</span>
                </Alert>
              </template>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="pengajuan_proposal_step_copy == 4">
      <h5 class="form-title">Pengajuan Proposal Selesai</h5>
      <div class="row justify-content-center">
        <div class="col-sm-8 status-wrap">
          <div class="row justify-content-center">
            <div class="col-sm-10 status-inner">
              <Spin v-if="!ajax_fetch_status_selesai"></Spin>
              <template v-else>
                <p class="text-wrap"><span class="text-label">Judul Tesis</span></p>
                <input type="text" :value="pengajuan_proposal.judul_tesis" class="form-control" readonly>
                <p class="text-wrap"><span class="text-label">Bidang Minat</span></p>
                <input type="text" :value="pengajuan_proposal.nama_bidang_minat" class="form-control" readonly>
                <p class="text-wrap"><span class="text-label">Pembimbing 1</span></p>
                <input type="text" :value="pengajuan_proposal.dospem[0].nama_pegawai" class="form-control" readonly>
                <p class="text-wrap"><span class="text-label">Pembimbing 2</span></p>
                <input type="text" :value="pengajuan_proposal.dospem[1].nama_pegawai" class="form-control" readonly>
                <p class="text-wrap"><span class="text-label">Status</span></p>
                <Alert show-icon type="success">
                  {{ pengajuan_proposal_persetujuan.persetujuan_selesai.status }}
                </Alert>
              </template>
            </div>
          </div>
        </div>
      </div>
    </div>
  </Card>
</template>
<script>
export default {
  name: 'PengajuanProposalProgress',
  props: ['id_proposal'],
  data: function() {
    return {
      row_dosen: '',
      bidang_minat: [],
      pengajuan_proposal: '',
      pengajuan_proposal_status: '',
      pengajuan_proposal_persetujuan: {
        persetujuan_kaprodi: {},
        persetujuan_akademik: {},
        persetujuan_dosen_1: {},
        persetujuan_dosen_2: {},
        persetujuan_akhir: {},
        persetujuan_selesai: {},
      },
      ajax_fetch_data: true,
      ajax_fetch_status_selesai: false,
      pengajuan_proposal_step: 0,
      pengajuan_proposal_step_copy: 0,
    }
  },
  computed: {
    form_size: function(){
      return 'col-md-12';
    },
    form_size_button: function(){
      return '';
    },
    next_button_disabled: function(){
      if(this.pengajuan_proposal_step_copy == this.pengajuan_proposal_step){
        return true
      }
      else{
        return false;
      }
    },
    prev_button_disabled: function(){
      if(this.pengajuan_proposal_step_copy == 0){
        return true;
      }
      else{
        false;
      }
    }
  },
  created: function(){
    this.get_pengajuan_proposal();
  },
  components: {

  },
  watch: {

  },
  methods: {
    get_dosen: function(){
      var self = this;
      if(this.$store.state.dosen.length == 0){
        console.log('get dosen from ajax');
        this.$store.dispatch('get_all_dosen').then(() => {
          self.row_dosen = self.$store.state.dosen;
          self.row_dosen.forEach(function(val,index,item){
            self.row_dosen[index].status = 'Not Selected';
          });
          self.ajax_fetch_data = false;
        });
      }
      else{
        console.log('get dosen from store');
        self.row_dosen = self.$store.state.dosen;
        self.row_dosen.forEach(function(val,index,item){
          self.row_dosen[index].status = 'Not Selected';
        });
        self.ajax_fetch_data = false;
      }
    },
    get_bidang_minat: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Bidang_Minat",
        data: {token: this.$store.state.token},
        success: function(response){
          response.forEach(function(val,index,item){
            self.bidang_minat.push(val);
            // console.log(self.bidang_minat);
          });
        }
      })
    },
    submit_pengajuan: function(){
      var self = this;
      this.$validator.validateAll().then((result) => {
        if (result) {
          $.ajax({
            type: 'POST',
            url: "https://simt.000webhostapp.com/index.php/Pengajuan_Proposal",
            data: {token: this.$store.state.token, data: self.form_pengajuan},
            success: function(response){
              if(response.status == 'Berhasil'){
                self.$Notice.open({
                  title: 'Pengajuan berhasil disubmit!',
                });
                self.$router.push('/pengajuan-judul');
                // self.pengajuan_proposal_step = 1;
                // self.pengajuan_proposal_step_copy = 1;
              }
              console.log(response);
            }
          })
          return;
        }
        self.$Message.error('Harap lengkapi form pengajuan')
      });
    },
    get_pengajuan_proposal: function(){
      var self = this;
      let param = {};
      console.log('try get pengajuan from ajax');
      param.id_pengajuan = self.id_proposal;
      this.$store.dispatch('get_pengajuan_proposal',param).then((pengajuan_proposal) => {
        if(pengajuan_proposal.length != 0){
          console.log('ditemukan');
          // console.log(pengajuan_proposal);
          self.pengajuan_proposal = pengajuan_proposal[0];
          self.get_ubah_status_progress();
          self.pengajuan_proposal_step = 1;
          self.pengajuan_proposal_step_copy = 1;
          self.ajax_fetch_data = false;
        }
        else{
          self.get_bidang_minat();
          self.get_dosen();
          self.pengajuan_proposal = '';
          console.log('gak ada');
        }
      });
    },
    get_ubah_status_progress: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Progress/get_ubah_status_progress",
        data: {token: self.$store.state.token,id_progress: self.pengajuan_proposal.id_progress},
        beforeSend: function(){

        },
        success: function(response){
          // console.log(response);
          self.pengajuan_proposal_status = response;
          self.cek_status_persetujuan_pengajuan();
          self.ajax_fetch_status_selesai = true;
        }
      })
    },
    cek_status_persetujuan_pengajuan: function(){
      var self = this;
      this.pengajuan_proposal_status.forEach(function(val,index,item){
        if(item[index].id_status_progress == 'SP01'){
          if(item[index].is_selesai == "0"){
            self.pengajuan_proposal_persetujuan.persetujuan_akademik.status = "Menunggu "+item[index].nama_status_progress;
            self.pengajuan_proposal_persetujuan.persetujuan_akademik.alert = "warning";
          }
          else if(item[index].is_selesai == "1"){
            self.pengajuan_proposal_persetujuan.persetujuan_akademik.status = "Sudah "+item[index].nama_status_progress+" ("+item[index].tgl_selesai+")";
            self.pengajuan_proposal_persetujuan.persetujuan_akademik.alert = "success";
          }
          else{
            self.pengajuan_proposal_persetujuan.persetujuan_akademik.status = item[index].nama_status_progress+" ("+item[index].tgl_selesai+")";
            self.pengajuan_proposal_persetujuan.persetujuan_akademik.alert = "error";
          }
        }
        else if(item[index].id_status_progress == 'SP02'){
          if(item[index].is_selesai == "0"){
            self.pengajuan_proposal_persetujuan.persetujuan_kaprodi.status = "Menunggu "+item[index].nama_status_progress;
            self.pengajuan_proposal_persetujuan.persetujuan_kaprodi.alert = "warning";
          }
          else if(item[index].is_selesai == "1"){
            self.pengajuan_proposal_persetujuan.persetujuan_kaprodi.status = "Sudah "+item[index].nama_status_progress+" ("+item[index].tgl_selesai+")";
            self.pengajuan_proposal_persetujuan.persetujuan_kaprodi.alert = "success";
            self.pengajuan_proposal_step = 2;
            self.pengajuan_proposal_step_copy = 2;
          }
          else{
            self.pengajuan_proposal_persetujuan.persetujuan_akademik.status = item[index].nama_status_progress+" ("+item[index].tgl_selesai+")";
            self.pengajuan_proposal_persetujuan.persetujuan_akademik.alert = "error";
          }
        }
        else if(item[index].id_status_progress == 'SP03'){
          if(item[index].is_selesai == "0"){
            self.pengajuan_proposal_persetujuan.persetujuan_dosen_1.status = "Menunggu "+item[index].nama_status_progress;
            self.pengajuan_proposal_persetujuan.persetujuan_dosen_1.alert = "warning";
          }
          else if(item[index].is_selesai == "1"){
            self.pengajuan_proposal_persetujuan.persetujuan_dosen_1.status = "Sudah "+item[index].nama_status_progress+" ("+item[index].tgl_selesai+")";
            self.pengajuan_proposal_persetujuan.persetujuan_dosen_1.alert = "success";
          }
        }
        else if(item[index].id_status_progress == 'SP04'){
          if(item[index].is_selesai == "0"){
            self.pengajuan_proposal_persetujuan.persetujuan_dosen_2.status = "Menunggu "+item[index].nama_status_progress;
            self.pengajuan_proposal_persetujuan.persetujuan_dosen_2.alert = "warning";
          }
          else if(item[index].is_selesai == "1"){
            self.pengajuan_proposal_persetujuan.persetujuan_dosen_2.status = "Sudah "+item[index].nama_status_progress+" ("+item[index].tgl_selesai+")";
            self.pengajuan_proposal_persetujuan.persetujuan_dosen_2.alert = "success";
            self.pengajuan_proposal_step = 3;
            self.pengajuan_proposal_step_copy = 3;
          }
        }
        else if(item[index].id_status_progress == 'SP05'){
          if(item[index].is_selesai == "0"){
            self.pengajuan_proposal_persetujuan.persetujuan_akhir.status = "Menunggu "+item[index].nama_status_progress;
            self.pengajuan_proposal_persetujuan.persetujuan_akhir.alert = "warning";
          }
          else if(item[index].is_selesai == "1"){
            self.pengajuan_proposal_persetujuan.persetujuan_akhir.status = "Sudah "+item[index].nama_status_progress+" ("+item[index].tgl_selesai+")";
            self.pengajuan_proposal_persetujuan.persetujuan_akhir.alert = "success";
            self.pengajuan_proposal_step = 4;
            self.pengajuan_proposal_step_copy = 4;
          }
        }
        else if(item[index].id_status_progress == 'SP06'){
          self.pengajuan_proposal_persetujuan.persetujuan_selesai.status = item[index].nama_status_progress;
          self.pengajuan_proposal_persetujuan.persetujuan_selesai.alert = "success";
        }
      });
    },
    next: function(){
      this.pengajuan_proposal_step_copy+=1;
    },
    back: function(){
      this.pengajuan_proposal_step_copy-=1;
    },
  }
}
</script>
<style lang="css" scoped>
  .card{
    padding: 1rem;
  }
  .card-block{
    border: 1px solid #e5e5e5;
    padding: 15px 20px 20px 20px;
  }
  .form-title{
    text-align: center;
    font-weight: 500;
    margin-top: 10px;
    margin-bottom: 40px;
  }
  .form-sub-title{
    position: relative;
    top: -20px;
  }
  .form-row{
    margin-bottom: 1rem;
  }
  .status-wrap{
    background-color: #f5f7f9;
  }
  .status-inner{
    background-color: white;
    padding: 20px;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .ivu-modal{
    width: 90%;
    top: 0;
  }
  .ivu-card-head{
    position: relative;
  }
  .ivu-card-head p, .ivu-card-head-inner{
    height: auto !important;
  }
  .ivu-steps{
    margin-bottom: 15px;
    margin-top: 15px;
  }
  .ivu-btn-group{
    position: absolute;
    right: 15px;
  }
  .step-content-wrap{
    margin-top: 30px;
    min-height: 400px;
  }
  .text-label{
    font-weight: bold;
  }
  .text-wrap{
    margin-top: 10px;
    margin-bottom: 5px;
  }
</style>



