<template>
    <div class="layout" :class="{'layout-hide-text': spanLeft < 5}">
        <Row type="flex">
            <Col :span="spanLeft" class="layout-menu-left">
              <Menu theme="dark" width="auto" :active-name="menu_aktif" accordion>
                  <div class="layout-logo-left"></div>
                  <router-link to="/" exact>
                    <Menu-item name="dashboard">
                      <Icon type="ios-navigate" :size="iconSize"></Icon>
                      <span class="layout-text">Dashboard</span>
                    </Menu-item>
                  </router-link>
                  <router-link to="/pengajuan-judul" exact>
                    <Menu-item name="pengajuan-judul">
                      <Icon type="ios-compose" :size="iconSize"></Icon>
                      <Badge :dot="have_notif"><span class="layout-text">Pengajuan Judul</span></Badge>
                    </Menu-item>
                  </router-link>
                  <!-- menu ujian proposal -->
                  <template v-if="$store.state.profile.mahasiswa">
                    <router-link to="/ujian-proposal" exact>
                      <Menu-item name="ujian-proposal">
                        <Icon type="ios-compose" :size="iconSize"></Icon>
                        <Badge :dot="have_notif"><span class="layout-text">Ujian Proposal</span></Badge>
                      </Menu-item>
                    </router-link>
                  </template>
                  <template v-if="$store.state.profile.pegawai">
                    <Submenu name="ujian-proposal-pegawai">
                      <template slot="title">
                        <Icon type="social-buffer-outline" :size="iconSize"></Icon>
                        <span class="layout-text">Ujian Proposal</span>
                      </template>
                      <router-link to="/ujian-proposal/persetujuan-ujian-proposal" exact>
                        <Menu-item name="persetujuan-ujian-proposal">
                          <Icon type="social-buffer-outline" :size="iconSize"></Icon>
                          <Badge :dot="have_notif"><span class="layout-text">Persetujuan Ujian Proposal</span></Badge>
                        </Menu-item>
                      </router-link>
                      <router-link to="/ujian-proposal/pengajuan-ujian-proposal" exact>
                        <Menu-item name="pengajuan-ujian-proposal">
                          <Icon type="social-buffer-outline" :size="iconSize"></Icon>
                          <Badge :dot="have_notif"><span class="layout-text">Pengajuan Ujian Proposal</span></Badge>
                        </Menu-item>
                      </router-link>
                      <router-link to="/ujian-proposal/pelaksanaan-ujian-proposal" exact>
                        <Menu-item name="pelaksanaan-ujian-proposal">
                          <Icon type="social-buffer-outline" :size="iconSize"></Icon>
                          <Badge :dot="have_notif"><span class="layout-text">Pelaksanaan Ujian Proposal</span></Badge>
                        </Menu-item>
                      </router-link>
                    </Submenu>
                  </template>
                  <!-- menu seminar hasil  -->
                  <template v-if="$store.state.profile.mahasiswa">
                    <router-link to="/seminar-hasil" exact>
                      <Menu-item name="seminar-hasil">
                        <Icon type="social-buffer-outline" :size="iconSize"></Icon>
                        <Badge :dot="have_notif"><span class="layout-text">Seminar Hasil</span></Badge>
                      </Menu-item>
                    </router-link>
                  </template>
                  <template v-else-if="$store.state.profile.pegawai">
                    <Submenu name="seminar-hasil">
                      <template slot="title">
                        <Icon type="social-buffer-outline" :size="iconSize"></Icon>
                        <span class="layout-text">Seminar Hasil</span>
                      </template>
                      <router-link to="/seminar-hasil/persetujuan-seminar-hasil" exact>
                        <Menu-item name="persetujuan-seminar-hasil">
                          <Icon type="social-buffer-outline" :size="iconSize"></Icon>
                          <Badge :dot="have_notif"><span class="layout-text">Persetujuan Seminar Hasil</span></Badge>
                        </Menu-item>
                      </router-link>
                      <router-link to="/seminar-hasil/pengajuan-seminar-hasil" exact>
                        <Menu-item name="pengajuan-seminar-hasil">
                          <Icon type="social-buffer-outline" :size="iconSize"></Icon>
                          <Badge :dot="have_notif"><span class="layout-text">Pengajuan Seminar Hasil</span></Badge>
                        </Menu-item>
                      </router-link>
                      <router-link to="/seminar-hasil/pelaksanaan-seminar-hasil" exact>
                        <Menu-item name="pelaksanaan-seminar-hasil">
                          <Icon type="social-buffer-outline" :size="iconSize"></Icon>
                          <Badge :dot="have_notif"><span class="layout-text">Pelaksanaan Seminar Hasil</span></Badge>
                        </Menu-item>
                      </router-link>
                    </Submenu>
                  </template>
                  <!-- menu ujian tesis -->
                  <template v-if="$store.state.profile.mahasiswa">
                    <router-link to="/ujian-tesis" exact>
                      <Menu-item name="ujian-tesis">
                        <Icon type="social-buffer-outline" :size="iconSize"></Icon>
                        <Badge :dot="have_notif"><span class="layout-text">Ujian Tesis</span></Badge>
                      </Menu-item>
                    </router-link>
                  </template>
                  <template v-else-if="$store.state.profile.pegawai">
                    <Submenu name="ujian-tesis">
                      <template slot="title">
                        <Icon type="social-buffer-outline" :size="iconSize"></Icon>
                        <span class="layout-text">Ujian Tesis</span>
                      </template>
                      <router-link to="/ujian-tesis/persetujuan-ujian-tesis" exact>
                        <Menu-item name="persetujuan-ujian-tesis">
                          <Icon type="social-buffer-outline" :size="iconSize"></Icon>
                          <Badge :dot="have_notif"><span class="layout-text">Persetujuan Ujian Tesis</span></Badge>
                        </Menu-item>
                      </router-link>
                      <router-link to="/ujian-tesis/pengajuan-ujian-tesis" exact>
                        <Menu-item name="pengajuan-ujian-tesis">
                          <Icon type="social-buffer-outline" :size="iconSize"></Icon>
                          <Badge :dot="have_notif"><span class="layout-text">Pengajuan Ujian Tesis</span></Badge>
                        </Menu-item>
                      </router-link>
                      <router-link to="/ujian-tesis/pelaksanaan-ujian-tesis" exact>
                        <Menu-item name="pelaksanaan-ujian-tesis">
                          <Icon type="social-buffer-outline" :size="iconSize"></Icon>
                          <Badge :dot="have_notif"><span class="layout-text">Pelaksanaan Ujian Tesis</span></Badge>
                        </Menu-item>
                      </router-link>
                    </Submenu>
                  </template>
                  <!-- menu tesis -->
                  <template v-if="$store.state.profile.mahasiswa">
                    <router-link to="/tesis" exact>
                      <Menu-item name="tesis">
                        <Icon type="social-buffer-outline" :size="iconSize"></Icon>
                        <Badge :dot="have_notif"><span class="layout-text">Tesis</span></Badge>
                      </Menu-item>
                    </router-link>
                  </template>
                  <!-- memu mahasiswa -->
                  <template v-if="$store.state.profile.pegawai">
                    <router-link to="/mahasiswa" exact>
                      <Menu-item name="mahasiswa">
                        <Icon type="person-stalker" :size="iconSize"></Icon>
                        <Badge :dot="have_notif"><span class="layout-text">Mahasiswa</span></Badge>
                      </Menu-item>
                    </router-link>
                  </template>
                  <!-- memu list dosen -->
                  <template v-if="$store.state.profile.pegawai">
                    <router-link v-if="$store.state.profile.pegawai.tipe_pegawai == 'Kaprodi'" to="/dosen" exact>
                      <Menu-item name="dosen">
                        <Icon type="university" :size="iconSize"></Icon>
                        <Badge :dot="have_notif"><span class="layout-text">Dosen</span></Badge>
                      </Menu-item>
                    </router-link>
                  </template>
              </Menu>
            </Col>
            <Col :span="spanRight" class-name="main-content">
              <div class="layout-header">
                <Button type="text" @click="toggleClick">
                  <Icon type="navicon" size="32"></Icon>
                </Button>
                <div class="float-right">
                  <Menu mode="horizontal" active-name="2" class="menu-user">
                    <Submenu name="2">
                      <template slot="title">
                        <Avatar :class="bg_avatar">{{ profile.inisial_nama }}</Avatar>
                        <span>{{ profile.nama }}</span>
                      </template>
                      <Menu-item name="2-0">
                        <router-link to="/profile" class="lol" exact>
                          <Icon type="person"></Icon>Profile
                        </router-link>
                      </Menu-item>
                      <Menu-item name="2-1">
                        <router-link to="/reset_pass" class="lol" exact>
                          <Icon type="key"></Icon>Ganti Password
                        </router-link>
                      </Menu-item>
                      <Menu-item name="2-2"><a @click="log_out()"><Icon type="power"></Icon>Log Out</a></Menu-item>
                    </Submenu>
                  </Menu>
                </div>
              </div>
              <div class="layout-breadcrumb" key="breadcumb">
                <Breadcrumb>
                  <Breadcrumb-item href="/">Home</Breadcrumb-item>
                  <Breadcrumb-item
                    v-for="(item,index) in url"
                    v-if="item != ''"
                    :href="'/'+item"
                    key="index">
                    {{ item.replace("-"," ")}}
                  </Breadcrumb-item>
                </Breadcrumb>
              </div>
              <div class="layout-content">
                <template v-if="ajax_fetch">
                  <Spin></Spin>
                </template>
                <template v-else>
                  <router-view></router-view>
                </template>
              </div>
              <div class="layout-copy">
                  2017 &copy; Sistem Informasi Manajemen Tesis
              </div>
            </Col>
        </Row>
    </div>
</template>
<script>
export default {
  data: function() {
    return {
      profile: {
        nama: '',
        inisial_nama: '',
        tipe: ''
      },
      ajax_fetch: '',
      url: [],
      bg_avatar: '',
      notifikasi: '',
      spanLeft: 5,
      spanRight: 19
    }
  },
  computed: {
    menu_aktif: function(){
      return this.url[this.url.length-1];
    },
    have_notif(){
      if(this.notifikasi.length != 0){
        return true;
      }
      else{
        return false;
      }
    },
    iconSize() {
      return this.spanLeft === 5 ? 15 : 24;
    },
  },
  created: function(){
    this.get_profile();
    this.get_url();
  },
  methods: {
    alert: function(name){
      if(name == '2-2'){
        this.log_out();
      }
    },
    toggleClick:function(){
      if (this.spanLeft === 5) {
        this.spanLeft = 2;
        this.spanRight = 22;
      } else {
        this.spanLeft = 5;
        this.spanRight = 19;
      }
    },
    get_url: function(){
      this.url = this.$route.fullPath.split("/");
    },
    get_profile: function(){
      var self = this;
      this.ajax_fetch = true;
      this.$store.dispatch('get_profile').then((progress) => {
        if(progress.pegawai){
          if(progress.pegawai.tipe_pegawai == "Dosen"){
            self.bg_avatar = 'avatar-dosen';
          }
          else if(progress.pegawai.tipe_pegawai == "Kaprodi"){
            self.bg_avatar = 'avatar-kaprodi';
          }
          else if(progress.pegawai.tipe_pegawai == "Akademik"){
            self.bg_avatar = 'avatar-akademik';
          }
          self.profile.tipe = 'pegawai';
          self.profile.nama = progress.pegawai.nama_pegawai;
          var split_nama = progress.pegawai.nama_pegawai.split(" ");
          if(split_nama.length > 1){
            self.profile.inisial_nama = split_nama[0].charAt(0)+split_nama[1].charAt(0);
          }
          else{
            self.profile.inisial_nama = split_nama[0].charAt(0);
          }

        }
        else if(progress.mahasiswa){
          self.bg_avatar = 'avatar-mahasiswa';
          self.profile.tipe = 'mahasiswa';
          self.profile.nama = progress.mahasiswa.nama_mahasiswa;
          var split_nama = progress.mahasiswa.nama_mahasiswa.split(" ");
          if(split_nama[1]){
            self.profile.inisial_nama = split_nama[0].charAt(0)+split_nama[1].charAt(0);
          }
          else{
            self.profile.inisial_nama = split_nama[0].charAt(0);
          }
          self.cek_progress();
        }
        self.ajax_fetch = false;
      });
    },
    cek_progress: function(){
      var self = this;
      if(this.$store.state.profile.mahasiswa){
        this.ajax_fetch = true;
        // get status pengajuan proposal
        this.$store.dispatch('cek_status_pengajuan_proposal').then(
        this.$store.dispatch('cek_status_ujian_proposal')).then(
        this.$store.dispatch('cek_status_seminar_hasil')).then(
        this.$store.dispatch('cek_status_ujian_tesis')).then(function(){
          self.ajax_fetch = false;
        })
      }
    },
    log_out(){
      this.$store.commit('delete_dosen');
      this.$store.commit('delete_token');
      this.$store.commit('delete_tesis');
      this.$store.commit('delete_profile');
      this.$store.commit('delete_profile');
      this.$store.commit('delete_pengajuan_proposal');
      this.$store.commit('reset_dosen_pembimbing');
      this.$store.commit('delete_mahasiswa');
      this.$router.push('/login');
    }
  },
  watch: {
    '$route': function(){
      this.url = this.$route.fullPath.split("/");
      if(this.url[1] == 'pengajuan-judul' || this.url[1] == 'ujian-proposal' || this.url[1] == 'seminar-hasil' || this.url[1] == 'ujian-tesis'){
        this.cek_progress();
        console.log('aku dipanggil');
      }
      // console.log(this.url);
    }
  },
  filters: {
    uppercase(value) {
      return value.toUpperCase();
    }
  }
}
</script>
<style>
    .main-content{
      overflow: scroll;
    }
    .layout{
        background: #f5f7f9;
        position: relative;
        height: 100%;
    }
    .layout-breadcrumb{
        padding: 10px 15px 0;
    }
    .layout-content{
        min-height: 200px;
        margin: 15px;
        /*overflow: hidden;*/
        border-radius: 4px;
    }
    .layout-content-main{
        padding: 10px;
    }
    .layout-copy{
        text-align: center;
        padding: 10px 0 20px;
        color: #9ea7b4;
    }
    .layout-menu-left{
        background: #464c5b;
    }
    .layout-header{
        height: 60px;
        background: #fff;
        box-shadow: 0 1px 1px rgba(0,0,0,.1);
    }
    .layout-logo-left{
        width: 90%;
        height: 30px;
        background: #5b6270;
        border-radius: 3px;
        margin: 15px auto;
    }
    .layout-ceiling-main a{
        color: #9ba7b5;
    }
    .layout-hide-text .layout-text{
        display: none;
    }
    .ivu-col{
        transition: width .2s ease-in-out;
    }
    .ivu-row-flex{
      height: 100%;
    }
    .ivu-badge-dot{
      box-shadow: none;
      margin-left: 40px;
      right: -10px;
    }
    .ivu-avatar-string{
      position: static !important;
    }
    .avatar-dosen{
      color: white;
      left: -5px;
      background-color: #19be6b;
    }
    .avatar-kaprodi{
      color: white;
      left: -5px;
      background-color: #ed3f14;
    }
    .avatar-akademik{
      color: white;
      left: -5px;
      background-color: #1c2438;
    }
    .avatar-mahasiswa{
      color: white;
      background-color: #2d8cf0;
    }
    .ivu-breadcrumb{
      text-transform: capitalize;
    }
    .menu-user .ivu-menu-item-active{
      border-bottom: none !important;
    }
    .menu-user .ivu-menu-item>a>i{
      margin-right: 6px;
    }
    .menu-user .ivu-menu-item>a, .menu-user .ivu-menu-item>i{
      color: #495060;
    }
    .menu-user .ivu-select-dropdown .ivu-menu-item-selected{
      background-color: transparent !important;
    }
    .ivu-menu-horizontal .ivu-menu-submenu .ivu-select-dropdown .ivu-menu-item-selected, .ivu-menu-horizontal .ivu-menu-submenu .ivu-select-dropdown .ivu-menu-item-selected:hover{
      color: #495060;
    }
</style>



