<template lang="html">
  <transition name="fade" mode="out-in">
  <template v-if="ajax_fetch_profile">
    <Spin size="large"></Spin>
  </template>
  <template v-else>
    <div class="layout-content-main">
      <div class="">
        <Card :bordered="false">
          <Row type="flex" class="row">
            <Col span="6">
              <div class="img-profile">{{ inisial_nama }}</div>
            </Col>
            <Col span="18" style="padding:15px">
              <h3 class="profile-nama">{{ mahasiswa.nama_mahasiswa }}</h3>
              <h5 class="profile-nim">NIM : {{ mahasiswa.nim_mahasiswa }}</h5>
              <ul>
                <li><span>Email : </span>{{ mahasiswa.email_mahasiswa }}</li>
                <li><span>No HP : </span>{{ mahasiswa.no_hp_mahasiswa }}</li>
              </ul>
            </Col>
          </Row>
        </Card>
        <br>
        <Row>
          <Col span="24" class="tesis-section">
            <Card :bordered="false">
              <p slot="title">Data Tesis</p>
              <template v-if="ajax_fetch_tesis">
                <Spin size="large"></Spin>
              </template>
              <template v-else>
                <div v-if="tesis.length == 0" class="tidak-ada-data">
                  <div style="text-align:center">
                    <Icon type="document-text"></Icon>
                    <br>
                    <span>Data Tesis Tidak Ditemukan</span>
                  </div>
                </div>
                <card-tesis v-else v-for="(item,index) in tesis" :key="item.id_tesis" :prop_data="item" prop_tipe=""></card-tesis>
              </template>
            </Card>
          </Col>
          <Col span="24">
            <div class="layout-content-main">
              <router-view></router-view>
            </div>
          </Col>
        </Row>
        <br>
        <Row>
          <Col span="24" class="pengajuan-judul-section">
            <Card :bordered="false">
              <p slot="title">Data Pengajuan Judul</p>
              <template v-if="ajax_fetch_pengajuan_proposal">
                <Spin size="large"></Spin>
              </template>
              <template v-else>
                <div v-if="pengajuan_proposal.length == 0" class="tidak-ada-data">
                  <div style="text-align:center">
                    <Icon type="document-text"></Icon>
                    <br>
                    <span>Data Pengajuan Proposal Tidak Ditemukan</span>
                  </div>
                </div>
                <card-pengajuan-proposal v-else v-for="(item,index) in pengajuan_proposal" :key="item.id_pengajuan" :prop_data="item"></card-pengajuan-proposal>
              </template>
            </Card>
          </Col>
        </Row>
      </div>
    </div>
  </template>
</transition>
</template>

<script>
import CardPengajuanProposal from './Card_Pengajuan_Proposal.vue';
import CardTesis from './Card_Tesis.vue';
export default {
  name: "MahasiswaProfile",
  props: {

  },
  //variable
  data: function(){
    return{
      ajax_fetch_profile: '',
      ajax_fetch_tesis: '',
      ajax_fetch_pengajuan_proposal: '',
      mahasiswa: '',
      tesis: [],
      nim: '',
      pengajuan_proposal: [],
      param_nim: this.$route.params.nim,
    }
  },
  computed: {
    inisial_nama: function(){
      var split_nama = this.mahasiswa.nama_mahasiswa.split(" ");
      if(split_nama[1]){
        return split_nama[0].charAt(0)+split_nama[1].charAt(0);
      }
      else{
        return split_nama[0].charAt(0);
      }
    },
  },
  components: {
    CardTesis,
    CardPengajuanProposal
  },
  //methods
  watch: {
    '$route': function(to,from){
      console.log(to);
      console.log(from);
    }
  },
  methods: {
    set_param: function(){
      let a = this.$route.path.split("/");
      if(a[1] == 'tesis'){
        this.nim = this.$store.state.profile.mahasiswa.nim_mahasiswa;
      }
      else if(a[1] == 'profile'){
        this.nim = this.$store.state.profile.mahasiswa.nim_mahasiswa;
      }
      else if(a[1] == 'mahasiswa'){
        this.nim = this.param_nim;
      }
      this.get_mahasiswa_by_nim();
      this.get_tesis_by_nim();
      this.get_pengajuan_proposal();
    },
    get_mahasiswa_by_nim: function(){
      var self = this;
      if(this.$store.state.mahasiswa.length != 0){
        var mahasiswa_ada = this.$store.state.mahasiswa.find(mahasiswa => mahasiswa.nim_mahasiswa == this.nim);
        this.mahasiswa = mahasiswa_ada;
      }
      else{
        if(this.mahasiswa == ''){
          console.log('get data mahasiswa from ajax');
          this.ajax_fetch_profile = true;
          this.$store.dispatch('get_mahasiswa_by_nim',this.nim).then((mahasiswa) => {
              this.mahasiswa = mahasiswa;
              this.ajax_fetch_profile = false;
          });
        }
      }
    },
    get_tesis_by_nim: function(){
      console.log('get tesis from ajax');
      this.ajax_fetch_tesis = true;
      this.$store.dispatch('get_tesis_by_nim',this.nim).then((tesis) => {
        this.tesis = tesis;
        this.ajax_fetch_tesis = false;
      });
    },
    get_pengajuan_proposal: function(){
      var self = this;
      let param = {};
      param.nim = this.nim;
      console.log('get pengajuan_proposal from ajax');
      this.ajax_fetch_pengajuan_proposal = true;
      this.$store.dispatch('get_pengajuan_proposal',param).then((pengajuan_proposal) => {
        this.pengajuan_proposal = pengajuan_proposal;
        this.ajax_fetch_pengajuan_proposal = false;
      });
    },
  },
  //components life cycle hook
  created: function(){
    this.set_param();
    // this.get_mahasiswa_by_nim();
    // this.get_tesis_by_nim();
    // this.get_pengajuan_proposal();
  }
}
</script>

<style lang="css" scoped>
.layout-content-main{
  background-color: #f5f7f9;
}
.row{
  padding: 15px;
  padding-left: 5px;
  background-color: white;
}
.tidak-ada-data{
  height: 150px;
  display:flex;
  justify-content:center;
  align-items:center;
  font-size: 16px;
}
.tidak-ada-data i{
  font-size: 35px;
}
.tesis-section{
  /*margin-bottom: 15px;*/
}
.tesis-section .title{
  font-size: 16px;
  font-weight: 500;
  border-bottom: 1px solid #e9eaec;
}
ul>li{
  font-size: 14px;
}
a{
  color: #495060;
}
a:hover,a.router-link-active{
  color: #2d8cf0;
}
/*profile*/
.img-profile{
  width: 100%;
  height: 100%;
  color: white;
  background-color: #2c85e4;
  font-size: 25px;
  text-transform:uppercase;
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}
.profile-nama,.profile-nim{
  text-transform: capitalize;
  margin-bottom: 10px;
}
.profile-nim{
  color: #80848f;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .2s
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0
}
</style>



