<template lang="html">
  <transition name="fade" mode="out-in">
    <div class="layout-content-main">
      <Row>
        <Col span="24" class="column">
          <div class="column-inner">
            <Spin v-if="ajax_fetch.tesis" fix size="large"></Spin>
            <div v-else>
              <p class="text-label">Judul Tesis</p>
              <p class="text-content">{{ tesis.judul_tesis }}</p>
              <p class="text-label">Bidang Minat</p>
              <p class="text-content">{{ tesis.nama_bidang_minat }}</p>
              <p class="text-label">Tanggal Pengajuan</p>
              <p class="text-content">{{ tesis.tgl_pengajuan }}</p>
              <p class="text-label">Dosen Pembimbing</p>
              <p style="margin-bottom:10px;">
                <Avatar class="avatar" size="small">{{ inisial_nama(tesis.nama_dospem_1) }}</Avatar>
                <span>{{ tesis.nama_dospem_1 }}</span>
              </p>
              <p style="margin-bottom:10px;">
                <Avatar class="avatar" size="small">{{ inisial_nama(tesis.nama_dospem_2) }}</Avatar>
                <span>{{ tesis.nama_dospem_2 }}</span>
              </p>
            </div>
          </div>
        </Col>
        <Col span="24" class="column">
          <Tabs type="card" @on-click="pindah_tab">
            <TabPane label="Pengajuan Judul" icon="clipboard"></TabPane>
            <TabPane label="Ujian Proposal" icon="clipboard"></TabPane>
            <TabPane label="Seminar Hasil" icon="clipboard"></TabPane>
            <TabPane label="Ujian Tesis" icon="clipboard"></TabPane>
          </Tabs>
