            <Spin v-if="ajax_fetch.pengajuan_proposal" size="large"></Spin>
            <template v-else>
              <div v-if="pengajuan_proposal == ''" class="tidak-ada-data">
                <div style="text-align:center">
                  <Icon type="document-text"></Icon>
                  <br>
                  <span>Data tidak ditemukan</span>
                </div>
              </div>
              <div v-else>
                <Col span="12">
                  <p class="text-label">Judul Tesis</p>
                  <p class="text-content">{{ pengajuan_proposal.judul_tesis }}</p>
                  <p class="text-label">Bidang Minat</p>
                  <p class="text-content">{{ pengajuan_proposal.nama_bidang_minat }}</p>
                  <p class="text-label">Tanggal Pengajuan</p>
                  <p class="text-content">{{ pengajuan_proposal.tgl_progress }}</p>
                  <p class="text-label">Pilihan Dosen Pembimbing</p>
