                        <template v-for="(item,index) in timeline_persetujuan_ujian_proposal">
                          <TimelineItem>
                            <p class="time">{{ format_tanggal(item.tgl_selesai) }}</p>
                            <p class="content">{{ item.nama_status_progress }}</p>
                          </TimelineItem>
                          <TimelineItem v-if="index == timeline_persetujuan_ujian_proposal.length-1">
                            <p class="time">{{ format_tanggal(item.tgl_progress) }}</p>
                            <p class="content">Pengajuan persetujuan Ujian Proposal</p>
