          nilai_penyajian_pembimbing_1: 0,
          nilai_mempertahankan_tesis_pembimbing_1: 0,
          saran_pembimbing_1: '',

          nilai_draf_pembimbing_2: 0,
          nilai_penyajian_pembimbing_2: 0,
          nilai_mempertahankan_tesis_pembimbing_2: 0,
          saran_pembimbing_2: '',

          nilai_draf_penguji_1: 0,
          nilai_penyajian_penguji_1: 0,
          nilai_mempertahankan_tesis_penguji_1: 0,
          saran_penguji_1: '',

          nilai_draf_penguji_2: 0,
          nilai_penyajian_penguji_2: 0,
          nilai_mempertahankan_tesis_penguji_2: 0,
          saran_penguji_2: '',
        },
        styles: {
          top: '20px'
        }
      },
      nim: '',

      pengajuan_proposal: '',
      progress_pengajuan_proposal: '',
      progress_persetujuan_ujian_proposal: '',
      progress_pengajuan_ujian_proposal: '',
      pilih_time_line: 1,
      tab: 0,
      tesis: '',

      timeline_pengajuan_proposal: [],
      timeline_persetujuan_ujian_proposal: [],
      timeline_pengajuan_ujian_proposal: [],
      timeline_persetujuan_seminar_hasil: [],
      timeline_pengajuan_seminar_hasil: [],
      timeline_persetujuan_ujian_tesis: [],
      timeline_pengajuan_ujian_tesis: [],

      hasil_ujian_proposal: [],
      hasil_seminar_hasil: [],
      hasil_ujian_tesis: [],
      seminar_hasil: '',
      ubah_status_progress: '',
      ujian_proposal: '',
      ujian_proposal_step: "1",
      ujian_tesis: '',
      link_ba_ujian_proposal: '/Dokumen/Ujian-Proposal/'+this.$route.params.id_tesis,
      link_ba_seminar_hasil: '/Dokumen/Seminar-Hasil/'+this.$route.params.id_tesis,
      link_ba_ujian_tesis: '/Dokumen/Ujian-Tesis/'+this.$route.params.id_tesis
    }
  },
  computed: {

  },
  components: {

  },
  //methods
  watch: {

  },
  methods: {
    // untuk set nim yg digunakan di request
    set_param: function(){
      if(this.$store.state.profile.mahasiswa){
        this.nim = this.$store.state.profile.mahasiswa.nim_mahasiswa;
      }
      else if(this.$store.state.profile.pegawai){
        this.nim = this.$route.params.nim;
      }
    },

    // untuk get tesis dan riwayat pengajuan proposal
    get_tesis_by_id_tesis: function(){
      if(this.$store.state.tesis.length != 0){
        var tesis_ada = this.$store.state.tesis.find(tesis => tesis.id_tesis == this.$route.params.id_tesis);
        this.tesis = tesis_ada;
        this.get_pengajuan_proposal();
        this.get_timeline_pengajuan_proposal();
      }
      else{
        if(this.tesis == ''){
          var self = this;
          console.log('get tesis from ajax');
          this.ajax_fetch.tesis = true;
          this.$store.dispatch('get_tesis_by_id_tesis',this.$route.params.id_tesis).then((tesis) => {
            this.tesis = tesis[0];
            this.get_pengajuan_proposal();
            this.get_timeline_pengajuan_proposal();
            this.ajax_fetch.tesis = false;
          });
        }
      }
    },

    // get riwayat pengajuan judul dari tesis yang sudah ada
    get_pengajuan_proposal: function(){
      console.log('get pengajuan proposal from ajax');
      this.ajax_fetch.pengajuan_proposal = true;
      var self = this;
      let param = {};
      param.id_pengajuan = this.tesis.id_pengajuan_proposal;
      this.$store.dispatch('get_pengajuan_proposal',param).then((pengajuan_proposal) => {
        this.pengajuan_proposal = pengajuan_proposal[0];
        this.ajax_fetch.pengajuan_proposal = false;
      });
    },

    // get ujian proposal dari tesis
    get_ujian_proposal_by_id_tesis: function(){
      console.log('get ujian proposal from ajax');
      this.ajax_fetch.ujian_proposal = true;
      var self = this;
      var parameter = [];
      parameter['id_tesis'] = this.$route.params.id_tesis;
      parameter['tipe_ujian'] = 'UP';
      this.$store.dispatch('get_ujian_by_id_tesis',parameter).then((ujian_proposal) => {
        this.ujian_proposal = ujian_proposal[0];
        if(this.ujian_proposal.penguji_1 != ''){
          let penguji_1 = this.ujian_proposal.penguji_1[0].nama_pegawai;
          this.ujian_proposal.penguji_1 = penguji_1;
        }
        else{
          this.ujian_proposal.penguji_1 = 'Belum Ada';
        }
        if(this.ujian_proposal.penguji_2 != ''){
          let penguji_2 = this.ujian_proposal.penguji_2[0].nama_pegawai;
          this.ujian_proposal.penguji_2 = penguji_2;
        }
        else{
          this.ujian_proposal.penguji_1 = 'Belum Ada';
        }
        this.ajax_fetch.ujian_proposal = false;
        this.get_timeline_persetujuan_ujian_proposal();
        this.get_timeline_pengajuan_ujian_proposal();
        this.get_hasil_ujian_proposal();
        this.get_seminar_hasil();
      });
    },

    //get seminar hasil dari tesis
    get_seminar_hasil: function(){
      console.log('get seminar hasil from ajax');
      this.ajax_fetch.seminar_hasil = true;
      var self = this;
      var parameter = [];
      parameter['id_tesis'] = this.$route.params.id_tesis;
      parameter['tipe_ujian'] = 'SH';
      this.$store.dispatch('get_ujian_by_id_tesis',parameter).then((seminar_hasil) => {
        this.seminar_hasil = seminar_hasil[0];
        this.get_timeline_persetujuan_seminar_hasil();
        this.get_timeline_pengajuan_seminar_hasil();
        this.get_hasil_seminar_hasil();
        this.ajax_fetch.seminar_hasil = false;
        this.get_ujian_tesis();
      });
    },

    get_ujian_tesis: function(){
      console.log('get ujian tesis from ajax');
      this.ajax_fetch.ujian_tesis = true;
      var self = this;
      var parameter = [];
      parameter['id_tesis'] = this.$route.params.id_tesis;
      parameter['tipe_ujian'] = 'UT';
      this.$store.dispatch('get_ujian_by_id_tesis',parameter).then((ujian_tesis) => {
        this.ujian_tesis = ujian_tesis[0];
        if(this.ujian_tesis.penguji_1 != ''){
          let penguji_1 = this.ujian_tesis.penguji_1[0].nama_pegawai;
          this.ujian_tesis.penguji_1 = penguji_1;
        }
        else{
          this.ujian_tesis.penguji_1 = 'Belum Ada';
        }
        if(this.ujian_tesis.penguji_2 != ''){
          let penguji_2 = this.ujian_tesis.penguji_2[0].nama_pegawai;
          this.ujian_tesis.penguji_2 = penguji_2;
        }
        else{
          this.ujian_tesis.penguji_1 = 'Belum Ada';
        }
        this.get_timeline_persetujuan_ujian_tesis();
        this.get_timeline_pengajuan_ujian_tesis();
        this.get_hasil_ujian_tesis();
        this.ajax_fetch.ujian_tesis = false;

      });
    },

    //Get Hasil Ujian Proposal
    get_hasil_ujian_proposal: function(){
      console.log('get hasil ujian proposal from ajax');
      this.ajax_fetch.ujian_proposal = true;
      var self = this;
      var parameter = [];
      parameter['id_ujian'] = this.ujian_proposal.id_ujian;
      this.$store.dispatch('get_hasil_ujian',parameter).then((hasil_ujian_proposal) => {
        hasil_ujian_proposal.forEach(function(val,index,item){
          self.hasil_ujian_proposal.push(item[index]);
        });
        this.ajax_fetch.ujian_proposal = false;
      });
    },
    // Get Hasil Seminar Hasil
    get_hasil_seminar_hasil: function(){
      console.log('get hasil seminar hasil from ajax');
      this.ajax_fetch.seminar_hasil = true;
      var self = this;
      var parameter = [];
      parameter['id_ujian'] = this.seminar_hasil.id_ujian;
      this.$store.dispatch('get_hasil_ujian',parameter).then((hasil_seminar_hasil) => {
        hasil_seminar_hasil.forEach(function(val,index,item){
          self.hasil_seminar_hasil.push(item[index]);
        });
        this.ajax_fetch.seminar_hasil = false;
      });
    },
    get_hasil_ujian_tesis: function(){
      console.log('get hasil ujian tesis from ajax');
      this.ajax_fetch.ujian_tesis = true;
      var self = this;
      var parameter = [];
      parameter['id_ujian'] = this.ujian_tesis.id_ujian;
      this.$store.dispatch('get_hasil_ujian',parameter).then((hasil_ujian_tesis) => {
        hasil_ujian_tesis.forEach(function(val,index,item){
          self.hasil_ujian_tesis.push(item[index]);
        });
        this.ajax_fetch.ujian_tesis = false;
      });
    },
    // Time Line Pengajuan Proposal
    get_timeline_pengajuan_proposal: function(){
      var self = this;
      if(this.$store.state.timeline_pengajuan_proposal.length != 0){
        var timeline = this.$store.state.timeline_pengajuan_proposal;
        timeline.forEach(function(val,index,item){
          if(item[index].id_progress == self.tesis.id_pengajuan_proposal){
            self.timeline_pengajuan_proposal.push(item[index]);
          }
        });
      }
      else{
        if(this.timeline_pengajuan_proposal == ''){
          console.log('get timeline pengajuan proposall from ajax');
          this.ajax_fetch.timeline_pengajuan_proposal = true;
          this.$store.dispatch('get_timeline_pengajuan_proposal',this.tesis.id_pengajuan_proposal).then((time_line) => {
            time_line.forEach(function(val,index,item){
              self.timeline_pengajuan_proposal.push(item[index]);
            });
            self.ajax_fetch.timeline_pengajuan_proposal = false;
          });
        }
      }
    },

    //Time Line persetujuan
    get_timeline_persetujuan_ujian_proposal: function(){
      var self = this;
      console.log('get timeline persetujuan ujian proposall from ajax');
      this.ajax_fetch.timeline_persetujuan_ujian_proposal = true;
      this.$store.dispatch('get_timeline_persetujuan',this.ujian_proposal.id_persetujuan_ujian).then((time_line) => {
        time_line.forEach(function(val,index,item){
          self.timeline_persetujuan_ujian_proposal.push(item[index]);
        });
        self.ajax_fetch.timeline_persetujuan_ujian_proposal = false;
      });
    },
    get_timeline_persetujuan_seminar_hasil: function(){
      var self = this;
      console.log('get timeline persetujuan seminar hasil from ajax');
      this.ajax_fetch.get_timeline_persetujuan_seminar_hasil = true;
      this.$store.dispatch('get_timeline_persetujuan',this.seminar_hasil.id_persetujuan_ujian).then((time_line) => {
        time_line.forEach(function(val,index,item){
          self.timeline_persetujuan_seminar_hasil.push(item[index]);
        });
        self.ajax_fetch.get_timeline_persetujuan_seminar_hasil = false;
      });
    },
