      var self = this;
      console.log('get timeline persetujuan ujian tesis from ajax');
      this.ajax_fetch.get_timeline_persetujuan_ujian_tesis = true;
      this.$store.dispatch('get_timeline_persetujuan',this.ujian_tesis.id_persetujuan_ujian).then((time_line) => {
