    //Insert Nilai
    insert_nilai_ujian_proposal: function(){
      if(this.modal.ujian_proposal.saran_pembimbing_1 != '' &&
         this.modal.ujian_proposal.saran_pembimbing_2 != '' &&
         this.modal.ujian_proposal.saran_penguji_1 != '' &&
         this.modal.ujian_proposal.saran_penguji_2 != ''
       ){
         this.modal.ujian_proposal.nilai_akhir = (this.modal.ujian_proposal.nilai_materi_pembimbing_1+
                                                 this.modal.ujian_proposal.nilai_materi_pembimbing_2+
                                                 this.modal.ujian_proposal.nilai_materi_penguji_1+
                                                 this.modal.ujian_proposal.nilai_materi_penguji_2+
                                                 this.modal.ujian_proposal.nilai_penalaran_pembimbing_1+
                                                 this.modal.ujian_proposal.nilai_penalaran_pembimbing_2+
                                                 this.modal.ujian_proposal.nilai_penalaran_penguji_1+
                                                 this.modal.ujian_proposal.nilai_penalaran_penguji_2)/8;
         var self = this;
         $.ajax({
           type: 'POST',
           url: "https://simt.000webhostapp.com/index.php/Ujian/insert_nilai_ujian",
           data: {
             token: this.$store.state.token,
             tipe: 'ujian-proposal',
             nilai: this.modal.ujian_proposal,
             penguji_1: this.ujian_proposal.id_penguji_1,
             penguji_2: this.ujian_proposal.id_penguji_2,
             pembimbing_1: this.tesis.id_dospem_1,
             pembimbing_2: this.tesis.id_dospem_2,
             id_ujian: this.ujian_proposal.id_ujian
           },
           beforeSend: function(){
             self.ajax_fetch.submit = true;
           },
           success: function(response){
             self.ajax_fetch.submit = false;
             self.modal.ujian_proposal.visible = false;
             if(response.status == 'berhasil'){
               self.$Notice.success({
                 title: 'Insert Nilai Ujian Proposal berhasil',
               });
               self.get_ujian_proposal_by_id_tesis();
             }
           }
         })
       }
       else{
         this.$Message.error('Harap Isi seluruh inputan form');
       }
    },
    insert_nilai_seminar_hasil: function(){
      if(this.modal.seminar_hasil.saran_pembimbing_1 != '' &&
         this.modal.seminar_hasil.saran_pembimbing_2 != ''
       ){
         this.modal.seminar_hasil.nilai_akhir = (this.modal.seminar_hasil.nilai_diskusi_pembimbing_1+
                                                 this.modal.seminar_hasil.nilai_diskusi_pembimbing_1+
                                                 this.modal.seminar_hasil.nilai_draf_pembimbing_1+
                                                 this.modal.seminar_hasil.nilai_draf_pembimbing_2+
                                                 this.modal.seminar_hasil.nilai_penyajian_pembimbing_1+
                                                 this.modal.seminar_hasil.nilai_penyajian_pembimbing_2)/6;
         var self = this;
         $.ajax({
           type: 'POST',
           url: "https://simt.000webhostapp.com/index.php/Ujian/insert_nilai_ujian",
           data: {
             token: this.$store.state.token,
             tipe: 'seminar-hasil',
             nilai: this.modal.seminar_hasil,
             pembimbing_1: this.tesis.id_dospem_1,
             pembimbing_2: this.tesis.id_dospem_2,
             id_ujian: this.seminar_hasil.id_ujian
           },
           beforeSend: function(){
             self.ajax_fetch.submit = true;
           },
           success: function(response){
             self.ajax_fetch.submit = false;
             self.modal.seminar_hasil.visible = false;
             if(response.status == 'berhasil'){
               self.$Notice.success({
                 title: 'Insert Nilai Seminar Hasil Berhasil',
               });
               self.get_seminar_hasil();
             }
           }
         })
       }
       else{
         this.$Message.error('Harap Isi seluruh inputan form');
       }
    },
    insert_nilai_ujian_tesis: function(){
      if(this.modal.ujian_tesis.saran_pembimbing_1 != '' &&
         this.modal.ujian_tesis.saran_pembimbing_2 != '' &&
         this.modal.ujian_tesis.saran_penguji_1 != '' &&
         this.modal.ujian_tesis.saran_penguji_2 != ''
       ){
         this.modal.ujian_tesis.nilai_akhir = (this.modal.ujian_tesis.nilai_draf_pembimbing_1+
                                                 this.modal.ujian_tesis.nilai_draf_pembimbing_2+
                                                 this.modal.ujian_tesis.nilai_draf_penguji_1+
                                                 this.modal.ujian_tesis.nilai_draf_penguji_2+
                                                 this.modal.ujian_tesis.nilai_penyajian_pembimbing_1+
                                                 this.modal.ujian_tesis.nilai_penyajian_pembimbing_2+
                                                 this.modal.ujian_tesis.nilai_penyajian_penguji_1+
                                                 this.modal.ujian_tesis.nilai_penyajian_penguji_2+
                                                 this.modal.ujian_tesis.nilai_mempertahankan_tesis_pembimbing_1+
                                                 this.modal.ujian_tesis.nilai_mempertahankan_tesis_pembimbing_2+
                                                 this.modal.ujian_tesis.nilai_mempertahankan_tesis_penguji_1+
                                                 this.modal.ujian_tesis.nilai_mempertahankan_tesis_penguji_2)/12;

         var self = this;
         $.ajax({
           type: 'POST',
           url: "https://simt.000webhostapp.com/index.php/Ujian/insert_nilai_ujian",
           data: {
             token: this.$store.state.token,
             tipe: 'ujian-tesis',
             nilai: this.modal.ujian_tesis,
             penguji_1: this.ujian_tesis.id_penguji_1,
             penguji_2: this.ujian_tesis.id_penguji_2,
             pembimbing_1: this.tesis.id_dospem_1,
             pembimbing_2: this.tesis.id_dospem_2,
             id_ujian: this.ujian_tesis.id_ujian
           },
           beforeSend: function(){
             self.ajax_fetch.submit = true;
           },
           success: function(response){
             self.ajax_fetch.submit = false;
             self.modal.ujian_tesis.visible = false;
             if(response.status == 'berhasil'){
               self.$Notice.success({
                 title: 'Insert Nilai Ujian Tesis berhasil',
               });
               self.get_ujian_tesis();
             }
           }
         })
       }
       else{
         this.$Message.error('Harap Isi seluruh inputan form');
       }
    },

    inisial_nama: function(nama){
      var split_nama = nama.split(" ");
      if(split_nama.length > 1){
        return split_nama[0].charAt(0)+split_nama[1].charAt(0);
      }
      else{
        return split_nama[0].charAt(0)
      }
    },
    format_tanggal: function(tanggal){
      var days = ['Minggu', 'Senin', 'Selasa', 'Rabu', 'Kamis', 'Jumat', 'Sabtu'];
      var date = new Date(tanggal);
      var month = date.getMonth() + 1;
      var day = date.getDate();
      var nama_hari = days[date.getDay()]
      var year = date.getFullYear();
      return nama_hari+", "+day+"-"+month+"-"+year;
    },
    pindah_tab: function(name){
      this.tab = name;
    },
    show_modal: function(nama_modal){
      if(nama_modal == 'ujian-proposal'){
        this.modal.ujian_proposal.visible = true;
      }
      else if(nama_modal == 'seminar-hasil'){
        this.modal.seminar_hasil.visible = true;
      }
      else if(nama_modal == 'ujian-tesis'){
        this.modal.ujian_tesis.visible = true;
      }
    }
  },
  //components life cycle hook
  created: function(){
    this.set_param();
    this.get_tesis_by_id_tesis();
    this.get_ujian_proposal_by_id_tesis();
  }
}
</script>

