<template lang="html">
  <div class="item-wrap">
    <Avatar class="avatar" size="large">{{ inisial_nama }}</Avatar>
    <router-link :to="link_profile"><span class="nama">{{ dosen.nama_pegawai }}</span></router-link>
    <span class="nim">NIP. {{ dosen.nip }}</span>
  </div>
</template>

<script>
export default {
  name: "DosenList",
  props: {
    data_dosen: '',
  },
  //variable
  data: function(){
    return{
      dosen: this.data_dosen
    }
  },
  computed: {
    link_profile: function(){
      return '/dosen/'+this.dosen.id_pegawai;
    },
    inisial_nama: function(){
      var split_nama = this.dosen.nama_pegawai.split(" ");
      if(split_nama[1]){
        return split_nama[0].charAt(0)+split_nama[1].charAt(0);
      }
      else{
        return split_nama[0].charAt(0);
      }
    }
  },
  components: {

  },
  //methods
  watch: {

  },
  methods: {

  },
  //components life cycle hook
  created: function(){

  }
}
</script>

<style lang="css" scoped>
.item-wrap{
  position: relative;
  top: -5px;
  border-bottom: 1px solid #dddee1;
  margin: 10px;
  padding-bottom: 10px;
  /*font-family: 'Poppins', sans-serif;*/
  line-height: 1;
}
.item-wrap .avatar{
  background-color: #19be6b;
  top: 4px;
}
.item-wrap .nama{
  color: #495060 !important;
  font-size: 15px;
  font-weight: 500;
  position: absolute;
  top: 5px;
  left: 45px;
  text-transform: capitalize;
}
.item-wrap .nim{
  position: absolute;
  left: 45px;
  bottom: 10px;
  font-size: 14px;
  color: #80848f;
}
</style>



