<template lang="html">
  <Spin v-if="ajax_fetch"></Spin>
  <div v-else class="container">
    <!-- status bar -->
    <div class="row align-items-center row-main">
      <div class="col-sm-3">
        <div class="item-count">
          <div class="row align-items-center">
            <div class="col-sm-4 no-padding-right">
              <div class="icon-wrap icon-dosen">
                <Icon type="clipboard" class="icon"></Icon>
              </div>
            </div>
            <div class="col-sm-8 no-padding-left">
              <div class="item-wrap">
                <p class="item-number">
                  {{ jumlah_pengajuan_judul }}
                </p>
                <p class="item-info">
                  <span>Pengajuan Judul</span><br>
                  <span class="item-info-sub">(Semester ini)</span>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-sm-3">
        <div class="item-count">
          <div class="row align-items-center">
            <div class="col-sm-6 no-padding-right">
              <div class="icon-wrap icon-dosen">
                <Icon type="clipboard" class="icon"></Icon>
              </div>
            </div>
            <div class="col-sm-6 no-padding-left">
              <div class="item-wrap">
                <p class="item-number">
                  {{ jumlah_ujian_proposal }}
                </p>
                <p class="item-info">
                  <span>Ujian Proposal</span>
                  <span class="item-info-sub">(Semester ini)</span>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-sm-3">
        <div class="item-count">
          <div class="row align-items-center">
            <div class="col-sm-6 no-padding-right">
              <div class="icon-wrap icon-dosen">
                <Icon type="clipboard" class="icon"></Icon>
              </div>
            </div>
            <div class="col-sm-6 no-padding-left">
              <div class="item-wrap">
                <p class="item-number">
                  {{ jumlah_seminar_hasil }}
                </p>
                <p class="item-info">
                  <span>Seminar Hasil</span>
                  <span class="item-info-sub">(Semester ini)</span>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-sm-3">
        <div class="item-count">
          <div class="row align-items-center">
            <div class="col-sm-6 no-padding-right">
              <div class="icon-wrap icon-dosen">
                <Icon type="clipboard" class="icon"></Icon>
              </div>
            </div>
            <div class="col-sm-6 no-padding-left">
              <div class="item-wrap">
                <p class="item-number">
                  {{ jumlah_ujian_tesis }}
                </p>
                <p class="item-info">
                  <span>Ujian Tesis</span>
                  <span class="item-info-sub">(Semester ini)</span>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- chart  -->
    <div class="row">
      <div class="col-sm-7">
        <Card :bordered="false">
          <p slot="title">Jumlah Bimbingan Dosen</p>
          <Table border height="370" :columns="kolom_dosen" :data="bimbingan_dosen" class="tabel-bimbingan-dosen"></Table>
        </Card>
      </div>
      <div class="col-sm-5">
        <Card :bordered="false">
            <p slot="title">Bidang Minat Dosen</p>
            <dosen-chart v-if="!ajax_fetch_bimbingan_dosen" :bidang_minat="bidang_minat_dosen"></dosen-chart>
        </Card>
      </div>
    </div>
    <!-- section 3 -->
    <div class="row">
      <div class="col">
        <Card :bordered="false">
          <p slot="title">Aktivitas Terbaru</p>
          <ul class="aktivitas-list">
            <li v-for="data in aktivitas_tebaru" class="row">
              <div class="col-auto no-padding-right">
                <Avatar :class="bg_avatar">{{ inisial_nama(data.nama_pegawai) }}</Avatar>
              </div>
              <div class="col no-padding">
                <p class="aktivitas-user">
                  <span class="nama">{{ data.nama_pegawai }}</span>
                  <span class="waktu">
                    <timeago :since="data.tgl_selesai"></timeago>
                  </span>
                </p>
                <p class="aktivitas-act">{{ data.nama_status_progress }}</p>
              </div>
            </li>
          </ul>
        </Card>
      </div>
      <div class="col">
        <Card :bordered="false">
          <p slot="title">Menunggu Persetujuan Anda</p>
          <ul class="aktivitas-list">
            <li v-for="data in menunggu_persetujuan" class="row">
              <div class="col-auto no-padding-right">
                <Icon type="information-circled" color="#ff9900" size="25"></Icon>
              </div>
              <div class="col aktivitas-user-wrap">
                <p class="aktivitas-user">
                  <span class="nama">{{ data.nama_mahasiswa }}</span>
                  <span class="waktu">
                    <timeago :since="data.tgl_selesai"></timeago>
                  </span>
                </p>
                <p class="aktivitas-act">{{ data.nama_status_progress }}</p>
              </div>
            </li>
          </ul>
        </Card>
      </div>
    </div>
  </div>
</template>

<script>
import DosenChart from './Dosen_Chart.vue';
export default {
  name: 'DashboardKaprodi',
  props: {

  },
  //variable
  data: function(){
    return{
      ajax_fetch: '',
      ajax_fetch_bimbingan_dosen: '',
      bidang_minat_dosen: {},
      menunggu_persetujuan: {},
      aktivitas_tebaru: {},
      // total baner
      jumlah_pengajuan_judul: 0,
      jumlah_ujian_proposal: 0,
      jumlah_seminar_hasil: 0,
      jumlah_ujian_tesis: 0,
      // list aktivitas
      bg_avatar: 'avatar-dosen',
      //kolom table dosen
      kolom_dosen: [
        {
          title: 'Nama',
          key: 'nama_dosen',
          sortable: true
        },
        {
          title: 'Berjalan',
          key: 'proses_bimbingan',
          width: 120,
          sortable: true
        },
        {
          title: 'Selesai',
          key: 'selesai_bimbingan',
          width: 120,
          sortable: true
        },
        {
          title: 'Total',
          key: 'jumlah_bimbingan',
          width: 80,
          sortable: true
        }
      ],
      bimbingan_dosen: [],
    }
  },
  computed: {
    time: function(){
      return '2017-12-03 17:33:00';
    }
  },
  components: {
    DosenChart
  },
  //methods
  watch: {

  },
  methods: {
    inisial_nama: function(nama){
      var split_nama = nama.split(" ");
      if(split_nama.length > 1){
        return split_nama[0].charAt(0)+split_nama[1].charAt(0);
      }
      else{
        return split_nama[0].charAt(0)
      }
    },
    get_bimbingan_dosen: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Pegawai/get_bimbingan_dosen/",
        data: {token: self.$store.state.token},
        beforeSend: function(){
          self.ajax_fetch_bimbingan_dosen = true;
        },
        success: function(response){
          response.bimbingan_dosen.forEach(function(val,index,item){
            var data = {};
            data.nama_dosen = val.nama_pegawai;
            data.proses_bimbingan = val.bimbingan_berjalan;
            data.selesai_bimbingan = val.bimbingan_selesai;
            data.jumlah_bimbingan = val.bimbingan_total;
            data.id_bidang_minat = val.id_bidang_minat;
            if(val.id_bidang_minat == 1){
              self.bidang_minat_dosen.pemasaran = val.id_bidang_minat;
            }
            else if(val.id_bidang_minat == 2){
              self.bidang_minat_dosen.keuangan = val.id_bidang_minat;
            }
            else if(val.id_bidang_minat == 3){
              self.bidang_minat_dosen.msdm = val.id_bidang_minat;
            }
            else if(val.id_bidang_minat == 4){
              self.bidang_minat_dosen.sim = val.id_bidang_minat;
            }
            else if(val.id_bidang_minat == 5){
              self.bidang_minat_dosen.pariwisata = val.id_bidang_minat;
            }
            self.bimbingan_dosen.push(data);
          });
          self.ajax_fetch_bimbingan_dosen = false;
        }
      })
    },
    get_menunggu_persetujuan:function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ubah_Status_Progress/get_menunggu_persetujuan",
        data: {token: self.$store.state.token},
        beforeSend: function(){
          self.ajax_fetch = true;
        },
        success: function(response){
          self.menunggu_persetujuan = response.ubah_status_progress;
          // response.bimbingan_dosen.forEach(function(val,index,item){
          //   var data = {};
          //   data.nama_dosen = val.nama_pegawai;
          //   data.proses_bimbingan = val.bimbingan_berjalan;
          //   data.selesai_bimbingan = val.bimbingan_selesai;
          //   data.jumlah_bimbingan = val.bimbingan_total;
          //   data.id_bidang_minat = val.id_bidang_minat;
          //   if(val.id_bidang_minat == 1){
          //     self.bidang_minat_dosen.pemasaran = val.id_bidang_minat;
          //   }
          //   else if(val.id_bidang_minat == 2){
          //     self.bidang_minat_dosen.keuangan = val.id_bidang_minat;
          //   }
          //   else if(val.id_bidang_minat == 3){
          //     self.bidang_minat_dosen.msdm = val.id_bidang_minat;
          //   }
          //   else if(val.id_bidang_minat == 4){
          //     self.bidang_minat_dosen.sim = val.id_bidang_minat;
          //   }
          //   else if(val.id_bidang_minat == 5){
          //     self.bidang_minat_dosen.pariwisata = val.id_bidang_minat;
          //   }
          //   self.bimbingan_dosen.push(data);
          // });
          // console.log(self.bimbingan_dosen);
          self.ajax_fetch = false;
        }
      })
    },
    get_aktivitas_terbaru: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ubah_Status_Progress/aktivitas_terbaru",
        data: {token: self.$store.state.token},
        beforeSend: function(){
          self.ajax_fetch = true;
        },
        success: function(response){
          self.aktivitas_tebaru = response.ubah_status_progress;
          // self.jumlah_pengajuan_judul = response.pengajuan_judul;
          // self.jumlah_ujian_proposal = response.ujian_proposal;
          // self.jumlah_seminar_hasil = response.seminar_hasil;
          // self.jumlah_ujian_tesis = response.ujian_tesis;
          // self.menunggu_persetujuan = response.ubah_status_progress;
          self.ajax_fetch = false;
        }
      })
    },
    get_total_progress: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Progress/get_total_tiap_progress",
        data: {token: self.$store.state.token},
        beforeSend: function(){
          self.ajax_fetch = true;
        },
        success: function(response){
          self.jumlah_pengajuan_judul = response.pengajuan_judul;
          self.jumlah_ujian_proposal = response.ujian_proposal;
          self.jumlah_seminar_hasil = response.seminar_hasil;
          self.jumlah_ujian_tesis = response.ujian_tesis;
          // self.menunggu_persetujuan = response.ubah_status_progress;
          self.ajax_fetch = false;
        }
      })
    }
  },
  //components life cycle hook
  created: function(){
    this.get_bimbingan_dosen();
    this.get_menunggu_persetujuan();
    this.get_total_progress();
    this.get_aktivitas_terbaru();
  }
}
</script>

<style lang="css" scoped>
  .row{
    margin-bottom: 10px;
    /*height: 100px;*/
    /*background-color: white;*/
  }
  .item-count{
    background-color: white;
  }
  .chart{
    background-color: white;
  }
  .item-wrap{
    text-align: center;
  }
  .icon-wrap{
    text-align: center;
  }
  .icon-dosen{
    background-color: #2d8cf0;
  }
  .icon{
    font-size: 50px;
    padding-top: 10px;
    padding-bottom: 10px;
    color: white;
  }
  .item-number{
    font-size: 30px;
    font-weight: bold;
    color: #2d8cf0;
    line-height: 1;
  }
  .item-info{
    color: #bbbec4;
    font-size: 14px;
    line-height: 1;
  }
  .item-info-sub{
    font-size: 12px;
    line-height: 1;
  }
  /*altivitas list*/
  .aktivitas-list li{
    border-bottom: 1px solid #e9eaec;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .avatar-dosen{
    color: white;
    background-color: #19be6b;
  }
  .aktivitas-user{
    position: relative;
  }
  .aktivitas-user .nama{
    font-weight: 600;
    font-size: 14px;
  }
  .aktivitas-user .waktu{
    position: absolute;
    right: 10px;
    color: #bbbec4;
  }
  .aktivitas-user-wrap{
    padding-left: 5px;
  }
  .aktivitas-act{
    color: #495060;
  }
  /* override */
  .ivu-table{
    max-height: 452px !important;
    overflow-y: scroll;
  }
  /*helper*/
  .no-padding{
    padding: 0;
  }
  .no-padding-right{
    padding-right: 0;
  }
  .no-padding-left{
    padding-left: 0;
  }
</style>



