<template lang="html">
  <Row class-name="batas">
    <Spin v-if="ajax_fetch_data" fix size="large"></Spin>
    <template v-else>
      <Col span="24" class="title-section">
        <Col span="4">
          <img src="./../assets/logo-ub.png" alt="" class="img-fluid">
        </Col>
        <Col span="20">
          <p class="top-title">KEMENTERIAN RISET, TEKNOLOGI DAN PENDIDIKAN TINGGI</p>
          <p class="top-title">UNIVERSITAS BRAWIJAYA</p>
          <p class="top-title">FAKULTAS ILMU ADMINISTRASI</p>
          <p class="sub-title">Jl. MT. Haryono 163, Malang 65145, Indonesia</p>
          <p class="sub-title">Telp. : +62-341-553737, 568914, 558226   Fax : +62-341-558227</p>
          <p class="sub-title">http://fia.ub.ac.id                  E-mail: fia@ub.ac.id</p>
        </Col>
      </Col>
      <Col span="24">
        <p class="berita-acara">BERITA ACARA UJIAN PROPOSAL PENELITIAN TESIS</p>
        <p class="section-separator">Mahasiswa tersebut dibawah ini:</p>
        <table>
          <tr>
            <td style="width:250px;">Nama</td>
            <td>:</td>
            <td>{{ tesis.nama_mahasiswa }}</td>
          </tr>
          <tr>
            <td style="width:250px;">Nomor Induk Mahasiswa</td>
            <td>:</td>
            <td>{{tesis.nim_mahasiswa }}</td>
          </tr>
          <tr>
            <td style="width:250px;">Program Studi</td>
            <td>:</td>
            <td>Magister Administrasi Bisnis</td>
          </tr>
          <tr>
            <td style="width:250px;">Kekhususan</td>
            <td>:</td>
            <td>-</td>
          </tr>
          <tr>
            <td style="width:250px;">Judul</td>
            <td>:</td>
            <td>{{ tesis.judul_tesis }}</td>
          </tr>
        </table>
        <p class="section-separator">yang diselenggarakan pada :</p>
        <table>
          <tr>
            <td style="width:250px;">Hari</td>
            <td>:</td>
            <td>{{ format_hari(ujian_proposal.tgl_ujian) }}</td>
          </tr>
          <tr>
            <td style="width:250px;">Tanggal</td>
            <td>:</td>
            <td>{{ format_tanggal(ujian_proposal.tgl_ujian) }}</td>
          </tr>
          <tr>
            <td style="width:250px;">Jam</td>
            <td>:</td>
            <td>{{ ujian_proposal.waktu_mulai_ujian }} - {{ ujian_proposal.waktu_selesai_ujian }}</td>
          </tr>
          <tr>
            <td style="width:250px;">Nilai</td>
            <td>:</td>
            <td>{{ ujian_proposal.nilai }}</td>
          </tr>
        </table>
        <table class="table-penilaian">
          <tr>
            <th colspan="2" style="width:60%;text-align:center">Nama</th>
            <th style="width:40%;text-align:center">Nilai</th>
          </tr>
          <tr>
            <td>Ketua</td>
            <td>{{ tesis.nama_dospem_1 }}</td>
            <td>{{ (parseInt(hasil_ujian_proposal[0].nilai_1)+parseInt(hasil_ujian_proposal[0].nilai_2))/2 }}</td>
          </tr>
          <tr>
            <td>Anggota</td>
            <td>{{ tesis.nama_dospem_2 }}</td>
            <td>{{ (parseInt(hasil_ujian_proposal[1].nilai_1)+parseInt(hasil_ujian_proposal[1].nilai_2))/2 }}</td>
          </tr>
          <tr>
            <td>Anggota</td>
            <td>{{ ujian_proposal.penguji_1 }}</td>
            <td>{{ (parseInt(hasil_ujian_proposal[2].nilai_1)+parseInt(hasil_ujian_proposal[2].nilai_2))/2 }}</td>
          </tr>
          <tr>
            <td>Anggota</td>
            <td>{{ ujian_proposal.penguji_2 }}</td>
            <td>{{ (parseInt(hasil_ujian_proposal[3].nilai_1)+parseInt(hasil_ujian_proposal[3].nilai_2))/2 }}</td>
          </tr>
        </table>
        <br><br>
        <Col span="8" offset="16">
          <p>Malang,  </p>
          <p>Tim Penguji</p>
          <p>Ketua</p>
          <br>
          <br>
          <br>
          <p>{{ tesis.nama_dospem_1 }}</p>
          <p>NIP. {{ tesis.NIP_dospem_1 }}</p>
        </Col>
      </Col>
    </template>
  </Row>
</template>

<script>
export default {
  name: "DokumenBAUjianProposal",
  props: {

  },
  //variable
  data: function(){
    return{
      tesis: '',
      ujian_proposal: '',
      hasil_ujian_proposal: [],
      ajax_fetch_data: ''

    }
  },
  computed: {

  },
  components: {

  },
  //methods
  watch: {

  },
  methods: {
    get_tesis_by_id_tesis: function(){
      console.log('get tesis from ajax');
      this.ajax_fetch_data = true;
      var self = this;
      this.$store.dispatch('get_tesis_by_id_tesis',this.$route.params.id_tesis).then((tesis) => {
        this.tesis = tesis[0];
        this.get_ujian_by_id_tesis();
        // this.ajax_fetch.tesis = false;
      });
    },
    get_ujian_by_id_tesis: function(){
      console.log('get ujian proposal from ajax');
      var self = this;
      var args = [];
      args['id_tesis'] = this.$route.params.id_tesis;
      args['tipe_ujian'] = 'UP';
      this.$store.dispatch('get_ujian_by_id_tesis',args).then((ujian_proposal) => {
        this.ujian_proposal = ujian_proposal[0];
        this.get_hasil_ujian();
        this.ujian_proposal.penguji_1 = this.ujian_proposal.penguji_1[0].nama_pegawai;
        this.ujian_proposal.penguji_2 = this.ujian_proposal.penguji_2[0].nama_pegawai;
        // this.ajax_fetch.ujian_proposal = false;
      });
    },
    get_hasil_ujian: function(){
      console.log('get hasil ujian proposal from ajax');
      var self = this;
      var args = [];
      args['id_ujian'] = this.ujian_proposal.id_ujian;
      this.$store.dispatch('get_hasil_ujian',args).then((hasil_ujian_proposal) => {
        hasil_ujian_proposal.forEach(function(val,index,item){
          self.hasil_ujian_proposal.push(item[index]);
        });
        this.ajax_fetch_data = false;
        setTimeout(function(){
          window.print();
          // if(this.hasil_ujian_proposal){
          //
          // }
        },200);
      });
    },
    format_hari: function(tanggal){
      var days = ['Minggu', 'Senin', 'Selasa', 'Rabu', 'Kamis', 'Jumat', 'Sabtu'];
      var date = new Date(tanggal);
      var day = date.getDate();
      var nama_hari = days[date.getDay()]
      return nama_hari;
    },
    format_tanggal: function(tanggal){
      var date = new Date(tanggal);
      var month = date.getMonth() + 1;
      var day = date.getDate();
      var year = date.getFullYear();
      return day+"-"+month+"-"+year;
    },
  },
  //components life cycle hook
  created: function(){
    this.get_tesis_by_id_tesis();
  }
}
</script>

<style lang="css" scoped>
*{
  color: black;
  font-size: 14px;
  border: none;
}
.batas{
  width: 210mm;
  margin-left: auto;
  margin-right: auto;
}
@media print {
    html, body {
    width: 210mm;
  }
}
.title-section{
  border-bottom: 5px solid black;
}
.top-title{
  font-size: 20px;
  line-height: 1;
  text-align: center;
  color: black;
}
.sub-title{
  font-size: 14px;
  text-align: center;
  color: black;
}
.berita-acara{
  text-align: center;
  text-decoration: underline;
  font-size: 16px;
  font-weight: bold;
  margin-top: 20px;
  margin-bottom: 40px;
  letter-spacing: 0;
}
.section-separator{
  margin-top: 15px;
  margin-bottom: 15px;
}
.table-penilaian{
  width: 100%;
}
.table-penilaian td,th{
  border: 1px solid black;
}
img{
  width: 100%;
  height: auto;
}
</style>



