<template lang="html">
  <div class="layout-content-main">
    <div class="">
      <Card :bordered="false">
        <p slot="title">Pengajuan Judul</p>
        <a slot="extra">tes</a>
        <PengajuanProposalCard
          v-for="(item,index) in pengajuan_proposal"
          :key="item.id_pengajuan"
           prop_tipe="limit_mode"
          :prop_data_pengajuan="item">
        </PengajuanProposalCard>
        </Card>
    </div>
  </div>
</template>

<script>
import PengajuanProposalCard from './Pengajuan_Proposal_Card.vue';
export default {
  name: "MahasiswaPengajuanJudul",
  props: {

  },
  //variable
  data: function(){
    return{
      pengajuan_proposal: '',
    }
  },
  computed: {

  },
  components: {
    PengajuanProposalCard
  },
  //methods
  watch: {

  },
  methods: {
    get_pengajuan_proposal: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Pengajuan_Proposal/get_pengajuan_proposal",
        data: {token: this.$store.state.token,nim_mahasiswa: this.$route.params.nim },
        success: function(response){
          self.pengajuan_proposal = response.pengajuan_proposal;
          console.log(response);
        }
      })
    },
    tes: function(){
      console.log(this.$route.params);
    }
  },
  //components life cycle hook
  created: function(){
    this.get_pengajuan_proposal();
  }
}
</script>

<style lang="css">
</style>



