<template lang="html">
  <Card :bordered="false">
    <p slot="title">Ubah Password</p>
    <div class="row">
      <div class="col-md-3"></div>
      <div class="col-md-6">
        <div class="form-group">
          <label for="password">Password Baru</label>
          <input
          v-model="password"
          type="password"
          :class="{'form-control': true,'is-invalid': password_error}"
          name="password"
          placeholder="Password">
          <div class="invalid-feedback">
            {{ password_error }}
          </div>
        </div>
        <div class="form-group">
          <label for="ulangi_password">Ulangi Password Baru</label>
          <input
          v-model="ulangi_password"
          type="password"
          :class="{'form-control': true,'is-invalid': ulangi_password_error}"
          name="ulangi_password"
          placeholder="Password">
          <div class="invalid-feedback">
            {{ ulangi_password_error }}
          </div>
        </div>
        <Button @click="submit()" type="primary">Submit</Button>
      </div>
    </div>
  </Card>
</template>

<script>
export default {
  name: 'ResetPass',
  props: {

  },
  //variable
  data: function(){
    return{
      password: '',
      ulangi_password: ''
    }
  },
  computed: {
    password_error: function(){
      if(this.password == ''){
        return 'Harap isi password baru anda';
      }
      else{
        return '';
      }
    },
    ulangi_password_error: function(){
      if(this.ulangi_password == ''){
        return 'Harap ulangi password baru anda';
      }
      else if(this.password != this.ulangi_password){
        return 'Passoword tidak sama';
      }
      else{
        return '';
      }
    }
  },
  components: {

  },
  //methods
  watch: {

  },
  methods: {
    submit: function(){
      var self = this;
      if(this.password_error == '' && this.ulangi_password_error == ''){
        $.ajax({
          type: 'POST',
          url: "https://simt.000webhostapp.com/index.php/Login/ubah_pass",
          data: {token: self.$store.state.token, password: self.password},
          success: function(html){
            if(html.status != 'Gagal'){
              self.$Message.success('Ubah Password berhasil');
              //log out
              self.$store.commit('delete_token');
              self.$store.commit('delete_tesis');
              self.$store.commit('delete_profile');
              self.$store.commit('delete_profile');
              self.$store.commit('delete_pengajuan_proposal');
              self.$store.commit('reset_dosen_pembimbing');
              self.$store.commit('delete_mahasiswa');
              self.$router.push('/login');
            }
            else{
              self.$Message.error('Ubah Password gagal');
            }
          }
        })
      }
      else{
        this.$Message.error('Harap cek kembali inputan anda');
      }
    }
  },
  //components life cycle hook
  created: function(){

  }
}
</script>

<style lang="css">
</style>



