<template lang="html">
  <input type="text" name="" value="" placeholder="Pilih Tanggal..">
</template>

<script>
import flatpickr from 'flatpickr';
import 'flatpickr/dist/flatpickr.min.css';
import { Indonesian } from "flatpickr/dist/l10n/id.js"
import 'flatpickr/dist/themes/airbnb.css';
export default {
  name: 'PilihTanggal',
  props: {

  },
  data: function(){
    return{

    }
  },
  watch: {

  },
  methods: {
    selected_date: function(tanggal){
      this.$emit('pilih_tanggal',tanggal);
    }
  },
  mounted: function(){
    var self = this;
    flatpickr(this.$el, {
      "locale": Indonesian,
      "altInput": true,
      "time_24hr": false,
      "minDate": new Date().fp_incr(7),
      "disable": [
        function(date) {
          return (date.getDay() === 0 || date.getDay() === 6);
        }
      ],
      onClose: function(selectedDates, dateStr, instance) {
        self.selected_date(dateStr);
      },
    });
  }
}
</script>

<style lang="css">
  .form-control:disabled, .form-control[readonly]{
    background-color: white !important;
  }
</style>



