<template lang="html">
  <div class="layout-content-main">
    <div class="">
      <Card :bordered="false">
        <p slot="title">Pengajuan Ujian Proposal Tesis</p>
        <ujian-proposal-card
          v-for="(item,index) in progress"
          :key="item.id_progress"
          :prop_data_progress="item"
          :prop_index="index">
          </ujian-proposal-card>
      </Card>
    </div>
  </div>
</template>

<script>
import UjianProposalCard from './Ujian_Proposal_Card.vue';
export default {
  name: 'PengajuanUjianProposal',
  prop: {

  },
  data: function(){
    return {
      progress: ''
    }
  },
  computed: {

  },
  components: {
    UjianProposalCard
  },
  watch: {

  },
  methods: {
    get_progress: function(){
      this.$store.commit('set_tipe_modal','ujian_proposal');
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ujian/get_progress_ujian",
        data: {token: self.$store.state.token,id_tipe_progress: 'PRG03'},
        success: function(response){
          self.progress = response.progress;
          console.log(response);
          self.progress.forEach(function(){
            self.$store.commit('push_modal_dosen_pembimbing');
          });
        }
      })
    },
  },
  created: function(){
    this.get_progress();
  }
}
</script>

<style lang="css">

</style>



