<template lang="html">
  <Card :bordered="false" :dis-hover="true">
    <p slot="title">Pengajuan Judul dan dosen Pembimbing</p>
    <div class="step-content-wrap">
      <h5 class="form-title">
        Form Pengajuan Judul dan dosen Pembimbing
      </h5>
      <div  class="row justify-content-center">
        <div class="col-sm-10">
          <Spin v-if="ajax_fetch_data"></Spin>
          <div  v-else class="card-block">
            <form @submit.prevent="submit_pengajuan">
              <div class="form-group">
                <label for="judul_tesis">Judul Tesis<span class="required-star">*</span></label>
                <input
                  v-model="form_pengajuan.judul_tesis"
                  v-validate="'required'"
                  :class="{'form-control': true,'is-invalid': errors.has('judul_tesis')}"
                  type="text"
                  id="judul_tesis"
                  name="judul_tesis"
                  placeholder="Masukkan Judul Tesis">
                <div v-show="errors.has('judul_tesis')" class="invalid-feedback">
                  {{ errors.first('judul_tesis') }}
                </div>
              </div>
              <div class="form-group">
                <label for="tipe_penelitian">Tipe Penelitian<span class="required-star">*</span></label>
                <select
                  v-model="form_pengajuan.tipe_penelitian"
                  v-validate="'required'"
                  :class="{'form-control': true,'is-invalid': errors.has('tipe_penelitian')}"
                  name="tipe_penelitian"
                  id="tipe_penelitian">
                    <option value="" disabled>Pilih tipe penelitian</option>
                    <option v-for="bm in bidang_minat" :value="bm.id_bidang_minat" :key="bm.id_bidang_minat">{{ bm.nama_bidang_minat }}</option>
                </select>
                <div v-show="errors.has('tipe_penelitian')" class="invalid-feedback">
                  {{ errors.first('tipe_penelitian') }}
                </div>
              </div>
              <!-- linkage penelitian -->
              <div class="form-group linkage-group">
                <label for="judul_tesis">Linkage Peneltian</label>
              </div>
              <div class="form-group linkage-item">
                <label for="linkage_dosen">Dosen<span class="required-star">*</span></label>
                <input
                  v-model="form_pengajuan.linkage_dosen"
                  v-validate="'required'"
                  :class="{'form-control': true,'is-invalid': errors.has('linkage_dosen')}"
                  type="text"
                  id="linkage_dosen"
                  name="linkage_dosen"
                  placeholder="Masukkan Nama Dosen">
                <div v-show="errors.has('linkage_dosen')" class="invalid-feedback">
                  {{ errors.first('linkage_dosen') }}
                </div>
              </div>
              <div class="form-group linkage-item">
                <label for="linkage_tema">Tema<span class="required-star">*</span></label>
                <input
                  v-model="form_pengajuan.linkage_tema"
                  v-validate="'required'"
                  :class="{'form-control': true,'is-invalid': errors.has('linkage_tema')}"
                  type="text"
                  id="linkage_tema"
                  name="linkage_tema"
                  placeholder="Masukkan Tema Penelitian">
                <div v-show="errors.has('linkage_tema')" class="invalid-feedback">
                  {{ errors.first('linkage_tema') }}
                </div>
              </div>
              <!-- calon dosen -->
              <div class="form-row">
                <div class="col-md-12">
                  <label for="dosen_1">Calon Dosen Pembimbing 1<span class="required-star">*</span></label>
                </div>
                <div :class="form_size">
                  <input
                    v-model="form_pengajuan.dosen_1.nama_pegawai"
                    v-validate:dosen_1_ada="'required'"
                    :class="{'form-control': true,'is-invalid': errors.has('dosen_pembimbing_1')}"
                    type="text"
                    name="dosen_pembimbing_1"
                    id="dosen_1"
                    readonly>
                    <div v-show="errors.has('dosen_pembimbing_1')" class="invalid-feedback">
                      {{ errors.first('dosen_pembimbing_1') }}
                    </div>
                </div>
                <div :class="form_size_button">
                  <Button v-if="dosen_1_ada" type="error" icon="close-round" @click="delete_dospem(1)"></Button>
                  <Button v-else type="primary" @click="tampil_modal_dospem(1)" long>Pilih</Button>
                </div>
              </div>
              <div class="form-row">
                <div class="col-md-12">
                  <label for="dosen_2">Calon Dosen Pembimbing 2<span class="required-star">*</span></label>
                </div>
                <div :class="form_size">
                  <input
                    v-model="form_pengajuan.dosen_2.nama_pegawai"
                    v-validate:dosen_2_ada="'required'"
                    :class="{'form-control': true,'is-invalid': errors.has('dosen_pembimbing_2')}"
                    type="text"
                    name="dosen_pembimbing_2"
                    id="dosen_2"
                    readonly>
                    <div v-show="errors.has('dosen_pembimbing_2')" class="invalid-feedback">
                      {{ errors.first('dosen_pembimbing_2') }}
                    </div>
                </div>
                <div :class="form_size_button">
                  <Button v-if="dosen_2_ada" type="error" icon="close-round" @click="delete_dospem(2)"></Button>
                  <Button v-else type="primary" @click="tampil_modal_dospem(2)" long>Pilih</Button>
                </div>
              </div>
              <div class="form-row">
                <div class="col-md-12">
                  <label for="dosen_3">Calon Dosen Pembimbing (Opsional)</label>
                </div>
                <div :class="form_size">
                  <input
                    v-model="form_pengajuan.dosen_3.nama_pegawai"
                    type="text"
                    id="dosen_3"
                    class="form-control"
                    readonly>
                </div>
                <div :class="form_size_button">
                  <Button v-if="dosen_3_ada" type="error" icon="close-round" @click="delete_dospem(3)"></Button>
                  <Button v-else type="primary" @click="tampil_modal_dospem(3)" long>Pilih</Button>
                </div>
              </div>
              <div class="form-row">
                <div class="col-md-12">
                  <label for="dosen_4">Calon Dosen Pembimbing (Opsional)</label>
                </div>
                <div :class="form_size">
                  <input
                    v-model="form_pengajuan.dosen_4.nama_pegawai"
                    type="text"
                    id="dosen_4"
                    class="form-control"
                    readonly>
                </div>
                <div :class="form_size_button">
                  <Button v-if="dosen_4_ada" type="error" icon="close-round" @click="delete_dospem(4)"></Button>
                  <Button v-else type="primary" @click="tampil_modal_dospem(4)" long>Pilih</Button>
                </div>
              </div>
              <div class="form-row">
                <div class="col-md-12">
                  <label for="dosen_5">Calon Dosen Pembimbing (Opsional)</label>
                </div>
                <div :class="form_size">
                  <input
                    v-model="form_pengajuan.dosen_5.nama_pegawai"
                    type="text" id="dosen_5"
                    class="form-control"
                    readonly>
                </div>
                <div :class="form_size_button">
                  <Button v-if="dosen_5_ada" type="error" icon="close-round" @click="delete_dospem(5)"></Button>
                  <Button v-else type="primary" @click="tampil_modal_dospem(5)" long>Pilih</Button>
                </div>
              </div>
              <Button :loading="ajax_submit" html-type="submit"	size="large" type="primary">Submit</Button>
            </form>
          </div>
        </div>
      </div>
    </div>
    <Modal-Pilih-Dosen
      v-if="ajax_fetch_data == false"
      tipe="pengajuan_proposal"
      :modal_dosen="$store.state.modal_pilih_pembimbing.show"
      :row_dosen="$store.state.modal_pilih_pembimbing.row_dosen"
      :dosen="$store.state.modal_pilih_pembimbing.dosen_pilihan">
      </Modal-Pilih-Dosen>
  </Card>
</template>
<script>
import ModalPilihDosen from './Modal-pilih-dosen.vue';
export default {
  name: 'PengajuanProposalForm',
  props: ['id_proposal'],
  data: function() {
    return {
      row_dosen: '',
      bidang_minat: [],
      form_pengajuan: {
        judul_tesis: '',
        tipe_penelitian: '',
        linkage_dosen: '',
        linkage_tema: '',
        dosen_1: '',
        dosen_2: '',
        dosen_3: '',
        dosen_4: '',
        dosen_5: '',
      },
      ajax_submit: false,
      ajax_fetch_data: true,
      modal_dospem: {
        nomor_modal: '',
        status_modal: false
      }
    }
  },
  computed: {
    dosen_1_ada: function(){
      return this.form_pengajuan.dosen_1.nama_pegawai;
    },
    dosen_2_ada: function(){
      return this.form_pengajuan.dosen_2.nama_pegawai;
    },
    dosen_3_ada: function(){
      return this.form_pengajuan.dosen_3.nama_pegawai;
    },
    dosen_4_ada: function(){
      return this.form_pengajuan.dosen_4.nama_pegawai;
    },
    dosen_5_ada: function(){
      return this.form_pengajuan.dosen_5.nama_pegawai;
    },
    form_size: function(){
      return 'col-md-11';
    },
    form_size_button: function(){
      return 'col-md-1';
    },
  },
  created: function(){
    this.get_dosen();
    this.get_bidang_minat();
    this.set_modal_dosen_pembimbing();
  },
  components: {
    ModalPilihDosen
  },
  watch: {
    '$store.state.modal_pilih_pembimbing': {
      handler: function(after,before){
        console.log('aa');
        this.form_pengajuan.dosen_1 = this.$store.state.modal_pilih_pembimbing.dosen_pilihan[0].dosen_1;
        this.form_pengajuan.dosen_2 = this.$store.state.modal_pilih_pembimbing.dosen_pilihan[0].dosen_2;
        this.form_pengajuan.dosen_3 = this.$store.state.modal_pilih_pembimbing.dosen_pilihan[0].dosen_3;
        this.form_pengajuan.dosen_4 = this.$store.state.modal_pilih_pembimbing.dosen_pilihan[0].dosen_4;
        this.form_pengajuan.dosen_5 = this.$store.state.modal_pilih_pembimbing.dosen_pilihan[0].dosen_5;
      },
      deep: true,
    }
  },
  methods: {
    get_dosen: function(){
      var self = this;
      if(this.$store.state.dosen.length == 0){
        console.log('get dosen from ajax');
        this.$store.dispatch('get_all_dosen').then(() => {
          self.row_dosen = self.$store.state.dosen;
          self.row_dosen.forEach(function(val,index,item){
            self.row_dosen[index].status = 'Not Selected';
          });
          self.ajax_fetch_data = false;
        });
      }
      else{
        console.log('get dosen from store');
        self.row_dosen = self.$store.state.dosen;
        self.row_dosen.forEach(function(val,index,item){
          self.row_dosen[index].status = 'Not Selected';
        });
        self.ajax_fetch_data = false;
      }
    },
    get_bidang_minat: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Bidang_Minat",
        data: {token: this.$store.state.token},
        beforeSend: function(){
          this.ajax_fetch_data = true;
        },
        success: function(response){
          response.forEach(function(val,index,item){
            self.bidang_minat.push(val);
          });
          self.ajax_fetch_data = false;
        }
      })
    },
    submit_pengajuan: function(){
      var self = this;
      this.$validator.validateAll().then((result) => {
        if (result) {
          $.ajax({
            type: 'POST',
            url: "https://simt.000webhostapp.com/index.php/Pengajuan_Proposal",
            data: {token: this.$store.state.token, data: self.form_pengajuan},
            beforeSend: function(){
              self.ajax_submit = true;
            },
            success: function(response){
              if(response.status == 'Berhasil'){
                self.$Notice.success({
                  title: 'Pengajuan berhasil disubmit!',
                });
                self.ajax_submit = true;
                self.$router.push('/pengajuan-judul');
              }
              console.log(response);
            }
          })
          return;
        }
        self.$Message.error('Harap lengkapi form pengajuan')
      });
    },
    //show modal dospem
    tampil_modal_dospem(no_dospem){
      this.$store.commit('pilih_dosen_pembimbing_ke',no_dospem);
      this.$store.commit('open_modal_pilih_pembimbing');
    },
    delete_dospem(no_dospem){
      this.$store.commit('pilih_dosen_pembimbing_ke',no_dospem);
      this.$store.commit('hapus_dosen_pembimbing');
    },
    modal_close: function(){
      this.modal_dospem.status_modal = false;
    },
    set_modal_dosen_pembimbing: function(){
      this.$store.commit('reset_dosen_pembimbing');
      this.$store.commit('set_tipe_modal','pengajuan_proposal');
      this.$store.commit('push_modal_dosen_pembimbing');
    },
  }
}
</script>
<style lang="css" scoped>
  .card{
    padding: 1rem;
  }
  .card-block{
    border: 1px solid #e5e5e5;
    padding: 15px 20px 20px 20px;
  }
  .form-title{
    text-align: center;
    font-weight: 500;
    margin-top: 10px;
    margin-bottom: 40px;
  }
  .form-sub-title{
    position: relative;
    top: -20px;
  }
  .form-row{
    margin-bottom: 1rem;
  }
  .status-wrap{
    background-color: #f5f7f9;
  }
  .status-inner{
    background-color: white;
    padding: 20px;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .required-star{
    color: #ed3f14;
    font-size: 18px;
    font-weight: bold;
  }
  .linkage-group{
    margin: 0;
  }
  .linkage-item{
    margin-left: 30px;
  }
  .ivu-modal{
    width: 90%;
    top: 0;
  }
  .ivu-card-head{
    position: relative;
  }
  .ivu-card-head p, .ivu-card-head-inner{
    height: auto !important;
  }
  .ivu-steps{
    margin-bottom: 15px;
    margin-top: 15px;
  }
  .ivu-btn-group{
    position: absolute;
    right: 15px;
  }
  .step-content-wrap{
    margin-top: 30px;
    min-height: 400px;
  }
  .text-label{
    font-weight: bold;
  }
  .text-wrap{
    margin-top: 10px;
    margin-bottom: 5px;
  }
</style>



