<template lang="html">
  <transition name="fade" mode="out-in">
    <Row type="flex" class="item">
      <!-- pengajuan item header -->
      <Col span="18" class="tesis">
        <p class="judul">{{ pengajuan_proposal.judul_tesis }}</p>
        <p class="nama-mhs"><span>{{ pengajuan_proposal.nama_mahasiswa }} - </span><span class="nim-mhs">{{ pengajuan_proposal.nim_mahasiswa }}</span></p>
        <p class="detail">
          <span class="text-label">Calon Dosen Pembimbing : </span>
          <p v-for="(item,index) in pengajuan_proposal.calon_dospem" style="margin-bottom:10px;">
            <Avatar class="avatar-dosen" size="small">{{ inisial_nama(item.nama_pegawai) }}</Avatar>
            <span>{{ item.nama_pegawai }}</span>
          </p>
        </p>
        <p class="detail">
          <span class="text-label">Bidang Minat : </span>
          <Tag color="yellow">{{ pengajuan_proposal.nama_bidang_minat }}</Tag>
        </p>
      </Col>
      <!-- pengajuan item button group -->
      <Col span="5" offset="1" style="text-align:right">
        <div class="button-link">
          <router-link :to="link_detail_tesis">Detail</router-link>
        </div>
      </Col>
    </Row>
  </transition>
</template>

<script>
export default {
  name: "CardPengajuanProposal",
  props: {
    prop_data: ''
  },
  //variable
  data: function(){
    return{
      pengajuan_proposal: this.prop_data,
      param_nim: this.$route.params.nim
    }
  },
  computed: {
    link_detail_tesis: function(){
      let a = this.$route.path.split("/");
      if(a[1] == 'profile' || a[1] == 'tesis'){
        return '/mahasiswa/'+this.$store.state.profile.mahasiswa.nim_mahasiswa+'/pengajuan-judul/'+this.pengajuan_proposal.id_pengajuan;
      }
      else{
        return '/mahasiswa/'+this.param_nim+'/pengajuan-judul/'+this.pengajuan_proposal.id_pengajuan;
      }
    }
  },
  components: {

  },
  //methods
  watch: {

  },
  methods: {
    inisial_nama: function(nama){
      var split_nama = nama.split(" ");
      if(split_nama.length > 1){
        return split_nama[0].charAt(0)+split_nama[1].charAt(0);
      }
      else{
        return split_nama[0].charAt(0)
      }
    },
  },
  //components life cycle hook
  created: function(){

  }
}
</script>

<style lang="css" scoped>
  .tesis .judul{
    color: #2b85e4 !important;
    font-weight: bold;
    font-size: 16px;
  }
  .tesis .detail{
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .tesis .detail .text-label{
    font-weight: 500;
  }
  .tesis .nama-mhs{
    margin-top: 10px;
    margin-bottom: 10px;
    font-weight: 500;
  }
  .tesis .avatar-dosen{
    color: white;
    left: 0px;
    margin-right: 5px;
    background-color: #19be6b;
  }
  .button-link a{
    color: white;
    padding: 3px 8px;
    font-size: 14px;
    border-radius: 3px;
    background-color: #2db7f5;
    line-height: 1.5;
  }
</style>



