<template lang="html">
  <div class="layout-content-main">
    <div class="">
      <Card :bordered="false">
        <p slot="title">Pengajuan Ujian Tesis</p>
        <Spin v-if="ajax_fetch_data"></Spin>
        <template v-else>
          <div v-if="ujian_tesis.length == 0" class="tidak-ada-data">
            <div style="text-align:center">
              <Icon type="document-text"></Icon>
              <br>
              <span>Data pengajuan judul tidak ditemukan</span>
            </div>
          </div>
          <ujian-tesis-card
          v-else
          v-for="(item,index) in ujian_tesis"
          :key="item.id_ujian_tesis"
          :prop_index="index"
          :prop_data_progress="item">
          </ujian-tesis-card>
        </template>
        </Card>
    </div>
  </div>
</template>

<script>
import UjianTesisCard from './ujian_tesis_Card.vue';
export default {
  name: 'UjianTesisPengajuan',
  props: {

  },
  //variable
  data: function(){
    return{
      ajax_fetch_data: '',
      ujian_tesis: [],
    }
  },
  computed: {

  },
  components: {
    UjianTesisCard,
  },
  //methods
  watch: {

  },
  methods: {
    get_pengajuan_ujian_tesis: function(){
      this.$store.commit('set_tipe_modal','ujian_tesis');
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ujian/get_progress_ujian",
        data: {token: self.$store.state.token,id_tipe_progress: 'PRG09'},
        success: function(response){
          self.ujian_tesis = response.progress;
          console.log(response);
          self.ujian_tesis.forEach(function(){
            self.$store.commit('push_modal_dosen_pembimbing');
          });
        }
      })
    },
  },
  //components life cycle hook
  created: function(){
    this.get_pengajuan_ujian_tesis();
  }
}
</script>

<style lang="css">
</style>



