<template lang="html">
  <Modal v-model="modal_dosen_visibility" v-on:on-cancel="modal_close"  width="85%" class-name="modal-pilih-dosen">
    <p slot="header" style="color:#f60;text-align:center">
      <Icon type="information-circled"></Icon>
      <span v-if="tipe == 'pengajuan_proposal' || tipe == 'persetujuan_proposal'">Pilih Dosen Pembimbing</span>
      <span v-else-if="tipe == 'ujian_proposal' || tipe == 'ujian_tesis'">Pilih Dosen Penguji</span>
    </p>
    <div>
      <table style="width:100%">
        <tr>
          <th>Nama Dosen</th>
          <th>Bidang Minat</th>
          <th v-if="tipe == 'pengajuan_proposal' || tipe == 'persetujuan_proposal'">Bimbingan Berjalan</th>
          <th v-if="tipe == 'pengajuan_proposal' || tipe == 'persetujuan_proposal'">Bimbingan Selesai</th>
          <th v-if="tipe == 'pengajuan_proposal' || tipe == 'persetujuan_proposal'">Bimbingan Total</th>
          <th>Aksi</th>
        </tr>
        <tr v-for="(dosen,index) in row_dosen">
          <td>
            <template v-if="dosen.rekomendasi">
              <span>{{ dosen.nama_pegawai }}</span>
              <Tooltip v-if="dosen.rekomendasi != 'alternatif_dospem'" :content="dosen.rekomendasi" placement="right">
                <Icon type="information-circled" style="color:#19be6b"></Icon>
              </Tooltip>
              <Tooltip v-else content="rekomendasi dospem" placement="right">
                <Icon type="information-circled"></Icon>
              </Tooltip>
            </template>
            <template v-else>
              <span>{{ dosen.nama_pegawai }}</span>
            </template>
            <Tag color="yellow" v-if="dosen.status != 'Not Selected'">
              {{ dosen.status }}
            </Tag>
          </td>
          <td>{{ dosen.nama_bidang_minat }}</td>
          <td v-if="tipe == 'pengajuan_proposal' || tipe == 'persetujuan_proposal'">{{ dosen.bimbingan_berjalan }}</td>
          <td v-if="tipe == 'pengajuan_proposal' || tipe == 'persetujuan_proposal'">{{ dosen.bimbingan_selesai }}</td>
          <td v-if="tipe == 'pengajuan_proposal' || tipe == 'persetujuan_proposal'">{{ dosen.bimbingan_total }}</td>
          <td>
            <Button type="primary" long @click="pilih_dosen(index)" :disabled="dosen.status != 'Not Selected'">Pilih</Button>
          </td>
        </tr>
      </table>
    </div>
    <div slot="footer"></div>
</Modal>
</template>

<script>
export default {
  name: 'ModalPilihDosen',
  data: function(){
    return {
      index: '',
      modal_dosen_visibility: this.modal_dosen,
      dosen_pilihan_count: 0,
      dosen_selected_1: '',
      dosen_selected_2: '',
      dosen_selected_3: '',
      dosen_selected_4: '',
      dosen_selected_5: '',
    }
  },
  props: {
    dospem_1: '',
    dospem_2: '',
    tipe: '',
    modal_dosen: '',
    dosen: '',
    dosen_pilihan_mhs: '',
    row_dosen: ''
  },
  computed: {
    
  },
  watch: {
    // watch_me: {
    //   handler: function(after,before){
    //     this.dosen_selected_1 = this.$store.state.modal_pilih_pembimbing.dosen_pilihan[this.index].dosen_1;
    //     this.dosen_selected_2 = this.$store.state.modal_pilih_pembimbing.dosen_pilihan[this.index].dosen_2;
    //   },
    //   deep: true,
    // },
    // dosen: {
    //   handler(val, oldVal){
    //     this.dosen_selected_1 = this.dosen.dosen_1;
    //     this.dosen_selected_2 = this.dosen.dosen_2;
    //     this.dosen_selected_3 = this.dosen.dosen_3;
    //     this.dosen_selected_4 = this.dosen.dosen_4;
    //     this.dosen_selected_5 = this.dosen.dosen_5;
    //   },
    //   deep: true
    // },
    modal_dosen: function(){
      var self = this;
      this.modal_dosen_visibility = this.modal_dosen;
      this.index = this.$store.state.modal_pilih_pembimbing.index;
      this.row_dosen.forEach(function(val,index,item){
        //Set label
        if(self.$store.state.modal_pilih_pembimbing.tipe == 'pengajuan_proposal'){
          if(item[index].id_pegawai == self.$store.state.modal_pilih_pembimbing.dosen_pilihan[0].dosen_1.id_pegawai){
            if(self.$store.state.modal_pilih_pembimbing.tipe == 'pengajuan_proposal'){
              item[index].status = "Calon Pembimbing 1";
            }
          }
          else if(item[index].id_pegawai == self.$store.state.modal_pilih_pembimbing.dosen_pilihan[0].dosen_2.id_pegawai){
            if(self.$store.state.modal_pilih_pembimbing.tipe == 'pengajuan_proposal'){
              item[index].status = "Calon Pembimbing 2";
            }
          }
          else if(item[index].id_pegawai == self.$store.state.modal_pilih_pembimbing.dosen_pilihan[0].dosen_3.id_pegawai){
            if(self.$store.state.modal_pilih_pembimbing.tipe == 'pengajuan_proposal'){
              item[index].status = "Calon Pembimbing";
            }
          }
          else if(item[index].id_pegawai == self.$store.state.modal_pilih_pembimbing.dosen_pilihan[0].dosen_4.id_pegawai){
            if(self.$store.state.modal_pilih_pembimbing.tipe == 'pengajuan_proposal'){
              item[index].status = "Calon Pembimbing";
            }
          }
          else if(item[index].id_pegawai == self.$store.state.modal_pilih_pembimbing.dosen_pilihan[0].dosen_5.id_pegawai){
            if(self.$store.state.modal_pilih_pembimbing.tipe == 'pengajuan_proposal'){
              item[index].status = "Calon Pembimbing";
            }
          }
          else{
            item[index].status = "Not Selected";
          }
        }
        else if(self.$store.state.modal_pilih_pembimbing.tipe == 'persetujuan_proposal' || self.$store.state.modal_pilih_pembimbing.tipe == 'ujian_proposal' || self.$store.state.modal_pilih_pembimbing.tipe == 'ujian_tesis'){
          if(item[index].id_pegawai == self.$store.state.modal_pilih_pembimbing.dosen_pilihan[self.index].dosen_1.id_pegawai){
            if(self.$store.state.modal_pilih_pembimbing.tipe == 'ujian_proposal' || self.$store.state.modal_pilih_pembimbing.tipe == 'ujian_tesis'){
              item[index].status = "Penguji 1";
            }
            else{
              item[index].status = "Pembimbing 1";
            }
          }
          else if(item[index].id_pegawai == self.$store.state.modal_pilih_pembimbing.dosen_pilihan[self.index].dosen_2.id_pegawai){
            if(self.$store.state.modal_pilih_pembimbing.tipe == 'ujian_proposal' || self.$store.state.modal_pilih_pembimbing.tipe == 'ujian_tesis'){
              item[index].status = "Penguji 2";
            }
            else{
              item[index].status = "Pembimbing 2";
            }
          }
          else{
            // Dosen Pembimbing tidak bisa jadi dosen penguji
            if(item[index].id_pegawai == self.dospem_1){
              console.log('sini 1');
              item[index].status = "Pembimbing 1";
            }
            else if(item[index].id_pegawai == self.dospem_2){
              console.log('sini 2');
              item[index].status = "Pembimbing 2";
            }
            else{
              console.log('madara');
              item[index].status = "Not Selected";
            }
          }
        }
      });
    },
  },
  created: function(){
    // this.get_dosen();
  },
  methods: {
    pilih_dosen(index){
      // this.$emit('pilih_dosen',this.row_dosen[index]);
      this.$store.commit('pilih_dosen_pembimbing',this.row_dosen[index]);
      this.$store.commit('close_modal_pilih_pembimbing');
    },
    hapus_dosen(index){
      // console.log(this.row_dosen[index].status);
      if(this.row_dosen[index].status == 'Pembimbing 1' || this.row_dosen[index].status == 'Penguji 1'){
        this.row_dosen[index].status = 'Not Selected';
        this.$emit('hapus_dosen',1);
      }
      else if(this.row_dosen[index].status == 'Pembimbing 2' || this.row_dosen[index].status == 'Penguji 2'){
        this.row_dosen[index].status = 'Not Selected';
        this.$emit('hapus_dosen',2);
      }
    },
    modal_close: function(){
      this.row_dosen.forEach(function(val,index,item){
        val.rekomendasi = '';
      });
      this.$store.commit('close_modal_pilih_pembimbing');
      // this.$emit('modal_close');
    },
  }
}
</script>

<style lang="css">
  .ivu-table .selected-row td{
    background-color: red;
  }
  .ivu-table .aw td{
    background-color: gray;
  }
  tr,td,th {
    padding: 8px 6px;
    border: 1px solid #e9eaec;
  }
  th{
    padding: 12px 6px;
  }
  .modal-pilih-dosen .ivu-modal{
    top: 1rem;
  }
</style>



