<template lang="html">
  <div class="layout-content-main">
    <div class="">
      <Card :bordered="false">
        <p slot="title">Pengajuan Ujian Tesis</p>
        <span v-if="bisa_ujian_tesis" slot="extra">
          <router-link to="/Ujian-Tesis/Pengajuan">
            <Button type="primary" icon="plus">
              Ajukan Ujian Tesis
            </Button>
          </router-link>
        </span>
        <Spin v-if="ajax_fetch_data"></Spin>
        <template v-else>
          <div v-if="tesis.length == 0" class="tidak-ada-data">
            <div style="text-align:center">
              <Icon type="document-text"></Icon>
              <br>
              <span>Data Tesis tidak ditemukan,<br> Ujian Tesis Belum Bisa Diajukan</span>
            </div>
          </div>
          <div v-else-if="ujian_tesis.length == 0" class="tidak-ada-data">
            <div style="text-align:center">
              <Icon type="document-text"></Icon>
              <br>
              <span>Data peganjuan ujian tesis tidak ditemukan</span>
            </div>
          </div>
          <card-tesis
          v-else
          v-for="(item,index) in tesis"
          key="index"
          :prop_data="item"
          :prop_ujian="ujian_tesis"
          prop_tipe="ujian-tesis">
          </card-tesis>
        </template>
        </Card>
    </div>
  </div>
</template>

<script>
import UjianTesisCard from './ujian_tesis_Card.vue';
import CardTesis from './Card_Tesis.vue';
export default {
  name: 'UjianTesisList',
  props: {

  },
  //variable
  data: function(){
    return{
      ajax_fetch_data: '',
      ujian_tesis: [],
      tesis: '',
      status: this.$store.state.profile.status_ujian_tesis,
      // tipe_progres_ujian_tesis: '',
    }
  },
  computed: {
    bisa_ujian_tesis: function(){
      if(this.status == 'pengajuan baru'){
        return true;
      }
      else{
        return false;
      }
    },
  },
  components: {
    UjianTesisCard,
    CardTesis
  },
  //methods
  watch: {

  },
  methods: {    
    get_tesis: function(){
      console.log('get tesis from ajax');
      let nim = this.$store.state.profile.mahasiswa.nim_mahasiswa;
      this.ajax_fetch_data = true;
      var self = this;
      this.$store.dispatch('get_tesis_by_nim',nim).then((tesis) => {
        this.tesis = tesis;
        if(this.tesis.length != 0){
          this.get_ujian_tesis();
        }
        else{
          this.ajax_fetch_data = false;
        }
      });
    },
    get_ujian_tesis: function(){
      var self = this;
      this.tesis.forEach(function(val,index,item){
        var args = {};
        args.id_tesis = item[index].id_tesis;
        args.tipe_ujian = 'UT'
        self.$store.dispatch('get_ujian_by_id_tesis',args).then((ujian_tesis) => {
          self.ujian_tesis = ujian_tesis;
        });
      });
      this.ajax_fetch_data = false;
    }
  },
  //components life cycle hook
  created: function(){
    this.get_tesis();
  }
}
</script>

<style lang="css">
</style>



