<template lang="html">
  <transition name="fade" mode="out-in">
    <router-view v-if="!ajax_fetch_data" key="content"></router-view>
  </transition>
</template>

<script>
export default {
  name: "UjianProposalIndex",
  props: {

  },
  //variable
  data: function(){
    return{
      ajax_fetch_data: true
    }
  },
  computed: {

  },
  components: {

  },
  //methods
  watch: {

  },
  methods: {
    get_dosen: function(){
      var self = this;
      if(this.$store.state.dosen.length == 0){
        console.log(this.$store.state.dosen.length);
        console.log('get dosen from ajax');
        this.$store.dispatch('get_all_dosen').then(() => {
          self.ajax_fetch_data = false;
        });
      }
      else{
        self.ajax_fetch_data = false;
      }
    }
  },
  //components life cycle hook
  created: function(){
    this.get_dosen();
  }
}
</script>

<style lang="css" scoped>
.fade-enter-active, .fade-leave-active {
  transition: opacity .2s
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0
}
</style>



