<template lang="html">
  <Card :bordered="false" :dis-hover="true">
    <p slot="title">Pengajuan Ujian Tesis</p>
    <template>
      <span class="text-help">Tahapan Pengajuan Ujian Tesis</span>
      <Steps>
        <Step title="Tahap 1" content="Persetujuan Ujian Tesis" style="width:25%"></Step>
        <Step title="Tahap 2" content="Isi Form Pengajuan Ujian" style="width:25%"></Step>
        <Step title="Tahap 3" content="Persetujuan Akhir" style="width:25%"></Step>
        <Step title="Tahap 4" content="Selesai" style="width:25%"></Step>
      </Steps>
      <hr>
      <transition-group name="fade" mode="out-in">
        <div class="step-content-wrap" key="step_1">
          <h5 class="text-center text-title">Persetujuan Ujian Tesis</h5>
          <Row type="flex" justify="center" align="middle">
            <Col span="18" class="pengajuan-form-persetujuan">
              <Col span="24">
                <Tabs>
                  <TabPane label="Form Persetujuan" icon="compose">
                    <Col span="24">
                      <form>
                        <div class="form-group">
                          <label for="judul_tesis">Judul Tesis</label>
                          <input
                            v-model="tesis.judul_tesis"
                            type="text"
                            id="judul_tesis"
                            class="form-control"
                            disabled>
                        </div>
                        <div class="form-group">
                          <label for="bidang_minat">Bidang Minat</label>
                          <input
                            v-model="tesis.nama_bidang_minat"
                            type="text"
                            id="bidang_minat"
                            class="form-control"
                            disabled>
                        </div>
                        <div class="form-group">
                          <label for="pembimbing_1">Dosen Pembimbing 1</label>
                          <input
                            v-model="tesis.nama_dospem_1"
                            type="text"
                            id="pembimbing_1"
                            class="form-control"
                            disabled>
                        </div>
                        <div class="form-group">
                          <label for="pembimbing_2">Dosen Pembimbing 2</label>
                          <input
                            v-model="tesis.nama_dospem_2"
                            type="text"
                            id="pembimbing_2"
                            class="form-control"
                            disabled>
                        </div>
                        <!-- tombol ajukan persetujuan -->
                        <Button v-if="is_pengajuan_persetujuan" type="primary" @click="ajukan_persetujuan_ujian_tesis()" size="large" long :disabled="loading_submit_persetujuan">
                          <span v-if="loading_submit_persetujuan">
                            Sedang Menginput..
                          </span>
                          <span v-else>
                            Ajukan persetujuan
                          </span>
                        </Button>
                        <!-- tombol jika persetujuan sudah dilakukan-->
                        <div v-else class="button-selesai">
                          <span><Icon type="checkmark"></Icon>Pengajuan persetujuan sudah dilakukan</span>
                        </div>
                      </form>
                    </Col>
                  </TabPane>
                  <TabPane label="Status Persetujuan" icon="ios-analytics">
                    <Row type="flex" align="middle" justify="center" style="background-color:#f5f7f9">
                      <Col span="19" style="background-color:white;padding:20px;">
                        <h4 class="text-label text-heading-status">Status Persetujuan Ujian Tesis</h4>
                          <template v-if="loading_get_status_persetujuan">
                            <Spin></Spin>
                          </template>
                          <template v-else>
                            <p class="text-wrap"><span class="text-label">Kaprodi : </span></p>
                            <Alert show-icon>
                              <span>-</span>
                            </Alert>
                            <p class="text-wrap"><span class="text-label">Dospem 1 : {{ tesis.nama_dospem_1 }}</span></p>
                            <Alert show-icon>
                              <span>-</span>
                            </Alert>
                            <p class="text-wrap"><span class="text-label">Dospem 2 : {{ tesis.nama_dospem_2 }}</span></p>
                            <Alert show-icon>
                              <span>-</span>
                            </Alert>
                          </template>
                      </Col>
                    </Row>
                  </TabPane>
                </Tabs>
              </Col>
            </Col>
          </Row>
        </div>
      </transition-group>
    </template>
  </Card>
</template>

<script>
export default {
  name: 'UjianTesisForm',
  props: ['id_tesis'],
  data: function(){
    return {
      form_persetujuan_ujian_tesis: {
        item_read_only: true,
      },
      form_pengajuan_disabled: '',
      form_persetujuan_disabled: '',
      tesis: '',
      ujian_tesis: {},

      is_pengajuan_persetujuan: true,
      is_pengajuan_ujian: '',

      loading_get_status_persetujuan: false,
      loading_get_status_pengajuan: false,
      loading_submit_persetujuan: false,
      loading_submit_pengajuan: false
    }
  },
  computed: {
    pengajuan_ujian_tesis_baru: function(){
      if(this.id_tesis === 'Pengajuan-Seminar-Hasil'){
        return true;
      }
      else{
        return false;
      }
    },
    next_button_disabled: function(){
      if(this.ujian_tesis_step_copy == this.ujian_tesis_step){
        return true
      }
      else{
        return false;
      }
    },
    prev_button_disabled: function(){
      if(this.ujian_tesis_step_copy == 0){
        return true;
      }
      else{
        false;
      }
    }
  },
  components: {

  },
  watch: {

  },
  methods: {
    get_tesis: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Tesis/get_tesis",
        data: {token: self.$store.state.token},
        success: function(response){
          self.tesis = response.tesis[0];
          console.log(self.tesis);
        }
      })
    },
    ajukan_persetujuan_ujian_tesis: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ujian/ajukan_persetujuan_ujian",
        data: {
          token: self.$store.state.token,
          id_tesis: self.tesis.id_tesis,
          tipe_ujian: 'UT'
        },
        beforeSend: function(){
          self.loading_submit_persetujuan = true;
        },
        success: function(response){
          if(response.status == 'Berhasil'){
            self.loading_submit_persetujuan = false;
            self.$Notice.success({
              title: 'Data persetujuan berhasil disimpan',
            });
            self.is_pengajuan_persetujuan = false;
            self.$router.push('/ujian-tesis');
            // refresh status persetujuan
          }
        }
      })
    }
  },
  created: function(){
    this.get_tesis();
  },
  mounted: function(){
    // this.start_flatpickr();
  }
}
</script>

<style lang="css" scoped>
  .step-content-wrap{
    margin-top: 30px;
  }
  .text-help{
    color: #9ea7b4;
  }
  .text-title{
    margin-top: 10px;
    margin-bottom: 40px;
  }
  .text-label{
    font-weight: bold;
  }
  .text-wrap{
    margin-top: 10px;
    margin-bottom: 5px;
  }
  .card-footer{
    background-color: white;
    /*border-top: 1px solid #c3cbd6;*/
    /*border-bottom: 1px solid #9ea7b4;*/
  }
  .text-heading-status{
    color: #2e8cf0;
  }
  .ivu-steps{
    margin-bottom: 40px;
    margin-top: 20px;
  }
  .ivu-tabs{
    /*overflow-y: visible;*/
    /*height: 500px;*/
  }
  .pengajuan-submit{
    margin-top: 20px;
    margin-bottom: 20px;
    padding-left: 20px;
    padding-right: 20px;
  }
  .pengajuan-pilih-waktu,.pengajuan-form-persetujuan,.pengajuan-persetujuan-kaprodi{
    border: 1px solid #e5e5e5;
    padding: 15px 20px 20px 20px;
  }
  .pengajuan-nav{
    margin-bottom: 15px;
  }
  .pengajuan-back,.pengajuan-next{
    font-size: 14px;
    color: #2e8cf0 !important;
  }
  .pengajuan-back i{
    margin-right: 5px;
    font-size: 16px;
  }
  .pengajuan-next i{
    margin-left: 5px;
    font-size: 16px;
  }
  .pengauan-alert{
    margin-bottom: 20px;
  }
  .alert-status{
    display: inline-block;
    padding: 8px 28px 8px 38px;
  }
  .accordion-content{
    overflow: visible;
    padding-bottom: 80px;
  }
  .ivu-card-head{
    position: relative;
  }
  .ivu-btn-group{
    position: absolute;
    right: 15px;
  }
  .ivu-collapse{
    background-color: #f5f7f9;
  }
  .tag-status{
    background-color: #19be6b;
    color: white;
    padding: 3px;
  }
  .inline-alert{
    display: inline-block;
  }
  .button-selesai{
    display: inline-block;
    margin-bottom: 0;
    font-weight: 400;
    text-align: center;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    background-image: none;
    border: 1px solid transparent;
    white-space: nowrap;
    line-height: 1.5;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    padding: 6px 15px;
    font-size: 12px;
    border-radius: 4px;
    transition: color .2s linear,background-color .2s linear,border .2s linear;
    color: #495060;
    background-color: #f7f7f7;
    border-color: #dddee1;
    padding: 6px 15px 7px 15px;
    font-size: 14px;
    border-radius: 4px;
    width: 100%;
    color: #fff;
    background-color: #19be6b;
    border-color: #19be6b;
  }
  .icon-helper{
    /*position: absolute;
    right: 2%;
    top: 25%;*/
    color: #5c6b77;
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity .2s
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0
  }
  tr,td,th {
    padding: 5px;
    border: 1px solid black;
  }
  .tidak-ada-data{
    height: 200px;
    display:flex;
    justify-content:center;
    align-items:center;
    font-size: 16px;
  }
  .tidak-ada-data i{
    font-size: 35px;
  }
  .tidak-ada-data span{
    font-weight: bold;
    color: #495060;
  }
  .tidak-ada-data small{
    color: #80848f;
  }
  .hide {
    overflow: hidden;
    max-height: 0;
    padding-top: 0;
    padding-bottom: 0;
    margin-top: 0;
    margin-bottom: 0;
    -moz-transition-duration: 0.3s;
    -webkit-transition-duration: 0.3s;
    -o-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -moz-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
    -webkit-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
  }
  .show {
    -moz-transition-duration: 0.3s;
    -webkit-transition-duration: 0.3s;
    -o-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -moz-transition-timing-function: ease-in;
    -webkit-transition-timing-function: ease-in;
    -o-transition-timing-function: ease-in;
    transition-timing-function: ease-in;
    max-height: 1000px;
    overflow: hidden;
  }
</style>



