<template lang="html">
  <transition name="fade" mode="out-in">
    <template v-if="ajax_fetch_data">
      <Spin size="large"></Spin>
    </template>
    <template v-else>
      <div class="layout-content-main">
        <div class="">
          <div class="dosen-list-wrap">
            <div class="card">
              <div class="card-header">
                List Dosen
              </div>
              <div class="card-body">
                <Dosen-List v-for="(item,index) in dosen" :key="item.nip_dosen" :data_dosen="item"></Dosen-List>
              </div>
            </div>
          </div>
        </div>
      </div>
    </template>
  </transition>
</template>

<script>
import DosenList from './Dosen_List.vue';
export default {
  name: "DosenIndex",
  props: {

  },
  //variable
  data: function(){
    return{
      ajax_fetch_data: '',
      dosen: []
    }
  },
  computed: {

  },
  components: {
    DosenList
  },
  //methods
  watch: {

  },
  methods: {
    get_dosen: function(){
      this.ajax_fetch_data = true;
      this.$store.dispatch('get_all_dosen').then((dosen) => {
        this.dosen = dosen;
        this.ajax_fetch_data = false;
      });
    },
  },
  //components life cycle hook
  created: function(){
    this.get_dosen();
  }
}
</script>

<style lang="css" scoped>
.layout-content-main{
  background-color: white;
}
.dosen-list-wrap{
  padding: 15px;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .2s
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0
}
.hide {
  overflow: hidden;
  max-height: 0;
  padding-top: 0;
  padding-bottom: 0;
  margin-top: 0;
  margin-bottom: 0;
  -moz-transition-duration: 0.3s;
  -webkit-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -moz-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
  -webkit-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
}
.show {
  -moz-transition-duration: 0.3s;
  -webkit-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -moz-transition-timing-function: ease-in;
  -webkit-transition-timing-function: ease-in;
  -o-transition-timing-function: ease-in;
  transition-timing-function: ease-in;
  max-height: 1000px;
  overflow: hidden;
}
</style>



