<template lang="html">
  <transition name="fade" mode="out-in">
    <Row type="flex" class="item">
      <!-- pengajuan item header -->
      <Col span="18" class="tesis">
        <p class="judul">
          <router-link v-if="prop_tipe != ''" :to="link_detail">{{ tesis.judul_tesis }}</router-link>
          <router-link v-else :to="link_detail">{{ tesis.judul_tesis }}</router-link>
        </p>
        <p class="detail">
          <span class="text-label">Bidang Minat : </span>
          <Tag color="yellow">{{ tesis.nama_bidang_minat }}</Tag>
        </p>
        <p class="nama-mhs"><span>Mahasiswa : {{ tesis.nama_mahasiswa }} - </span><span class="nim-mhs">{{ tesis.nim_mahasiswa }}</span></p>
        <p class="detail" style="margin-bottom:10px;">
          <span class="text-label">Dosen Pembimbing : </span>
          <p style="margin-bottom:10px">
            <Avatar class="avatar-dosen" size="small">{{ inisial_nama(tesis.nama_dospem_1) }}</Avatar>
            <span>{{ tesis.nama_dospem_1 }}</span>
          </p>
          <p style="margin-bottom:10px">
            <Avatar class="avatar-dosen" size="small">{{ inisial_nama(tesis.nama_dospem_2) }}</Avatar>
            <span>{{ tesis.nama_dospem_2 }}</span>
          </p>
        </p>
        <div v-if="ujian">
          <ul class="ujian-list">
            <li v-for="(item,index) in ujian" class="ujian-item">
              <strong>
                <router-link :to="link_ujian(item.id_ujian)"><Icon type="document"></Icon>
                  <span class="link-ujian">{{ nama_ujian }}</span>
                </router-link>
              </strong>
            </li>
          </ul>
        </div>
      </Col>
      <!-- pengajuan item button group -->
      <Col span="5" offset="1" style="text-align:right">
        <div class="button-link">
          <router-link v-if="!prop_tipe" :to="link_detail">Detail</router-link>
        </div>
      </Col>
    </Row>
  </transition>
</template>

<script>
export default {
  name: "CardTesis",
  props: {
    prop_data: '',
    prop_ujian: '',
    prop_tipe: ''
  },
  //variable
  data: function(){
    return{
      tesis: this.prop_data,
      ujian: this.prop_ujian
    }
  },
  computed: {
    link_detail: function(){
      if(this.prop_tipe != ''){
        return '/'+this.prop_tipe+'/'+this.ujian[0].id_ujian;
      }
      else{
        var link_explode = this.$route.fullPath.split('/');
        if(link_explode[1] == 'tesis' || link_explode[1] == 'profile'){
          return '/tesis/'+this.tesis.id_tesis;
        }
        else if(link_explode[1] == 'mahasiswa'){
          return '/mahasiswa/'+link_explode[2]+'/tesis/'+this.tesis.id_tesis;
        }
      }
    },
    nama_ujian: function(){
      if(this.prop_tipe != ''){
        return this.prop_tipe.replace("-"," ");
      }
      else{
        return 'tidak ada';
      }
    }
  },
  components: {

  },
  //methods
  watch: {

  },
  methods: {
    inisial_nama: function(nama){
      var split_nama = nama.split(" ");
      if(split_nama.length > 1){
        return split_nama[0].charAt(0)+split_nama[1].charAt(0);
      }
      else{
        return split_nama[0].charAt(0)
      }
    },
    tes: function(){
      console.log(this.$route.fullPath.split('/'));
    },
    link_ujian: function(string){
      return '/'+this.prop_tipe+'/'+string;
    }
  },
  //components life cycle hook
  created: function(){
    this.tes();
  }
}
</script>

<style lang="css" scoped>
  .tesis .judul{
    color: #2b85e4 !important;
    font-weight: bold;
    font-size: 16px;
  }
  .tesis .detail{
    margin-top: 10px;
  }
  .tesis .detail .text-label{
    font-weight: 500;
  }
  .tesis .nama-mhs{
    margin-top: 10px;
    margin-bottom: 10px;
    font-weight: 500;
  }
  .tesis .avatar-dosen{
    color: white;
    left: 0px;
    margin-right: 5px;
    background-color: #19be6b;
  }
  .button-link a{
    color: white;
    padding: 3px 8px;
    font-size: 14px;
    border-radius: 3px;
    background-color: #2db7f5;
    line-height: 1.5;
  }
  .link-ujian{
    text-transform: capitalize;
  }
  .ujian-list{
    margin-top: 10px;
  }
  .ujian-item i{
    font-size: 18px;
    margin-right: 5px;
  }
</style>



