<template lang="html">
  <transition name="fade" mode="out-in">
  <template v-if="ajax_fetch_profile">
    <Spin size="large"></Spin>
  </template>
  <template v-else>
    <div class="layout-content-main">
      <div class="">
        <Card :bordered="false">
          <Row type="flex" class="row">
            <Col span="6">
              <div :class="img_tipe_profile">{{ inisial_nama(pegawai.nama_pegawai) }}</div>
            </Col>
            <Col span="18" style="padding:15px">
              <h3 class="profile-nama">{{ pegawai.nama_pegawai }}</h3>
              <h5 class="profile-nim">NIP : {{ pegawai.nip }}</h5>
              <ul style="margin-bottom:5px;">
                <li><span>Email : </span>{{ pegawai.email_pegawai }}</li>
                <li><span>Bidang Minat : </span>{{ pegawai.nama_bidang_minat }}</li>
              </ul>
              <button v-if="tipe_pegawai == 'Dosen'" class="btn btn-primary">
                Bimbingan Selesai
                <span v-if="jumlah_bimbingan.bimbingan_selesai" class="badge badge-light">{{ jumlah_bimbingan.bimbingan_selesai }}</span>
                <span v-else>0</span>
              </button>
              <button v-if="tipe_pegawai == 'Dosen'" class="btn btn-secondary">
                Bimbingan Berjalan
                <span v-if="jumlah_bimbingan.bimbingan_berjalan" class="badge badge-light">{{ jumlah_bimbingan.bimbingan_berjalan }}</span>
                <span v-else>0</span>
              </button>
            </Col>
          </Row>
        </Card>
        <br>
        <Row v-if="tipe_pegawai == 'Dosen'">
          <Col span="24" class="daftar-bimbingan-section">
            <Card :bordered="false">
              <p slot="title">Daftar bimbingan</p>
              <template v-if="ajax_fetch_daftar_bimbingan">
                <Spin size="large"></Spin>
              </template>
              <table v-else class="table table-bordered">
                <thead>
                  <tr>
                    <th v-for="kolom in kolom_dosen">{{ kolom.title }}</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="row in bimbingan_dosen">
                    <td>
                      <div class="item-wrap">
                        <Avatar class="avatar" size="large">{{ inisial_nama(row.nama_mahasiswa) }}</Avatar>
                        <router-link :to="'/mahasiswa/'+row.nim_mahasiswa"><span class="nama">{{ row.nama_mahasiswa }}</span></router-link>
                        <span class="nim">{{ row.nim_mahasiswa }}</span>
                      </div>
                    </td>
                    <td>{{ row.judul_tesis }}</td>
                    <td>{{ row.bidang_minat }}</td>
                    <td>{{ row.progres_terakhir }}</td>
                  </tr>
                </tbody>
              </table>
              <!-- <Table v-else border height="370" :columns="kolom_dosen" :data="bimbingan_dosen" class="tabel-bimbingan-dosen"></Table> -->
              <template v-else>
                <div v-if="daftar_bimbingan.length == 0" class="tidak-ada-data">
                  <div style="text-align:center">
                    <Icon type="document-text"></Icon>
                    <br>
                    <span>Data Bimbingan Tidak Ditemukan</span>
                  </div>
                </div>
              </template>
            </Card>
          </Col>
          <Col span="24">
            <div class="layout-content-main">
              <router-view></router-view>
            </div>
          </Col>
        </Row>
      </div>
    </div>
  </template>
</transition>
</template>

<script>
export default {
  name: "PegawaiProfile",
  props: {

  },
  //variable
  data: function(){
    return{
      ajax_fetch_profile: '',
      ajax_fetch_daftar_bimbingan: '',
      pegawai: '',
      daftar_bimbingan: [],
      jumlah_bimbingan: {
        bimbingan_selesai: '',
        bimbingan_berjalan: '',
      },
      id_pegawai: '',
      tipe_pegawai: '',
      //Table bimbingan
      kolom_dosen: [
        {
          title: 'Nama Mahasiswa',
          key: 'nama_mahasiswa',
          sortable: true
        },
        {
          title: 'Judul Tesis',
          key: 'judul_tesis',
          sortable: true
        },
        {
          title: 'Bidang Minat',
          key: 'bidang_minat',
          sortable: true
        },
        {
          title: 'Progres',
          key: 'progres_terakhir',
          sortable: true
        }
      ],
      bimbingan_dosen: [],
    }
  },
  computed: {
    img_tipe_profile: function(){
      if(this.tipe_pegawai == 'Dosen'){
        return {
          'img-profile-dosen': true,
          'img-profile': true,
        };
      }
      else if(this.tipe_pegawai == 'Kaprodi'){
        return {
          'img-profile-kaprodi': true,
          'img-profile': true,
        };
      }
      else if(this.tipe_pegawai == 'Akademik'){
        return {
          'img-profile-akademik': true,
          'img-profile': true,
        };
      }
    }
  },
  components: {

  },
  //methods
  watch: {
    '$route': function(to,from){
      console.log(to);
      console.log(from);
    }
  },
  methods: {
    set_param: function(){
      let a = this.$route.path.split("/");
      if(a[1] == 'profile'){
        this.id_pegawai = this.$store.state.profile.pegawai.id_pegawai;
        this.tipe_pegawai = this.$store.state.profile.pegawai.tipe_pegawai;
      }
      else if(a[1] == 'dosen'){
        this.id_pegawai = a[2];
        this.tipe_pegawai = 'Dosen';
      }
      this.get_pegawai();
      this.get_bimbingan_pegawai();
    },
    get_pegawai: function(){
      var self = this;
      console.log('get data pegawai from ajax');
      this.ajax_fetch_profile = true;
      this.$store.dispatch('get_pegawai_by_id',this.id_pegawai).then((pegawai) => {
        this.pegawai = pegawai;
        this.ajax_fetch_profile = false;
      });
    },
    get_bimbingan_pegawai: function(){
      if(this.tipe_pegawai == 'Dosen'){
        var self = this;
        $.ajax({
          type: 'POST',
          url: `https://simt.000webhostapp.com/index.php/Pegawai/get_bimbingan_dosen/${this.id_pegawai}`,
          data: {token: self.$store.state.token},
          beforeSend: function(){
            self.ajax_fetch_daftar_bimbingan = true;
          },
          success: function(response){
            if(response.bimbingan_dosen.jumlah_bimbingan){
              self.jumlah_bimbingan.bimbingan_selesai = response.bimbingan_dosen.jumlah_bimbingan.bimbingan_selesai;
              self.jumlah_bimbingan.bimbingan_berjalan = response.bimbingan_dosen.jumlah_bimbingan.bimbingan_berjalan;
              response.bimbingan_dosen.detail_bimbingan.forEach(function(val,index,item){
                var data = {};
                data.judul_tesis = val.judul_tesis;
                data.bidang_minat = val.nama_bidang_minat;
                data.nama_mahasiswa = val.nama_mahasiswa;
                data.nim_mahasiswa = val.nim_mahasiswa;
                data.progres_terakhir = val.progres_terakhir.nama_progress;
                self.bimbingan_dosen.push(data);
              });
            }
            self.ajax_fetch_daftar_bimbingan = false;
          }
        })
      }
    },
    inisial_nama: function(nama){
      var split_nama = nama.split(" ");
      if(split_nama.length > 1){
        return split_nama[0].charAt(0)+split_nama[1].charAt(0);
      }
      else{
        return split_nama[0].charAt(0)
      }
    },
  },
  //components life cycle hook
  created: function(){
    this.set_param();
  }
}
</script>

<style lang="css" scoped>
.demo-badge{
  margin-top: 10px;
  width: auto;
  padding: 10px;
  /* height: 42px; */
  background: #eee;
  border-radius: 6px;
  display: inline-block;
}
.badge-bimbingan{
  position: relative;
  width: 10px;
  height: 10px;
  color: white;
}
.badge-bimbingan:after{
  content: "";
  display: block;
  padding-bottom: 100%;
}
.badge-bimbingan-content{
  position: absolute;
  width: 100%;
  height: 100%;
}
.badge-bimbingan-selesai{
  background: #19be6b;
}
.badge-bimbingan-berjalan{
  background: #2d8cf0;
}
.layout-content-main{
  background-color: #f5f7f9;
}
.item-wrap{
  position: relative;
  top: -5px;
  border-bottom: 1px solid #dddee1;
  margin: 10px;
  padding-bottom: 10px;
  /*font-family: 'Poppins', sans-serif;*/
  line-height: 1;
}
.item-wrap .avatar{
  background-color: #2b85e4;
  top: 4px;
}
.item-wrap .nama{
  color: #495060 !important;
  font-size: 15px;
  font-weight: 500;
  position: absolute;
  top: 5px;
  left: 45px;
  text-transform: capitalize;
}
.item-wrap .nim{
  position: absolute;
  left: 45px;
  bottom: 10px;
  font-size: 14px;
  color: #80848f;
}
.row{
  padding: 15px;
  padding-left: 5px;
  background-color: white;
}
.tidak-ada-data{
  height: 150px;
  display:flex;
  justify-content:center;
  align-items:center;
  font-size: 16px;
}
.tidak-ada-data i{
  font-size: 35px;
}
.daftar-bimbingan-section .title{
  font-size: 16px;
  font-weight: 500;
  border-bottom: 1px solid #e9eaec;
}
ul>li{
  font-size: 14px;
}
a{
  color: #495060;
}
a:hover,a.router-link-active{
  color: #2d8cf0;
}
/*profile*/
.img-profile{
  width: 100%;
  height: 100%;
  color: white;
  font-size: 25px;
  text-transform:uppercase;
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}
.img-profile-dosen{
  background-color: #19be6b;
}
.img-profile-kaprodi{
  background-color: #ed3f14;
}
.img-profile-akademik{
  background-color: #464c5b;
}
.profile-nama,.profile-nim{
  text-transform: capitalize;
  margin-bottom: 10px;
}
.profile-nim{
  color: #80848f;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .2s
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0
}
</style>



