<template lang="html">
  <transition name="fade" mode="out-in">
    <Spin size="large" v-if="ajax_fetch_data"></Spin>
    <Row :class="[progress_visibility, progress_class]" v-else>
      <!-- item header -->
      <Col span="18">
        <router-link :to="link_detail_ujian_tesis">
          <p class="judul">
            {{ tesis_mhs.judul_tesis }}
          </p>
        </router-link>
        <p class="nama-mhs"><span>Mahasiswa : {{ tesis_mhs.nama_mahasiswa }} - </span><span class="pengajuan-nim-mhs">{{ tesis_mhs.nim_mahasiswa }}</span></p>
        <p>
          <span>Status : </span>
          <Alert :type="tipe_alert_status_progress" show-icon class="status-sekarang">{{ status_progress_sekarang }}</Alert>
          <Alert v-if="pengajuan_ujian_tesis_ditolak" show-icon class="status-sekarang">{{ status_terakhir.ekstra_detail_status }}</Alert>
        </p>
      </Col>
      <!-- item button group -->
      <Col span="5" offset="1" style="text-align:right">
        <Button-group>
          <Button type="info" icon="chevron-down" size="small" @click="toggle_detail()">Detail</Button>
          <Button v-if="kelola_setujui" type="success" icon="checkmark-round" size="small" @click="toggle_setujui()">Setujui</Button>
          <Button v-if="kelola_tolak" type="error" icon="android-cancel" size="small" @click="toggle_tolak()">Tolak</Button>
          <Button v-if="kelola_input_nilai" type="primary" icon="document-text" size="small" @click="show_modal_ujian_tesis()">Isi Nilai</Button>
          <Button v-if="kelola_input_surat" type="success" icon="checkmark-round" size="small" @click="konfirmasi_akademik()">Setujui</Button>
        </Button-group>
      </Col>
      <!-- bagian detail progress -->
      <Row :class="[detail_visibility, detail_class]">
        <Col span="22" offset="1" class="confirm-box">
          <Col span="24" class="confirm-box-section">
            <p class="confirm-box-section-label">Informasi Tesis</p>
          </Col>
          <Col span="24">
            <Col span="7" class="col-calon-dospem">
              <p class="progress-bidang-minat">Judul Tesis</p>
              <p>{{ tesis_mhs.judul_tesis }}</p>
            </Col>
            <Col span="5" class="col-bidang-minat">
              <p class="progress-bidang-minat">Bidang Minat</p>
              <p> <Tag color="yellow">{{ tesis_mhs.nama_bidang_minat }}</Tag></p>
            </Col>
            <Col span="5" class="col-tgl-progress">
              <p class="progress-tgl">Tgl. Pengajuan</p>
              <p>{{ data_progress.tgl_progress }}</p>
            </Col>
            <Col span="7" class="col-status">
              <p class="progress-calon-dospem">Dospem</p>
              <ul>
                <li><Icon type="person"></Icon>{{ tesis_mhs.nama_dospem_1 }}</li>
                <li><Icon type="person"></Icon>{{ tesis_mhs.nama_dospem_2 }}</li>
              </ul>
            </Col>
          </Col>
          <Col span="24" class="confirm-box-section">
            <p class="confirm-box-section-label">Informasi Ujian Tesis</p>
          </Col>
          <Col span="24">
            <template v-if="ajax_fetch_data">
              <Spin size="large"></Spin>
            </template>
            <template v-else>
              <Col span="7" class="col-calon-dospem">
                <p class="progress-bidang-minat">Tanggal Ujian</p>
                <p>{{ ujian_tesis.tgl_ujian }}</p>
              </Col>
              <Col span="5" class="col-bidang-minat">
                <p class="progress-bidang-minat">Jam Ujian</p>
                <p>{{ ujian_tesis.waktu_mulai_ujian }} - {{ ujian_tesis.waktu_selesai_ujian }}</p>
              </Col>
              <Col span="5" class="col-tgl-progress">
                <p class="progress-tgl">Ruang Ujian</p>
                <p>{{ ujian_tesis.ruang_ujian | is_kosong }}</p>
              </Col>
              <Col span="7" class="col-status">
                <p class="progress-calon-dospem">Dosen Penguji</p>
                <ul>
                  <li><Icon type="person"></Icon>{{ ujian_tesis.penguji_1 | is_kosong }}</li>
                  <li><Icon type="person"></Icon>{{ ujian_tesis.penguji_2 | is_kosong }}</li>
                </ul>
              </Col>
            </template>
          </Col>
        </Col>
      </Row>
      <!-- bagian setujui progress -->
      <Row type="flex" justify="center" align="middle" :class="[setujui_visibility, detail_class]">
        <Col span="20" class="confirm-box">
          <Col span="24" class="confirm-box-inner" id="setujui">
            <div v-if="pilih_dosen_visibility">
              <Steps :current="setujui_peganjuan_step">
                <Step title="Pilih Dosen" content="Pilih Dosen Sebagai Pengji"></Step>
                <Step title="Konfirmasi" content="Konfirmasi persetujuan"></Step>
              </Steps>
              <transition name="fade" mode="out-in">
                <!-- Step 1 -->
                <Row v-if="setujui_peganjuan_step == 0" class="pilih_dospem_box" key="0">
                  <Col span="24" class="pilih_dospem_box--right">
                    <p class="progress-calon-dospem">Pilih Dosen Penguji</p>
                    <div>
                      <span>Penguji 1 : </span><span v-if="dosen_pilihan.dosen_1 != ''"><Icon type="person"></Icon>{{ dosen_pilihan.dosen_1.nama_pegawai }}</span>
                      <Button type="primary" icon="ios-search-strong" size="small" @click="pilih_penguji(1)" v-if="dosen_pilihan.dosen_1 == ''">Pilih</Button>
                      <Button type="error" icon="close-round" size="small" @click="hapus_dosen(1)" v-else="dosen_pilihan.dosen_1 != ''">Cancel</Button>
                    </div>
                    <div>
                      <span>Penguji 2 : </span><span v-if="dosen_pilihan.dosen_2 != ''"><Icon type="person"></Icon>{{ dosen_pilihan.dosen_2.nama_pegawai }}</span>
                      <Button type="primary" icon="ios-search-strong" size="small" @click="pilih_penguji(2)" v-if="dosen_pilihan.dosen_2 == ''">Pilih</Button>
                      <Button type="error" icon="close-round" size="small" @click="hapus_dosen(2)" v-else="dosen_pilihan.dosen_2 != ''">Cancel</Button>
                    </div>
                    <div>
                      <Button long :disabled="submit_dosen_pilihan" type="primary" @click="setujui_progress(0)">Next</Button>
                    </div>
                  </Col>
                </Row>
                <!-- Step 2 -->
                <Row v-if="setujui_peganjuan_step == 1" class="pilih_dospem_confirm" key="1">
                  <h5>Setujui progress Proposal ini ?</h5>
                  <Card class="text-left">
                    <p class="">{{ tesis_mhs.judul_tesis }}</p>
                    <span><Tag color="yellow">{{ tesis_mhs.nama_bidang_minat }}</Tag></span>
                    <p class="progress-nama-mhs"><span class="progress-nim-mhs">NIM : {{ tesis_mhs.nim_mahasiswa }}</span><span> Nama : {{ tesis_mhs.nama_mahasiswa }}</span></p>
                    <ul>
                      <li class="progress-dospem" v-for="">Penguji 1<Icon type="person"></Icon>{{ dosen_pilihan.dosen_1.nama_pegawai }}</li>
                      <li class="progress-dospem" v-for="">Penguji 2<Icon type="person"></Icon>{{ dosen_pilihan.dosen_2.nama_pegawai }}</li>
                    </ul>
                    <div class="text-right">
                      <Button size="large" type="ghost" @click="">Back</Button>
                      <Button size="large" type="primary" @click="setujui_progress(1)">Ok</Button>
                    </div>
                  </Card>
                </Row>
              </transition>
            </div>
            <div v-else>
              <h5>Setujui Pengajuan Ujian Tesis ini ?</h5>
              <div class="text-center" style="margin-top:20px;">
                <div class="checklist" v-if="$store.state.profile.pegawai.tipe_pegawai == 'Akademik'">
                  <p>Harap Cek apakah mahasiswa sudah memenuhi syarat berikut</p>
                  <Checkbox v-model="checklist.jurnal_ilmiah">Mahasiswa menunjukkan bukti telah mempublikasikan artikel ilmiah (minimal sudah diterima/accepted)</Checkbox>
                  <Checkbox v-model="checklist.bebas_plagiasi">Mahasiswa menyerahkan bukti bahwa naskah tesis sudah dinyatakan bebas dari plagiasi</Checkbox>
                  <br>
                  <Button :disabled="!checklist_akademik_lengkap" :loading="ajax_submit" size="large" type="primary" @click="setujui_progress(1)">
                    <span v-if="!ajax_submit">Ok</span>
                    <span v-else>Loading...</span>
                  </Button>
                </div>
                <Button v-else :loading="ajax_submit" size="large" type="primary" @click="setujui_progress(1)">
                  <span v-if="!ajax_submit">Ok</span>
                  <span v-else>Loading...</span>
                </Button>
              </div>
            </div>
          </Col>
        </Col>
      </Row>
      <!-- bagian tolak progress -->
      <Row type="flex" justify="center" align="middle" :class="[tolak_visibility, detail_class]">
        <Col span="15" class="confirm-box">
          <div class="confirm-box-inner">
            <h5>Tolak Pengajuan Ujian Proposal ini ?</h5>
            <div class="form-group">
              <label for="alasan_tolak">Alasan Penolakan</label>
              <textarea
                v-model="alasan_tolak"
                v-validate="'required'"
                name="alasan_tolak"
                id="alasan_tolak"
                :class="{'form-control': true,'is-invalid': errors.has('alasan_tolak')}"
                >
              </textarea>
              <div v-show="errors.has('alasan_tolak')" class="invalid-feedback">
                {{ errors.first('alasan_tolak') }}
              </div>
            </div>
            <Button size="large" type="error" @click="tolak_progress()">Ok</Button>
          </div>
        </Col>
      </Row>
      <Modal-Pilih-Dosen
        :dospem_1="tesis_mhs.id_dospem_1"
        :dospem_2="tesis_mhs.id_dospem_2"
        :modal_dosen="$store.state.modal_pilih_pembimbing.show"
        :dosen="$store.state.modal_pilih_pembimbing.dosen_pilihan"
        :row_dosen="$store.state.modal_pilih_pembimbing.row_dosen"
        v-on:pilih_dosen='pilih_dosen_pembimbing'
        v-on:modal_close="modal_close">
        </Modal-Pilih-Dosen>
        <!-- modal insert nilai ujian tesis -->
        <Modal
          v-if="ujian_tesis"
          v-model="modal_ujian_tesis.visible"
          width="50"
          :styles	="modal_ujian_tesis.styles"
          title="Input Nilai Ujian Tesis"
          @on-ok="">
          <form>
            <!-- Pembimbing 1 -->
            <p>{{ tesis_mhs.nama_dospem_1 }} <span>(Pemibimbing 1)</span></p>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Draf Tesis</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_tesis.nilai_draf_pembimbing_1"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Penyajian Tesis</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_tesis.nilai_penyajian_pembimbing_1"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Mempertahankan Tesis</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_tesis.nilai_mempertahankan_tesis_pembimbing_1"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Saran Perbaikan</label>
              <div class="col-sm-7">
                <Input v-model="modal_ujian_tesis.saran_pembimbing_1" placeholder="Masukkan Saran Perbaikan"></Input>
              </div>
            </div>
            <!-- Pembimbing 2 -->
            <p>{{ tesis_mhs.nama_dospem_2 }} <span>(Pemibimbing 2)</span></p>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Draf Tesis</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_tesis.nilai_draf_pembimbing_2"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Penyajian Tesis</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_tesis.nilai_penyajian_pembimbing_2"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Mempertahankan Tesis</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_tesis.nilai_mempertahankan_tesis_pembimbing_2"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Saran Perbaikan</label>
              <div class="col-sm-7">
                <Input v-model="modal_ujian_tesis.saran_pembimbing_2" placeholder="Masukkan Saran Perbaikan"></Input>
              </div>
            </div>
            <!-- Penguji 1 -->
            <p>{{ ujian_tesis.penguji_1 }} <span>(Penguji 1)</span></p>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Draf Tesis</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_tesis.nilai_draf_penguji_1"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Penyajian Tesis</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_tesis.nilai_penyajian_penguji_1"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Mempertahankan Tesis</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_tesis.nilai_mempertahankan_tesis_penguji_1"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Saran Perbaikan</label>
              <div class="col-sm-7">
                <Input v-model="modal_ujian_tesis.saran_penguji_1" placeholder="Masukkan Saran Perbaikan"></Input>
              </div>
            </div>
            <!-- Penguji 2 -->
            <p>{{ ujian_tesis.penguji_2 }} <span>(Penguji 2)</span></p>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Draf Tesis</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_tesis.nilai_draf_penguji_2"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Penyajian Tesis</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_tesis.nilai_penyajian_penguji_2"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Mempertahankan Tesis</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_tesis.nilai_mempertahankan_tesis_penguji_2"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Saran Perbaikan</label>
              <div class="col-sm-7">
                <Input v-model="modal_ujian_tesis.saran_penguji_2" placeholder="Masukkan Saran Perbaikan"></Input>
              </div>
            </div>
          </form>
          <div slot="footer">
            <Button type="primary" size="large" long :loading="ajax_submit" @click="insert_nilai_ujian_tesis()">Submit</Button>
          </div>
          </Modal>
      </Row>
  </transition>
</template>
<script>
import ModalPilihDosen from './Modal-pilih-dosen.vue';
export default {
  name: 'UjianTesisCard',
  props: {
    prop_data_progress: '',
    prop_filter: '',
    prop_index: '',
  },
  data: function(){
    return {
      ajax_fetch_data: '',
      ajax_submit: false,
      data_progress: this.prop_data_progress,
      data_setujui_ubah_progress: {},//set_data_setujui_progress
      detail_class: 'progress-item-detail',
      detail_visibility: 'hide',
      dosen_pilihan: {
        dosen_1: '',
        dosen_2: '',
      },
      checklist: {
        jurnal_ilmiah: '',
        bebas_plagiasi: ''
      },
      modal_ujian_tesis: {
        visible: false,

        nilai_draf_pembimbing_1: 50,
        nilai_penyajian_pembimbing_1: 50,
        nilai_mempertahankan_tesis_pembimbing_1: 50,
        saran_pembimbing_1: 50,

        nilai_draf_pembimbing_2: 50,
        nilai_penyajian_pembimbing_2: 50,
        nilai_mempertahankan_tesis_pembimbing_2: 50,
        saran_pembimbing_2: 50,

        nilai_draf_penguji_1: 50,
        nilai_penyajian_penguji_1: 50,
        nilai_mempertahankan_tesis_penguji_1: 50,
        saran_penguji_1: 50,

        nilai_draf_penguji_2: 50,
        nilai_penyajian_penguji_2: 50,
        nilai_mempertahankan_tesis_penguji_2: 50,
        saran_penguji_2: 50,

        styles: {
          top: '20px'
        }
      },
      alasan_tolak: '',
      inisial_nama: '',
      kelola_setujui: '',
      kelola_tolak: '',
      kelola_input_surat: '',
      kelola_input_nilai: '',
      nomer_dosen: '', //dospem
      progress_class: 'progress-item',
      progress_visibility: 'show',
      mahasiswa: '',
      status_terakhir: '', //status_terakhir dari progress
      setujui_visibility: 'hide',
      setujui_peganjuan_step: 0,
      tesis_mhs: '',
      tipe_progress: '', //tipe_progress
      tolak_visibility: 'hide',
      url_ajax_ubah_status: '',// url ajax tertentu (tiap proses beda)
      ujian_tesis: {},
    }
  },
  computed: {
    checklist_akademik_lengkap: function(){
      if(this.checklist.bebas_plagiasi == true && this.checklist.jurnal_ilmiah == true){
        return true;
      }
      else{
        return false;
      }
    },
    link_detail_ujian_tesis: function(){
      return '/ujian-tesis/'+this.data_progress.id_progress
    },
    submit_dosen_pilihan: function(){
      if(this.dosen_pilihan.dosen_1.id_pegawai && this.dosen_pilihan.dosen_2.id_pegawai){
        return false;
      }
      else{
        return true;
      }
    },
    pilih_dosen_visibility: function(){
      if(this.$store.state.profile.pegawai){
        if(this.$store.state.profile.pegawai.tipe_pegawai == 'Kaprodi' && this.status_terakhir.id_status_progress == "SUT07"){
          return true;
        }
        else{
          return false;
        }
      }
      else{
        return false;
      }
    },
    status_progress_sekarang: function(){
      if(this.status_terakhir.ekstra_detail_status != ''){
        return 'Telah '+this.status_terakhir.nama_status_progress;
      }
      else{
        if(this.status_terakhir.is_selesai == 1){
          if(this.status_terakhir.id_status_progress == 'SUT12' || this.status_terakhir.id_status_progress == 'SUT18'){
            return this.status_terakhir.nama_status_progress;
          }
          else{
            return 'Sudah '+this.status_terakhir.nama_status_progress;
          }
        }
        else{
          return 'Menunggu '+this.status_terakhir.nama_status_progress;
        }
      }
    },
    tipe_alert_status_progress: function(){
      if(this.status_terakhir.ekstra_detail_status != ''){
        return 'error';
      }
      else{
        if(this.status_terakhir.is_selesai == 1){
          return 'success';
        }
        else{
          return 'warning';
        }
      }
    },
    pengajuan_ujian_tesis_ditolak: function(){
      if(this.status_terakhir.ekstra_detail_status != ''){
        return true;
      }
      else{
        return false;
      }
    },
    status_progress_baru: function(){
      if(this.tipe_progress == 'PRG08'){
        var split_status_progress = this.status_terakhir.id_status_progress.split("");
        if(split_status_progress[3] == "0"){
          var temp = parseInt(split_status_progress[4])+1
          return "SUT0"+temp;
        }
        else{
          var temp = parseInt(split_status_progress[4])+1+10;
          return "SUT"+temp;
        }
      }
      else if(this.tipe_progress == 'PRG09' || this.tipe_progress == 'PRG10'){
        var split_status_progress = this.status_terakhir.id_status_progress.split("");
        if(split_status_progress[3] == "0"){
          if(split_status_progress[4] == "9"){
            var temp = parseInt(split_status_progress[4])+1;
            return "SUT"+temp;
          }
          else{
            var temp = parseInt(split_status_progress[4])+1;
            return "SUT0"+temp;
          }
        }
        else{
          var temp = parseInt(split_status_progress[4])+1+10;
          return "SUT"+temp;
        }
      }
      else{
        return 'aw';
      }
    },
    watch_me: function(){
      return this.$store.state.modal_pilih_pembimbing.dosen_pilihan[this.prop_index];
    },
  },
  components: {
    ModalPilihDosen
  },
  watch: {
    watch_me: {
      handler: function(after,before){
        this.dosen_pilihan.dosen_1 = this.$store.state.modal_pilih_pembimbing.dosen_pilihan[this.prop_index].dosen_1;
        this.dosen_pilihan.dosen_2 = this.$store.state.modal_pilih_pembimbing.dosen_pilihan[this.prop_index].dosen_2;
      },
      deep: true,
    }
  },
  filters: {
    is_kosong(val) {
      if(val == ''){
        return "Tidak Ada";
      }
      else{
        return val;
      }
    }
  },
  methods: {
    kelola_data_progress: function(){
      if(this.$store.state.profile.pegawai){
        if(this.$store.state.profile.pegawai.id_pegawai == this.status_terakhir.id_pegawai && this.status_terakhir.is_selesai == 0){
          //untuk akademik insert nilai
          if(this.status_terakhir.id_status_progress == 'SUT16'){
            this.kelola_setujui = false;
            this.kelola_input_nilai = true;
          }
          else if(this.status_terakhir.id_status_progress == 'SUT17') {
            this.kelola_setujui = false;
            this.kelola_input_surat = true;
          }
          else{
            this.kelola_setujui = true;
            this.kelola_input_nilai = false;
            this.kelola_input_surat = false;
          }
          //untuk dosen pembimbing menolak ujian tesis
          if(this.status_terakhir.id_status_progress == 'SUT01' || this.status_terakhir.id_status_progress == 'SUT02' || this.status_terakhir.id_status_progress == 'SUT03'){
            this.kelola_tolak = true;
          }
          else{
            this.kelola_tolak = false;
          }
        }
        else{
          this.kelola_setujui = false;
          this.kelola_tolak = false;
          this.kelola_input_nilai = false;
          this.kelola_input_surat = false;
        }
      }
      else{
        this.kelola_setujui = false;
        this.kelola_tolak = false;
      }
    },
    get_mahasiswa: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Mahasiswa/get_mahasiswa",
        data: {token: self.$store.state.token,nim_mahasiswa: self.data_progress.nim_mahasiswa},
        beforeSend: function(){
          this.ajax_fetch_data = true;
        },
        success: function(response){
          self.mahasiswa = response.mahasiswa[0];
          self.set_inisial_nama();
          self.get_tesis();
          console.log(response);
          this.ajax_fetch_data = false;
        }
      })
    },
    get_status_progress: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ubah_Status_Progress/get_ubah_status_progress",
        data: {token: self.$store.state.token,id_progress: self.data_progress.id_progress},
        beforeSend: function(){
          self.ajax_fetch_data = true;
        },
        success: function(response){
          var ubah_status_progress = response.ubah_status_progress;
          ubah_status_progress.forEach(function(val,index,item){
            if(ubah_status_progress[index].is_selesai == 0){
              if(!self.status_terakhir){
                self.status_terakhir = ubah_status_progress[index];
              }
            }
            else if(ubah_status_progress[index].is_selesai == 1){
              if(!self.status_terakhir){
                self.status_terakhir = ubah_status_progress[index];
              }
            }
            else if(ubah_status_progress[index].is_selesai == 2){
              self.status_terakhir = ubah_status_progress[index];
            }
          });
          self.ajax_fetch_data = false;
          self.kelola_data_progress();
          console.log(response);
        }
      })
    },
    get_tesis: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Tesis/get_tesis",
        data: {token: self.$store.state.token,nim_mahasiswa: self.mahasiswa.nim_mahasiswa},
        success: function(response){
          self.tesis_mhs = response.tesis[0];
          console.log(response);
          self.get_ujian_tesis();
        }
      })
    },
    get_ujian_tesis: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ujian/get_ujian",
        data: {token: self.$store.state.token,id_tesis: self.tesis_mhs.id_tesis,tipe_ujian: 'UT'},
        beforeSend: function(){
          self.ajax_fetch_data = true;
        },
        success: function(response){
          self.ujian_tesis = response.ujian[0];
          if(self.ujian_tesis.penguji_1 != ''){
            var penguji_1 = self.ujian_tesis.penguji_1[0].nama_pegawai;
            self.ujian_tesis.penguji_1 = penguji_1;
          }
          else{
            var penguji_1 = 'Belum Ada';
          }
          if(self.ujian_tesis.penguji_2 != ''){
            var penguji_2 = self.ujian_tesis.penguji_2[0].nama_pegawai;
            self.ujian_tesis.penguji_2 = penguji_2;
          }
          else{
            var penguji_2 = 'Belum Ada';
          }
          self.ajax_fetch_data = false;
        }
      })
    },
    konfirmasi_akademik: function(){
      this.data_setujui_ubah_progress.tipe = 'ujian-tesis';
      this.data_setujui_ubah_progress.token = this.$store.state.token;
      this.data_setujui_ubah_progress.id_progress = this.data_progress.id_progress;
      this.data_setujui_ubah_progress.id_status_progress_baru = this.status_progress_baru;
      this.data_setujui_ubah_progress.id_ubah_status_progress = this.status_terakhir.id_ubah_status_progress;
      //tesis Mahasiswa
      this.data_setujui_ubah_progress.id_tesis = this.tesis_mhs.id_tesis;
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ujian/konfirmasi_akademik",
        data: self.data_setujui_ubah_progress,
        beforeSend: function(){
          self.ajax_submit = true;
        },
        success: function(response){
          if(response.status == 'berhasil'){
            self.$Notice.success({
              title: 'Konfirmasi Akademik Berhasil',
            });
            // self.setujui_visibility = 'hide';
            self.status_terakhir= '';
            self.get_status_progress();
            self.kelola_setujui = false;
            self.kelola_tolak = false;
            self.kelola_input_nilai = false;
            self.kelola_input_surat = false;
            self.ajax_submit = false;
          }
        }
      })
    },
    setujui_progress: function(index){
      this.set_data_setujui_ubah_progress();
      if(index == 0){
        this.setujui_peganjuan_step = 1;
      }
      else if(index == 1){
        var self = this;
        $.ajax({
          type: 'POST',
          url: self.url_ajax_ubah_status,
          data: self.data_setujui_ubah_progress,
          beforeSend: function(){
            self.ajax_submit = true;
          },
          success: function(response){
            console.log(response);
            if(response.status == 'berhasil'){
              self.$Notice.success({
                title: 'Progress Ujian Tesis Telah Disetujui',
              });
              self.toggle_setujui();
              // self.setujui_visibility = 'hide';
              self.status_terakhir= '';
              self.get_status_progress();
              self.kelola_setujui = false;
              self.kelola_tolak = false;
              self.kelola_input_nilai = false;
              self.kelola_input_surat = false;
            }
          }
        })
      }
    },
    tolak_progress: function(){
      var self = this;
      this.set_data_setujui_ubah_progress();
      this.data_setujui_ubah_progress.alasan_tolak = this.alasan_tolak;
      this.data_setujui_ubah_progress.id_status_progress = this.status_terakhir.id_status_progress;
      if(this.alasan_tolak != ''){
        $.ajax({
          type: 'POST',
          url: "https://simt.000webhostapp.com/index.php/Ujian/tolak_persetujuan_ujian/",
          data: self.data_setujui_ubah_progress,
          success: function(response){
            console.log(response);
            if(response.status == 'berhasil'){
              self.$Notice.success({
                title: 'Persetujuan Ujian Tesis telah ditolak',
              });
              self.tolak_visibility = 'hide';
              self.status_terakhir= '';
              self.get_status_progress();
              self.kelola_setujui = false;
              self.kelola_tolak = false;
            }
          }
        })
      }
      else {
        this.$Message.error('Harap Isi Alasan Penolakan');
      }
    },
    show_modal_ujian_tesis: function(){
      this.modal_ujian_tesis.visible = true;
    },
    insert_nilai_ujian_tesis: function(){
      if(this.modal_ujian_tesis.saran_pembimbing_1 != '' &&
         this.modal_ujian_tesis.saran_pembimbing_2 != '' &&
         this.modal_ujian_tesis.saran_penguji_1 != '' &&
         this.modal_ujian_tesis.saran_penguji_2 != ''
       ){
         this.modal_ujian_tesis.nilai_akhir = (this.modal_ujian_tesis.nilai_draf_pembimbing_1+
                                                 this.modal_ujian_tesis.nilai_draf_pembimbing_2+
                                                 this.modal_ujian_tesis.nilai_draf_penguji_1+
                                                 this.modal_ujian_tesis.nilai_draf_penguji_2+
                                                 this.modal_ujian_tesis.nilai_penyajian_pembimbing_1+
                                                 this.modal_ujian_tesis.nilai_penyajian_pembimbing_2+
                                                 this.modal_ujian_tesis.nilai_penyajian_penguji_1+
                                                 this.modal_ujian_tesis.nilai_penyajian_penguji_2+
                                                 this.modal_ujian_tesis.nilai_mempertahankan_tesis_pembimbing_1+
                                                 this.modal_ujian_tesis.nilai_mempertahankan_tesis_pembimbing_2+
                                                 this.modal_ujian_tesis.nilai_mempertahankan_tesis_penguji_1+
                                                 this.modal_ujian_tesis.nilai_mempertahankan_tesis_penguji_2)/12;

         var self = this;
         $.ajax({
           type: 'POST',
           url: "https://simt.000webhostapp.com/index.php/Ujian/insert_nilai_ujian",
           data: {
             token: this.$store.state.token,
             tipe: 'ujian-tesis',
             nilai: this.modal_ujian_tesis,
             penguji_1: this.ujian_tesis.id_penguji_1,
             penguji_2: this.ujian_tesis.id_penguji_2,
             pembimbing_1: this.tesis_mhs.id_dospem_1,
             pembimbing_2: this.tesis_mhs.id_dospem_2,
             id_ujian: this.ujian_tesis.id_ujian
           },
           beforeSend: function(){
             self.ajax_submit = true;
           },
           success: function(response){
             self.konfirmasi_akademik();
             self.ajax_submit = false;
             self.modal_ujian_tesis.visible = false;
             if(response.status == 'berhasil'){
               self.$Notice.success({
                 title: 'Insert Nilai Ujian Tesis berhasil',
               });
               self.get_ujian_tesis();
             }
           }
         })
       }
       else{
         this.$Message.error('Harap Isi seluruh inputan form');
       }
    },
    //set
    set_data_setujui_ubah_progress: function(){
      this.data_setujui_ubah_progress.tipe = 'ujian-tesis';
      this.data_setujui_ubah_progress.token = this.$store.state.token;
      this.data_setujui_ubah_progress.id_tesis = this.tesis_mhs.id_tesis;
      this.data_setujui_ubah_progress.id_progress = this.data_progress.id_progress;
      this.data_setujui_ubah_progress.id_status_progress_baru = this.status_progress_baru;
      this.data_setujui_ubah_progress.id_ubah_status_progress = this.status_terakhir.id_ubah_status_progress;
      this.data_setujui_ubah_progress.id_dospem_1 = this.tesis_mhs.id_dospem_1;
      this.data_setujui_ubah_progress.id_dospem_2 = this.tesis_mhs.id_dospem_2;
      this.data_setujui_ubah_progress.nim_mahasiswa = this.tesis_mhs.nim_mahasiswa;

      if(this.data_progress.id_tipe_progress == 'PRG09'){
        if(this.status_terakhir.id_status_progress == 'SUT07'){
          this.data_setujui_ubah_progress.id_penguji_1 = this.dosen_pilihan.dosen_1.id_pegawai;
          this.data_setujui_ubah_progress.id_penguji_2 = this.dosen_pilihan.dosen_2.id_pegawai;
        }
        else{
          this.data_setujui_ubah_progress.id_penguji_1 = this.ujian_tesis.id_penguji_1;
          this.data_setujui_ubah_progress.id_penguji_2 = this.ujian_tesis.id_penguji_2;
        }
      }
    },
    set_inisial_nama: function(){
      var split_nama = this.mahasiswa.nama_mahasiswa.split(" ");
      if(split_nama[1]){
        this.inisial_nama = split_nama[0].charAt(0)+split_nama[1].charAt(0);
      }
      else{
        this.inisial_nama = split_nama[0].charAt(0);
      }
    },
    set_tipe_progress: function(){
      if(this.data_progress.id_tipe_progress == "PRG08"){
        this.tipe_progress = 'PRG08';
        this.url_ajax_ubah_status = 'https://simt.000webhostapp.com/index.php/Ujian/setujui_persetujuan_ujian';
      }
      else if(this.data_progress.id_tipe_progress == "PRG09"){
        this.tipe_progress = 'PRG09';
        this.url_ajax_ubah_status = 'https://simt.000webhostapp.com/index.php/Ujian/setujui_pengajuan_ujian';
      }
      else if(this.data_progress.id_tipe_progress == 'PRG10'){
        this.tipe_progress = 'PRG10';
      }
    },
    pilih_penguji: function(index){
      this.$store.commit('set_index_modal',this.prop_index);
      //eksperiment
      // this.$store.commit('push_modal_dosen_pembimbing');
      // this.$store.commit('set_tipe_modal','ujian_proposal');
      this.$store.commit('pilih_dosen_pembimbing_ke',index);
      this.$store.commit('open_modal_pilih_pembimbing');
      // this.set_row_dosen();
      // this.show_modal_dosen = true;
      // this.nomer_dosen = index;
    },
    hapus_dosen: function(index){
      this.$store.commit('pilih_dosen_pembimbing_ke',index);
      this.$store.commit('hapus_dosen_pembimbing');
      // eksperiment
      if(index == 1){
        this.dosen_pilihan.dosen_1 = '';
      }
      else if(index == 2){
        this.dosen_pilihan.dosen_2 = '';
      }
      // console.log(this.dosen_pilihan);
    },
    pilih_dosen_pembimbing: function(dosen){
      if(this.nomer_dosen == 1){
        this.dosen_pilihan.dosen_1 = dosen;
      }
      else if(this.nomer_dosen == 2){
        this.dosen_pilihan.dosen_2 = dosen;
      }
      this.show_modal_dosen = false;
      // console.log(this.dosen_pilihan);
    },
    //fungsi toggle animasi hide/show
    modal_close: function(){
      this.show_modal_dosen = false;
    },
    toggle_progress_ujian_tesis: function(){
      if(this.prop_filter == 'all'){
        this.progress_ujian_tesis_visibility = 'show';
      }
      else if(this.prop_filter == 'new'){
        if(this.status_terakhir.is_selesai == 0){
          this.progress_ujian_tesis_visibility = 'show';
        }
        else{
          this.progress_ujian_tesis_visibility = 'hide';
        }
      }
    },
    toggle_detail: function(){
      if(this.detail_visibility == 'show'){
        this.detail_visibility = 'hide';
      }
      else{
        this.tolak_visibility = 'hide';
        this.setujui_visibility = 'hide';
        this.detail_visibility = 'show';
      }
    },
    toggle_setujui(){
      if(this.setujui_visibility == 'show'){
        this.setujui_visibility = 'hide'
      }
      else{
        this.tolak_visibility = 'hide';
        this.detail_visibility = 'hide';
        this.setujui_visibility = 'show';
      }
    },
    toggle_tolak(){
      if(this.tolak_visibility == 'show'){
        this.tolak_visibility = 'hide';
      }
      else{
        this.setujui_visibility = 'hide';
        this.detail_visibility = 'hide';
        this.tolak_visibility = 'show';
      }
    }
  },
  created: function(){
    this.get_mahasiswa();
    this.get_status_progress();
    this.set_tipe_progress();
  },
}
</script>
<style lang="css" scoped>
.judul{
  color: #2b85e4;
  font-weight: bold;
  font-size: 16px;
}
.nama-mhs{
  margin-top: 10px;
  margin-bottom: 10px;
}
.status-sekarang{
  display: inline-block;
}
.confirm-box-section-label{
  border-bottom: 2px solid #e9eaec;
}
/*dipakai*/
.progress-item{
  padding-top: 15px;
  padding-bottom: 15px;
  padding-right: 10px;
  border-bottom: 1px solid #e9eaec;
}
.progress-item-detail{
  background-color: #e9eaec;
  width: 100%;
}

.progress-nama-mhs{
  margin-top: 10px;
  margin-bottom: 10px;
}
.col-calon-dospem,.col-tgl-progress,.col-bidang-minat,.col-status{
  border-right: 1px solid #e9eaec;
  padding-left: 10px;
  padding-right: 10px;
}
.progress-calon-dospem,.progress-bidang-minat,.progress-status,.progress-tgl{
  padding-top: 5px;
  padding-bottom: 10px;
  font-weight: 700;
  /*text-decoration: underline;*/
}
.confirm-box{
  padding: 20px;
  background-color: white;
  margin-top: 20px;
  margin-bottom: 20px;
}
.confirm-box-inner{
  text-align: center;
}
.confirm-box-inner#setujui,.confirm-box-inner#tolak{
  text-align: center;
  /*min-height: 100px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;*/
}
.confirm-box-inner h5{
  font-weight: 700;
  margin-bottom: 20px;
}
.confirm-box-inner{
  margin-top: 15px;
}
.checklist{
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}
.checklist p{
  margin-bottom: 15px;
}
.pilih_dospem_confirm{
  padding-top: 20px;
}
.pilih_dospem_confirm h5{
  margin-bottom: 10px;
}
.pilih_dospem_box{
  padding-top: 0px;
  padding-bottom: 20px;
}
.pilih_dospem_box{
  text-align: left;
}
.pilih_dospem_box--left{
  border-right: 1px solid #e9eaec;
  padding-left: 20px;
}
.pilih_dospem_box--right{
  padding-left: 20px;
}
.pilih_dospem_box--right div{
  padding: 10px;
}
.ivu-steps{
  margin-bottom: 20px;
}
.ivu-modal-body p{
  font-size: 16px;
}
.ivu-modal-body p span{
  font-size: 14px;
  color: #5b6270;
}
.ivu-input-number{
  width: 100%;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .2s
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0
}
.hide {
  overflow: hidden;
  max-height: 0;
  padding-top: 0;
  padding-bottom: 0;
  margin-top: 0;
  margin-bottom: 0;
  -moz-transition-duration: 0.3s;
  -webkit-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -moz-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
  -webkit-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
}
.show {
  -moz-transition-duration: 0.3s;
  -webkit-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -moz-transition-timing-function: ease-in;
  -webkit-transition-timing-function: ease-in;
  -o-transition-timing-function: ease-in;
  transition-timing-function: ease-in;
  max-height: 1000px;
  overflow: hidden;
}
</style>



