<template lang="html">
  <Spin v-if="ajax_fetch"></Spin>
  <Card v-else :bordered="false" :dis-hover="true">
    <p slot="title">Pengajuan Ujian Proposal Tesis</p>
    <template>
      <span class="text-help">Tahapan Pengajuan Ujian Proposal</span>
      <Steps :current="ujian_proposal_step" :status="ujian_proposal_step_status">
        <Step title="Tahap 1" content="Persetujuan ujian proposal" style="width:25%"></Step>
        <Step title="Tahap 2" content="Isi Form Pengajuan Ujian" style="width:25%"></Step>
        <Step title="Tahap 3" content="Persetujuan Akhir" style="width:25%"></Step>
        <Step title="Tahap 5" content="Selesai" style="width:25%"></Step>
      </Steps>
      <hr>
      <transition-group name="fade" mode="out-in">
        <div v-show="ujian_proposal_step == 0" class="step-content-wrap" key="step_1">
          <h5 class="text-center text-title">Persetujuan Ujian Proposal</h5>
          <Row type="flex" justify="center" align="middle">
            <Col span="18" class="pengajuan-form-persetujuan">
              <Col span="24" class="pengajuan-nav">
                <a class="pengajuan-next" @click="pengajuan_next()" style="float: right;" v-if="is_pengajuan_persetujuan == false">
                  <span>Pengajuan Ujian Proposal</span><Icon type="arrow-right-a"></Icon>
                </a>
              </Col>
              <Col span="24">
                <Tabs>
                  <TabPane label="Form Persetujuan" icon="compose">
                    <Col span="24">
                      <div class="form-group">
                        <label for="judul_tesis">Judul Tesis</label>
                        <input
                          v-model="tesis.judul_tesis"
                          type="text"
                          id="judul_tesis"
                          class="form-control"
                          disabled>
                      </div>
                      <div class="form-group">
                        <label for="bidang_minat">Bidang Minat</label>
                        <input
                          v-model="tesis.nama_bidang_minat"
                          type="text"
                          id="bidang_minat"
                          class="form-control"
                          disabled>
                      </div>
                      <div class="form-group">
                        <label for="pembimbing_1">Dosen Pembimbing 1</label>
                        <input
                          v-model="tesis.nama_dospem_1"
                          type="text"
                          id="pembimbing_1"
                          class="form-control"
                          disabled>
                      </div>
                      <div class="form-group">
                        <label for="pembimbing_2">Dosen Pembimbing 2</label>
                        <input
                          v-model="tesis.nama_dospem_2"
                          type="text"
                          id="pembimbing_2"
                          class="form-control"
                          disabled>
                      </div>
                      <!-- tombol ajukan persetujuan -->
                      <Button v-if="is_pengajuan_persetujuan" :loading="loading_submit_persetujuan" type="primary" @click="ajukan_persetujuan_ujian_proposal()" size="large" long :disabled="loading_submit_persetujuan">
                        <span v-if="loading_submit_persetujuan">
                          Sedang Menginput..
                        </span>
                        <span v-else>
                          Ajukan persetujuan
                        </span>
                      </Button>
                      <!-- tombol jika persetujuan sudah dilakukan-->
                      <div v-else class="button-selesai">
                        <span><Icon type="checkmark"></Icon>Pengajuan persetujuan sudah dilakukan</span>
                      </div>
                    </Col>
                  </TabPane>
                  <TabPane label="Status Persetujuan" icon="ios-analytics">
                    <Row type="flex" align="middle" justify="center" style="background-color:#f5f7f9">
                      <Col span="19" style="background-color:white;padding:20px;">
                        <h4 class="text-label text-heading-status">Status Persetujuan Ujian Proposal <span @click="refresh_status('persetujuan')"><Icon type="refresh" class="icon-helper" size="20"></Icon></span></h4>
                          <template v-if="loading_get_status_persetujuan">
                            <Spin></Spin>
                          </template>
                          <template v-else>
                            <p class="text-wrap"><span class="text-label">Kaprodi : </span></p>
                            <Alert show-icon :type="persetujuan_pengajuan.persetujuan_kaprodi.alert">
                              <span v-if="persetujuan_pengajuan.persetujuan_kaprodi.status">{{ persetujuan_pengajuan.persetujuan_kaprodi.status }}</span>
                              <span v-else>-</span>
                            </Alert>
                            <p class="text-wrap"><span class="text-label">Dospem 1 : {{ tesis.nama_dospem_1 }}</span></p>
                            <Alert show-icon :type="persetujuan_pengajuan.persetujuan_dosen_1.alert">
                              <span v-if="persetujuan_pengajuan.persetujuan_dosen_1.status">{{ persetujuan_pengajuan.persetujuan_dosen_1.status }}</span>
                              <span v-else>-</span>
                            </Alert>
                            <p class="text-wrap"><span class="text-label">Dospem 2 : {{ tesis.nama_dospem_2 }}</span></p>
                            <Alert show-icon :type="persetujuan_pengajuan.persetujuan_dosen_2.alert">
                              <span v-if="persetujuan_pengajuan.persetujuan_dosen_2.status">{{ persetujuan_pengajuan.persetujuan_dosen_2.status }}</span>
                              <span v-else>-</span>
                            </Alert>
                          </template>
                      </Col>
                    </Row>
                  </TabPane>
                </Tabs>
              </Col>
            </Col>
          </Row>
        </div>
        <div v-show="ujian_proposal_step == 1" class="step-content-wrap" key="step_2">
          <h5 class="text-center text-title">Pengajuan Ujian Proposal</h5>
          <Row type="flex" justify="center">
            <Col span="18" class="pengajuan-pilih-waktu">
              <Col span="24" class="pengajuan-nav">
                <a class="pengajuan-back" @click="pengajuan_back()">
                  <Icon type="arrow-left-a"></Icon><span>Persetujuan Ujian Proposal</span>
                </a>
                <a class="pengajuan-next" @click="pengajuan_next()" style="float: right;" v-if="is_pengajuan_ujian == false">
                  <span>Persetujuan Akhir</span><Icon type="arrow-right-a"></Icon>
                </a>
              </Col>
              <Col span="24">
                <Tabs>
                  <TabPane label="Form Pengajuan" icon="compose">
                    <Alert show-icon class="pengauan-alert">Waktu ujian harap dikonsultasikan ke dospem masing-masing terlebih dahulu (1 minggu sebelum waktu pelaksanaan)</Alert>
                    <Alert show-icon class="pengauan-alert">
                      Harap Menyerahkan foto copy sertifikat TPA, TOEFL, bukti pembayaran spp semester terakhir, asli lembar pesetujuan (yang telah ditandatangani KPS) dan asli lembar mengikuti seminar (yang terisi minimal 10 kali keikutsertaan) masing-masing 1 lembar ke bagian akademik
                    </Alert>
                    <div class="form-group row">
                      <div class="col-sm-8">
                        <Button type="info" @click="show_modal_jadwal">Cek jadwal</Button>
                        <Modal
                          v-model="modal_jadwal_visible"
                          title="Jadwal Ujian Proposal"
                          @on-ok=""
                          @on-cancel="">
                          <Modal-Jadwal-Ujian-Proposal ></Modal-Jadwal-Ujian-Proposal>
                        </Modal>
                      </div>
                    </div>
                    <div class="form-group row">
                      <label for="tanggal_ujian" class="col-sm-4 col-form-label">Tanggal Ujian (Senin - Jumat)</label>
                      <div class="col-sm-8">
                        <div v-if="!form_pengajuan_disabled" key="a">
                          <pilih-tanggal v-on:pilih_tanggal="set_tgl_ujian"></pilih-tanggal>
                        </div>
                        <div v-else>
                          <input :value="tgl_ujian_proposal" type="text" class="form-control" disabled>
                        </div>
                      </div>
                    </div>
                    <div class="form-group row">
                      <label for="jam_ujian" class="col-sm-4 col-form-label">Jam Ujian</label>
                      <div class="col-sm-8">
                        <div v-if="!form_pengajuan_disabled" key="b">
                          <pilih-jam  v-on:pilih_jam="set_jam_ujian"></pilih-jam>
                        </div>
                        <div v-else>
                          <input :value="jam_ujian_proposal" type="text" class="form-control" disabled>
                        </div>
                      </div>
                    </div>
                    <Button v-if="is_pengajuan_ujian" type="primary" @click="ajukan_ujian_proposal()" size="large" long :disabled="submit_pengajuan_disabled">
                      <span v-if="loading_submit_pengajuan">
                        Sedang Menginput..
                      </span>
                      <span v-else>
                        Ajukan Ujian Proposal
                      </span>
                    </Button>
                    <div v-else class="button-selesai">
                      <span><Icon type="checkmark"></Icon>Pengajuan ujian sudah dilakukan</span>
                    </div>
                  </TabPane>
                  <TabPane label="Status Pengajuan" icon="ios-analytics">
                    <Row type="flex" align="middle" justify="center" style="background-color:#f5f7f9">
                      <Col span="22" style="background-color:white;padding:20px;">
                        <template v-if="loading_get_status_pengajuan">
                          <Spin></Spin>
                        </template>
                        <template v-else>
                          <h4 class="text-label text-heading-status">Status Pengajuan Ujian Proposal <span @click="refresh_status('pengajuan')"><Icon type="refresh" class="icon-helper" size="20"></Icon></span></h4>
                          <p class="text-wrap"><span class="text-label">Tanggal Pengajuan : </span></p>
                          <p>{{ progress_pengajuan.tgl_progress }}</p>
                          <p class="text-wrap"><span class="text-label">Akademik : </span></p>
                          <Alert show-icon :type="pengajuan_ujian.persetujuan_akademik.alert">
                            <span v-if="pengajuan_ujian.persetujuan_akademik.status">{{ pengajuan_ujian.persetujuan_akademik.status }}</span>
                            <span v-else>-</span>
                          </Alert>
                          <p class="text-wrap"><span class="text-label">Dosen Pembinbing 1 : {{ tesis.nama_dospem_1 }}</span></p>
                          <Alert show-icon :type="pengajuan_ujian.persetujuan_dosen_1.alert">
                            <span v-if="pengajuan_ujian.persetujuan_dosen_1.status">{{ pengajuan_ujian.persetujuan_dosen_1.status }}</span>
                            <span v-else>-</span>
                          </Alert>
                        </template>
                      </Col>
                    </Row>
                  </TabPane>
                </Tabs>
              </Col>
            </Col>
          </Row>
        </div>
        <div v-show="ujian_proposal_step == 2" class="step-content-wrap" key="step_3">
          <h5 class="text-center text-title">Persetujuan Akhir</h5>
          <Row type="flex" align="middle" justify="center">
            <Col span="18" style="background-color:white;padding:20px" class="pengajuan-persetujuan-kaprodi">
              <Col span="24" class="pengajuan-nav">
                <a class="pengajuan-back" @click="pengajuan_back()">
                  <Icon type="arrow-left-a"></Icon><span>Persetujuan Ujian Proposal</span>
                </a>
                <a class="pengajuan-next" @click="pengajuan_next()" style="float: right;" v-if="is_pengajuan_ujian == false">
                  <span>Selesai</span><Icon type="arrow-right-a"></Icon>
                </a>
              </Col>
              <Col span="24">
                <template v-if="loading_get_status_pengajuan">
                  <Spin></Spin>
                </template>
                <template v-else>
                  <h4 class="text-label text-heading-status">Status Pengajuan Ujian Proposal <span @click="refresh_status('pengajuan')"><Icon type="refresh" class="icon-helper" size="20"></Icon></span></h4>
                  <p class="text-wrap"><span class="text-label">Tanggal Pengajuan : </span></p>
                  <p>{{ progress_pengajuan.tgl_progress }}</p>
                  <p class="text-wrap"><span class="text-label">Kaprodi : </span></p>
                  <Alert show-icon :type="persetujuan_akhir.persetujuan_kaprodi.alert">
                    <span v-if="persetujuan_akhir.persetujuan_kaprodi.status">{{ persetujuan_akhir.persetujuan_kaprodi.status }}</span>
                    <span v-else>-</span>
                  </Alert>
                  <p class="text-wrap"><span class="text-label">Dosen Pembimbing 1 : {{ tesis.nama_dospem_1 }}</span></p>
                  <Alert show-icon :type="persetujuan_akhir.persetujuan_dosen_1.alert">
                    <span v-if="persetujuan_akhir.persetujuan_dosen_1.status">{{ persetujuan_akhir.persetujuan_dosen_1.status }}</span>
                    <span v-else>-</span>
                  </Alert>
                  <p class="text-wrap"><span class="text-label">Dosen Pembimbing 2 : {{ tesis.nama_dospem_2 }}</span></p>
                  <Alert show-icon :type="persetujuan_akhir.persetujuan_dosen_2.alert">
                    <span v-if="persetujuan_akhir.persetujuan_dosen_2.status">{{ persetujuan_akhir.persetujuan_dosen_2.status }}</span>
                    <span v-else>-</span>
                  </Alert>
                  <p class="text-wrap"><span class="text-label">Dosen Penguji 1 : {{ ujian_proposal.penguji_1 }}</span></p>
                  <Alert show-icon :type="persetujuan_akhir.persetujuan_penguji_1.alert">
                    <span v-if="persetujuan_akhir.persetujuan_penguji_1.status">{{ persetujuan_akhir.persetujuan_penguji_1.status }}</span>
                    <span v-else>-</span>
                  </Alert>
                  <p class="text-wrap"><span class="text-label">Dosen Penguji 2 : {{ ujian_proposal.penguji_2 }}</span></p>
                  <Alert show-icon :type="persetujuan_akhir.persetujuan_penguji_2.alert">
                    <span v-if="persetujuan_akhir.persetujuan_penguji_2.status">{{ persetujuan_akhir.persetujuan_penguji_2.status }}</span>
                    <span v-else>-</span>
                  </Alert>
                </template>
              </Col>
            </Col>
          </Row>
        </div>
        <div v-show="ujian_proposal_step == 3" class="step-content-wrap" key="step_4">
          <!-- <h5 class="text-center text-title">Persetujuan Akhir</h5> -->
          <Row type="flex" align="middle" justify="center">
            <Col span="18" style="background-color:white;padding:20px" class="pengajuan-persetujuan-kaprodi">
              <Col span="24" class="pengajuan-nav">
                <a class="pengajuan-back" @click="pengajuan_back()">
                  <Icon type="arrow-left-a"></Icon><span>Persetujuan Ujian Proposal</span>
                </a>
              </Col>
              <Col span="24">
                <template v-if="loading_get_status_pengajuan">
                  <Spin></Spin>
                </template>
                <template v-else>
                  <h4 class="text-label text-center">Ujian Proposal</h4>
                  <p class="text-wrap"><span class="text-label">Tanggal Ujian : </span></p>
                  <p>{{ ujian_proposal.tgl_ujian }}</p>
                  <p class="text-wrap"><span class="text-label">Jam Ujian : </span></p>
                  <p>{{ ujian_proposal.waktu_mulai_ujian }} - {{ ujian_proposal.waktu_selesai_ujian }}</p>
                  <p class="text-wrap"><span class="text-label">Ruang Ujian : </span></p>
                  <p>{{ ujian_proposal.ruang_ujian }}</p>
                  <Col span="12">
                    <p class="text-wrap"><span class="text-label">Dosen Pembimbing 1 : {{ tesis.nama_dospem_1 }}</span></p>
                    <p class="text-wrap"><span class="text-label">Dosen Pembimbing 2 : {{ tesis.nama_dospem_2 }}</span></p>
                  </Col>
                  <Col span="12">
                    <p class="text-wrap"><span class="text-label">Dosen Penguji 1 : {{ ujian_proposal.penguji_1 }}</span></p>
                    <p class="text-wrap"><span class="text-label">Dosen Penguji 2 : {{ ujian_proposal.penguji_2 }}</span></p>
                  </Col>
                </template>
              </Col>
            </Col>
          </Row>
        </div>
      </transition-group>
    </template>
  </Card>
</template>

<script>
import PilihTanggal from './Pilih_Tanggal';
import PilihJam from './Pilih_Jam';
import ModalJadwalUjianProposal from './Modal_Jadwal_Ujian_Proposal';
export default {
  name: 'UjianProposalProgress',
  props: ['id_ujian'],
  data: function(){
    return {
      ujian_proposal_step: 0,
      form_persetujuan_ujian_proposal: {
        item_read_only: true,
      },
      ajax_fetch: false,
      form_pengajuan_disabled: '',
      form_persetujuan_disabled: '',
      modal_jadwal_visible: false,
      pilih_tgl_ujian_proposal: '',
      pilih_jam_ujian_proposal: '',
      tesis: '',
      ujian_proposal: '',
      progress_persetujuan: '',
      progress_pengajuan: '',
      status_progress_persetujuan: '',
      status_progress_pengajuan: '',

      is_pengajuan_persetujuan: false,
      is_pengajuan_ujian: false,

      persetujuan_pengajuan: {
        persetujuan_kaprodi: {},
        persetujuan_dosen_1: {},
        persetujuan_dosen_2: {},
      },
      pengajuan_ujian: {
        persetujuan_akademik: {},
        persetujuan_dosen_1: {},
      },
      persetujuan_akhir: {
        persetujuan_kaprodi: {},
        persetujuan_dosen_1: {},
        persetujuan_dosen_2: {},
        persetujuan_penguji_1: {},
        persetujuan_penguji_2: {},
      },
      loading_get_status_persetujuan: false,
      loading_get_status_pengajuan: false,
      loading_submit_persetujuan: false,
      loading_submit_pengajuan: false
    }
  },
  computed: {
    submit_pengajuan_disabled: function(){
      if(this.pilih_tgl_ujian_proposal !== '' && this.pilih_jam_ujian_proposal !== ''){
        return false;
      }
      else if(this.loading_submit_pengajuan){
        return true;
      }
      else{
        return true;
      }
    },
    ujian_proposal_step_status: function(){
      if(this.progress_persetujuan.selesai == 0){
        return 'process';
      }
      else if(this.progress_persetujuan.selesai == 1){
        return 'process';
      }
      else if(this.progress_persetujuan.selesai == 2){
        return 'error';
      }
      else{
        return 'process';
      }
    },
    jam_ujian_proposal: function(){
      return this.ujian_proposal.waktu_mulai_ujian+" - "+this.ujian_proposal.waktu_selesai_ujian;
    },
    tgl_ujian_proposal: function(){
      return this.ujian_proposal.tgl_ujian;
    }
  },
  components: {
    PilihTanggal,
    ModalJadwalUjianProposal,
    PilihJam
  },
  watch: {

  },
  methods: {
    set_tgl_ujian: function(tanggal){
      this.pilih_tgl_ujian_proposal = tanggal;
    },
    set_jam_ujian: function(jam){
      this.pilih_jam_ujian_proposal = jam;
    },
    show_modal_jadwal: function(){
      this.modal_jadwal_visible = true;
    },
    pengajuan_back: function(){
      this.ujian_proposal_step -= 1;
    },
    pengajuan_next: function(){
      this.ujian_proposal_step += 1;
    },
    refresh_status: function(tipe_progress){
      if(tipe_progress == "persetujuan"){
        this.get_progress("persetujuan");
      }
      else if(tipe_progress == "pengajuan"){
        this.get_progress("pengajuan");
      }
      // this.get_ubah_status_progress(tipe_progress);
    },
    get_progress: function(tipe_progress){
      var self = this;
      if(tipe_progress == 'persetujuan'){
        $.ajax({
          type: 'POST',
          url: "https://simt.000webhostapp.com/index.php/Progress/get_progress",
          data: {token: self.$store.state.token,id_progress: self.ujian_proposal.id_persetujuan_ujian},
          success: function(response){
            if(response.progress.length == 0){
              self.is_pengajuan_persetujuan = true;
              self.form_persetujuan_disabled = false;
            }
            else{
              self.progress_persetujuan = response.progress[0];
              self.is_pengajuan_persetujuan = false;
              self.form_persetujuan_disabled = true;
              self.get_ubah_status_progress('persetujuan');
            }
          }
        })
      }
      else if(tipe_progress == 'pengajuan'){
        if(self.ujian_proposal.id_pengajuan_ujian){
          $.ajax({
            type: 'POST',
            url: "https://simt.000webhostapp.com/index.php/Progress/get_progress",
            data: {token: self.$store.state.token,id_progress: self.ujian_proposal.id_pengajuan_ujian},
            success: function(response){
              if(response.progress.length == 0){
                self.is_pengajuan_ujian = true;
                self.form_pengajuan_disabled = false;
              }
              else{
                self.progress_pengajuan = response.progress[0];
                self.is_pengajuan_ujian = false;
                self.form_pengajuan_disabled = true;
                self.get_ubah_status_progress('pengajuan');
              }
            }
          })
        }
        else{
          self.is_pengajuan_ujian = true;
          self.form_pengajuan_disabled = false;
        }
      }
    },
    get_tesis: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Tesis/get_tesis",
        data: {token: self.$store.state.token, id_tesis: self.ujian_proposal.id_tesis},
        success: function(response){
          self.tesis = response.tesis[0];
        }
      })
    },
    get_ujian_proposal: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ujian/get_ujian",
        data: {token: self.$store.state.token,id_ujian: self.id_ujian},
        success: function(response){
          self.ujian_proposal = response.ujian[0];
          if(self.ujian_proposal.penguji_1 != ''){
            let penguji_1 = self.ujian_proposal.penguji_1[0].nama_pegawai;
            self.ujian_proposal.penguji_1 = penguji_1;
          }
          else{
            self.ujian_proposal.penguji_1 = 'Belum Ada';
          }
          if(self.ujian_proposal.penguji_2 != ''){
            let penguji_2 = self.ujian_proposal.penguji_2[0].nama_pegawai;
            self.ujian_proposal.penguji_2 = penguji_2;
          }
          else{
            self.ujian_proposal.penguji_1 = 'Belum Ada';
          }
          self.get_tesis();
          self.get_progress("persetujuan");
        }
      })
    },
    get_ubah_status_progress: function(tipe_progress){
      var self = this;
      if(tipe_progress == 'persetujuan'){
        $.ajax({
          type: 'POST',
          url: "https://simt.000webhostapp.com/index.php/Progress/get_ubah_status_progress",
          beforeSend: function(){
            self.loading_get_status_persetujuan = true;
          },
          data: {token: self.$store.state.token,id_progress: self.progress_persetujuan.id_progress},
          success: function(response){
            self.status_progress_persetujuan = response;
            self.cek_status_persetujuan_pengajuan();
            self.loading_get_status_persetujuan = false;
            // console.log('caacad');
            // console.log(self.status_progress_persetujuan);
          }
        })
      }
      else if(tipe_progress == 'pengajuan'){
        $.ajax({
          type: 'POST',
          url: "https://simt.000webhostapp.com/index.php/Progress/get_ubah_status_progress",
          beforeSend: function(){
            self.loading_get_status_pengajuan = true;
          },
          data: {token: self.$store.state.token,id_progress: self.progress_pengajuan.id_progress},
          success: function(response){
            self.status_progress_pengajuan = response;
            self.cek_status_pengajuan_ujian();
            self.loading_get_status_pengajuan = false;
          }
        })
      }
    },
    ajukan_persetujuan_ujian_proposal: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ujian/ajukan_persetujuan_ujian",
        data: {
          token: self.$store.state.token,
          id_tesis: self.tesis.id_tesis,
          tipe_ujian: 'UP'
        },
        beforeSend: function(){
          self.loading_submit_persetujuan = true;
        },
        success: function(response){
          if(response.status == 'Berhasil'){
            self.loading_submit_persetujuan = false;
            self.$Notice.success({
              title: 'Data persetujuan berhasil disimpan',
            });
            self.is_pengajuan_persetujuan = false;
            // refresh status persetujuan
            self.get_progress("persetujuan");
          }
        }
      })
    },
    ajukan_ujian_proposal: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ujian/ajukan_pengajuan_ujian",
        data: {
          token: self.$store.state.token,
          // id_tesis: self.tesis.id_tesis,
          id_ujian: self.ujian_proposal.id_ujian,
          id_persetujuan_ujian: self.ujian_proposal.id_persetujuan_ujian,
          tgl_ujian: self.pilih_tgl_ujian_proposal,
          waktu_mulai_ujian: self.pilih_jam_ujian_proposal,
          tipe_ujian: 'UP'
        },
        beforeSend: function(){
          self.loading_submit_pengajuan = true;
        },
        success: function(response){
          if(response.status == 'Berhasil'){
            self.loading_submit_pengajuan = false;
            self.$Notice.success({
              title: 'Data pengajuan berhasil disimpan',
            });
            self.is_pengajuan_ujian = false;
            self.form_pengajuan_disabled = true;
            // self.get_progress("pengajuan");
            self.$router.push('/ujian-proposal');
          }
          else{
            self.loading_submit_pengajuan = false;
            self.$Notice.success({
              title: response.data,
            });
          }
        }
      })
    },
    cek_status_persetujuan_pengajuan: function(){
      var self = this;
      this.status_progress_persetujuan.forEach(function(val,index,item){
        if(item[index].id_status_progress == 'SUP01'){
          if(item[index].is_selesai == "0"){
            self.persetujuan_pengajuan.persetujuan_dosen_1.status = "Menunggu "+item[index].nama_status_progress;
            self.persetujuan_pengajuan.persetujuan_dosen_1.alert = "warning";
          }
          else if(item[index].is_selesai == "1"){
            self.persetujuan_pengajuan.persetujuan_dosen_1.status = "Sudah "+item[index].nama_status_progress+" ("+item[index].tgl_selesai+")";
            self.persetujuan_pengajuan.persetujuan_dosen_1.alert = "success";
          }
        }
        else if(item[index].id_status_progress == 'SUP02'){
          if(item[index].is_selesai == "0"){
            self.persetujuan_pengajuan.persetujuan_dosen_2.status = "Menunggu "+item[index].nama_status_progress;
            self.persetujuan_pengajuan.persetujuan_dosen_2.alert = "warning";
          }
          else if(item[index].is_selesai == "1"){
            self.persetujuan_pengajuan.persetujuan_dosen_2.status = "Sudah "+item[index].nama_status_progress+" ("+item[index].tgl_selesai+")";
            self.persetujuan_pengajuan.persetujuan_dosen_2.alert = "success";
          }
        }
        else if(item[index].id_status_progress == 'SUP03'){
          if(item[index].is_selesai == "0"){
            self.persetujuan_pengajuan.persetujuan_kaprodi.status = "Menunggu "+item[index].nama_status_progress;
            self.persetujuan_pengajuan.persetujuan_kaprodi.alert = "warning";
          }
          else if(item[index].is_selesai == "1"){
            self.persetujuan_pengajuan.persetujuan_kaprodi.status = "Sudah "+item[index].nama_status_progress+" ("+item[index].tgl_selesai+")";
            self.persetujuan_pengajuan.persetujuan_kaprodi.alert = "success";
          }
        }
        else if(item[index].id_status_progress == 'SUP04'){
          //persetujuan ujian proposal selesai
          // self.ujian_proposal_step = 1;
          // eksperiment --
          self.get_progress("pengajuan");
        }
        else if(item[index].id_status_progress == 'SUP13'){
          self.persetujuan_pengajuan.persetujuan_dosen_1.status = item[index].nama_status_progress+" ("+item[index].tgl_selesai+")";
          self.persetujuan_pengajuan.persetujuan_dosen_1.alert = "error";
        }
      });
      this.cek_step();
    },
    cek_status_pengajuan_ujian: function(){
      console.log("cek status pengajuan ujian");
      var self = this;
      this.status_progress_pengajuan.forEach(function(val,index,item){
        if(item[index].id_status_progress == 'SUP05'){
          if(item[index].is_selesai == "0"){
            self.pengajuan_ujian.persetujuan_akademik.status = "Menunggu "+item[index].nama_status_progress;
            self.pengajuan_ujian.persetujuan_akademik.alert = "warning";
          }
          else{
            self.pengajuan_ujian.persetujuan_akademik.status = "Sudah "+item[index].nama_status_progress+" ("+item[index].tgl_selesai+")";
            self.pengajuan_ujian.persetujuan_akademik.alert = "success";
          }
        }
        if(item[index].id_status_progress == 'SUP06'){
          if(item[index].is_selesai == "0"){
            self.pengajuan_ujian.persetujuan_dosen_1.status = "Menunggu "+item[index].nama_status_progress;
            self.pengajuan_ujian.persetujuan_dosen_1.alert = "warning";
          }
          else{
            self.pengajuan_ujian.persetujuan_dosen_1.status = "Sudah "+item[index].nama_status_progress+" ("+item[index].tgl_selesai+")";
            self.pengajuan_ujian.persetujuan_dosen_1.alert = "success";
            // pengajuan ujian proposal selesai
          }
        }
        if(item[index].id_status_progress == 'SUP07'){
          if(item[index].is_selesai == "0"){
            self.persetujuan_akhir.persetujuan_kaprodi.status = "Menunggu "+item[index].nama_status_progress;
            self.persetujuan_akhir.persetujuan_kaprodi.alert = "warning";
          }
          else{
            self.persetujuan_akhir.persetujuan_kaprodi.status = "Sudah "+item[index].nama_status_progress+" ("+item[index].tgl_selesai+")";
            self.persetujuan_akhir.persetujuan_kaprodi.alert = "success";
          }
        }
        if(item[index].id_status_progress == 'SUP08'){
          if(item[index].is_selesai == "0"){
            self.persetujuan_akhir.persetujuan_penguji_1.status = "Menunggu "+item[index].nama_status_progress;
            self.persetujuan_akhir.persetujuan_penguji_1.alert = "warning";
          }
          else{
            self.persetujuan_akhir.persetujuan_penguji_1.status = "Sudah "+item[index].nama_status_progress+" ("+item[index].tgl_selesai+")";
            self.persetujuan_akhir.persetujuan_penguji_1.alert = "success";
          }
        }
        if(item[index].id_status_progress == 'SUP09'){
          if(item[index].is_selesai == "0"){
            self.persetujuan_akhir.persetujuan_penguji_2.status = "Menunggu "+item[index].nama_status_progress;
            self.persetujuan_akhir.persetujuan_penguji_2.alert = "warning";
          }
          else{
            self.persetujuan_akhir.persetujuan_penguji_2.status = "Sudah "+item[index].nama_status_progress+" ("+item[index].tgl_selesai+")";
            self.persetujuan_akhir.persetujuan_penguji_2.alert = "success";
          }
        }
        if(item[index].id_status_progress == 'SUP10'){
          if(item[index].is_selesai == "0"){
            self.persetujuan_akhir.persetujuan_dosen_1.status = "Menunggu "+item[index].nama_status_progress;
            self.persetujuan_akhir.persetujuan_dosen_1.alert = "warning";
          }
          else{
            self.persetujuan_akhir.persetujuan_dosen_1.status = "Sudah "+item[index].nama_status_progress+" ("+item[index].tgl_selesai+")";
            self.persetujuan_akhir.persetujuan_dosen_1.alert = "success";
          }
        }
        if(item[index].id_status_progress == 'SUP11'){
          if(item[index].is_selesai == "0"){
            self.persetujuan_akhir.persetujuan_dosen_2.status = "Menunggu "+item[index].nama_status_progress;
            self.persetujuan_akhir.persetujuan_dosen_2.alert = "warning";
          }
          else{
            self.persetujuan_akhir.persetujuan_dosen_2.status = "Sudah "+item[index].nama_status_progress+" ("+item[index].tgl_selesai+")";
            self.persetujuan_akhir.persetujuan_dosen_2.alert = "success";
          }
        }
        if(item[index].id_status_progress == 'SUP12'){
          // persetujuan akhir ujian proposal selesai
          // self.ujian_proposal_step = 3;
        }
      });
      this.cek_step();
    },
    cek_step: function(){
      if(this.persetujuan_pengajuan.persetujuan_kaprodi.alert == "success"){
        this.ujian_proposal_step = 1;
      }
      if(this.pengajuan_ujian.persetujuan_dosen_1.alert == "success"){
        this.ujian_proposal_step = 2;
      }
      if(this.persetujuan_akhir.persetujuan_dosen_2.alert == "success"){
        this.ujian_proposal_step = 3;
      }
    },
    tes: function(){
      var self = this;
      self.ajax_fetch = true;
      self.get_ujian_proposal();
      // self.get_ujian_proposal().then(function(){
      //   console.log('ujian proposal selesai');
      //   console.log(self.ujian_proposal);
      //   self.get_tesis()
      // }).then(function(){
      //   console.log('ujian tesis selesai');
      //   console.log(self.tesis);
      //   self.get_progress("persetujuan")
      // }).then(function(){
      //   console.log('progress selesai');
      //   console.log(self.progress_persetujuan);
      //   self.get_ubah_status_progress('persetujuan')
      // }).then(function(){
      //   console.log('ubah status persetujuan selesai');
      //   self.cek_status_persetujuan_pengajuan();
      //   self.ajax_fetch = false;
      // });
        self.ajax_fetch = false;
    }
  },
  created: function(){
    this.tes();
  },
  mounted: function(){
    // this.start_flatpickr();
  }
}
</script>

