<template lang="html">
  <div class="layout-content-main">
    <div class="">
      <Card :bordered="false">
        <p slot="title">Pengajuan Judul dan dosen Pembimbing</p>
        <span slot="extra">
          <span>
            <router-link v-if="bisa_pengajuan_proposal" to="/pengajuan-judul/Pengajuan-Proposal">
              <Button type="primary" icon="plus">
                Ajukan Judul
              </Button>
            </router-link>
          </span>
        </span>
        <Spin v-if="ajax_fetch_data"></Spin>
        <template v-else>
          <div v-if="pengajuan_proposal.length == 0" class="tidak-ada-data">
            <div style="text-align:center">
              <Icon type="document-text"></Icon>
              <br>
              <span>Data pengajuan judul tidak ditemukan</span>
            </div>
          </div>
          <PengajuanProposalCard
          v-else
          v-for="(item,index) in pengajuan_proposal"
          :key="item.id_pengajuan"
          :prop_index="index"
          :prop_data_pengajuan="item"
           prop_tipe="full mode"
          :prop_filter="fiter_pengajuan_proposal">
          </PengajuanProposalCard>
        </template>
        </Card>
        <!-- komponen modal pilih dosen -->
        <modal-pilih-dosen
        v-if="$store.state.profile.pegawai"
        tipe="persetujuan_proposal"
        :modal_dosen="$store.state.modal_pilih_pembimbing.show"
        :dosen="$store.state.modal_pilih_pembimbing.dosen_pilihan"
        :row_dosen="$store.state.modal_pilih_pembimbing.row_dosen">
        </modal-pilih-dosen>
    </div>
  </div>
</template>

<script>
import PengajuanProposalCard from './Pengajuan_Proposal_Card.vue';
import ModalPilihDosen from './Modal-pilih-dosen.vue';
export default {
  name: 'pengajuan_proposal_list',
  components: {
    PengajuanProposalCard,
    ModalPilihDosen
  },
  data:function(){
    return{
      ajax_fetch_data: '',
      fiter_pengajuan_proposal: 'all',
      pengajuan_proposal: [],
    }
  },
  created: function(){
    this.set_tipe_modal();
    this.get_pengajuan_proposal();
  },
  computed: {
    bisa_pengajuan_proposal: function(){
      if(this.$store.state.profile.status_pengajuan_proposal == 'pengajuan baru'){
        return true;
      }
      else{
        return false;
      }
    }
  },
  methods: {
    get_pengajuan_proposal: function(){
      var self = this;
      self.ajax_fetch_data = true;
      this.$store.dispatch('get_pengajuan_proposal').then((progress) => {
        console.log('pengajuan proposal from ajax');
        self.pengajuan_proposal = progress;
        if(self.pengajuan_proposal.length == 0){
          self.ajax_fetch_data = false;
        }
        else{
          self.pengajuan_proposal.forEach(function(){
            self.$store.commit('push_modal_dosen_pembimbing');
          });
          self.get_dosen();
        }
      });
    },
    get_dosen: function(){
      var self = this;
      if(this.$store.state.dosen.length == 0){
        console.log(this.$store.state.dosen.length);
        console.log('get dosen from ajax');
        this.$store.dispatch('get_all_dosen').then(() => {
          self.ajax_fetch_data = false;
        });
      }
      else{
        self.ajax_fetch_data = false;
      }
    },
    set_tipe_modal: function(){
      this.$store.commit('set_tipe_modal','persetujuan_proposal');
    }
  }
}
</script>

<style lang="css">
.ivu-btn a{
  color: white !important;
}
.tidak-ada-data{
  height: 150px;
  display:flex;
  justify-content:center;
  align-items:center;
  font-size: 16px;
}
.tidak-ada-data i{
  font-size: 35px;
}
</style>



