<template lang="html">
  <div class="container-fluid login-wrap ">
    <div class="row justify-content-center align-items-center">
      <div :class="{'col-sm-6' : form_register,'col-sm-5' : form_login,'css_col' : true}">
        <div class="login-container">
          <h3>Sistem Informasi Manajemen Tesis</h3>
          <h5 class="title-1">Prodi Magister Administrasi Bisnis</h5>
          <h5 class="title-2">Fakultas Ilmu Administrasi UB</h5>
          <div  v-show="toggle == 'register'" class="login-type-wrap">
            <Button :type="tipe_mahasiswa" @click="ganti_tipe('mahasiswa')" icon="person">Mahasiswa</Button>
            <Button :type="tipe_pegawai" @click="ganti_tipe('pegawai')" icon="person">Pegawai</Button>
          </div>
          <div v-show="toggle == 'login'" key="login">
            <div class="form-group">
              <label>Email</label>
              <input
              v-model="login.email"
              type="email"
              name="username"
              placeholder="Masukkan Email"
              class="form-control">
            </div>
            <div class="form-group">
              <label for="password">Password</label>
              <input v-model="login.password"  id="password" type="password" name="password" placeholder="Masukkan Password" class="form-control">
            </div>
            <Button type="primary" @click="submit_login" class="login-button" long>Login</Button>
            <div class="toggle-wrap">
              <a @click.prevent="toggle_auth('register')" href="" class="toggle">Register</a>
              <a @click.prevent="toggle_auth('reset')" href="" class="reset">Reset Password</a>
            </div>
          </div>
          <div v-show="toggle == 'reset'" key="reset">
            <div class="form-group">
              <label>Email</label>
              <input
              v-model="reset_pass.email"
              type="email"
              name="reset"
              placeholder="Masukkan Email"
              class="form-control">
              <small id="emailHelp" class="form-text text-muted">* Password baru anda akan di kirim ke email</small>
            </div>
            <Button type="primary" @click="submit_reset" class="login-button" long>Submit</Button>
            <div class="toggle-wrap">
              <a @click.prevent="toggle_auth('login')" href="" class="toggle">Login</a>
            </div>
          </div>
          <div v-show="toggle == 'register'" key="register">
            <transition name="register" mode="out-in">
              <div v-if="register.tipe == 'mahasiswa'" key="mhs">
                <div class="form-group row">
                  <label class="col-sm-3">Email</label>
                  <div class="col-sm-9">
                    <input
                    v-model="register.mahasiswa.email.data"
                    v-validate="'required|email'"
                    data-vv-as="email"
                    :class="{'form-control': true,'input-loader': ajax_check,'is-invalid': error_email_mahasiwa}"
                    @keyup="cek_email"
                    type="email"
                    name="email_mahasiswa"
                    placeholder="Masukkan Email">
                    <div v-show="error_email_mahasiwa" class="invalid-feedback">
                      {{ error_email_mahasiwa_pesan }}
                    </div>
                  </div>
                </div>
                <div class="form-group row">
                  <label class="col-sm-3">Password</label>
                  <div class="col-sm-9">
                    <div class="input-group">
                      <input
                      v-if="show_password"
                      v-model="register.mahasiswa.password"
                      v-validate="'required'"
                      data-vv-as="password"
                      :class="{'form-control': true,'is-invalid': errors.has('password_mahasiswa')}"
                      type="text"
                      name="password_mahasiswa"
                      placeholder="Masukkan Password">

                      <input
                      v-else
                      v-model="register.mahasiswa.password"
                      v-validate="'required'"
                      data-vv-as="password"
                      :class="{'form-control': true,'is-invalid': errors.has('password_mahasiswa')}"
                      name="password_mahasiswa"
                      type="password"
                      placeholder="Masukkan Password">
                      <span class="input-group-addon">
                        <a href="" @click.prevent="change_show_password">
                          <Icon v-if="show_password" type="eye-disabled"></Icon>
                          <Icon v-else type="eye"></Icon>
                        </a>
                      </span>
                    </div>
                    <div v-show="errors.has('password_mahasiswa')" class="invalid-feedback invalid-custom">
                      {{ errors.first('password_mahasiswa') }}
                    </div>
                  </div>
                </div>
                <div class="form-group row">
                  <label class="col-sm-3">Nama</label>
                  <div class="col-sm-9">
                    <input
                    v-model="register.mahasiswa.nama"
                    v-validate="'required|alpha_spaces'"
                    data-vv-as="nama"
                    :class="{'form-control': true,'is-invalid': errors.has('nama_mahasiswa')}"
                    type="text"
                    name="nama_mahasiswa"
                    placeholder="Masukkan Nama">
                    <div v-show="errors.has('nama_mahasiswa')" class="invalid-feedback">
                      {{ errors.first('nama_mahasiswa') }}
                    </div>
                  </div>
                </div>
                <div class="form-group row">
                  <label class="col-sm-3">NIM</label>
                  <div class="col-sm-9">
                    <input
                    v-model="register.mahasiswa.nim"
                    v-validate="'required|numeric'"
                    :class="{'form-control': true,'is-invalid': errors.has('nim')}"
                    type="number"
                    name="nim"
                    placeholder="Masukkan NIM">
                    <div v-show="errors.has('nim')" class="invalid-feedback">
                      {{ errors.first('nim') }}
                    </div>
                  </div>
                </div>
                <div class="form-group row">
                  <label class="col-sm-3">No Telp</label>
                  <div class="col-sm-9">
                    <input
                    v-model="register.mahasiswa.telp"
                    v-validate="'numeric'"
                    :class="{'form-control': true,'is-invalid': errors.has('telp')}"
                    type="number"
                    name="telp"
                    placeholder="Masukkan No Telp">
                  </div>
                  <div v-show="errors.has('telp')" class="invalid-feedback">
                    {{ errors.first('telp') }}
                  </div>
                </div>
              </div>
              <div v-else key="pgw">
                <div class="form-group row">
                  <label class="col-sm-3">Email</label>
                  <div class="col-sm-9">
                    <input
                    v-model="register.pegawai.email.data"
                    v-validate="'required|email'"
                    data-vv-as="email"
                    :class="{'form-control': true,'input-loader': ajax_check,'is-invalid': error_email_pegawai}"
                    @keyup="cek_email"
                    type="email"
                    name="email_pegawai"
                    placeholder="Masukkan Email">
                    <div v-show="error_email_pegawai" class="invalid-feedback">
                      {{ error_email_pegawai_pesan }}
                    </div>
                  </div>
                </div>
                <div class="form-group row">
                  <label class="col-sm-3">Password</label>
                  <div class="col-sm-9">
                    <div class="input-group">
                      <input
                      v-if="show_password"
                      v-model="register.pegawai.password"
                      v-validate="'required'"
                      data-vv-as="password"
                      :class="{'form-control': true,'is-invalid': errors.has('password_pegawai')}"
                      name="password_pegawai"
                      type="text"
                      placeholder="Masukkan Password">

                      <input
                      v-else
                      v-model="register.pegawai.password"
                      v-validate="'required'"
                      data-vv-as="password"
                      :class="{'form-control': true,'is-invalid': errors.has('password_pegawai')}"
                      name="password_pegawai"
                      type="password"
                      placeholder="Masukkan Password">
                      <span class="input-group-addon">
                        <a href="" @click.prevent="change_show_password">
                          <Icon v-if="show_password" type="eye-disabled"></Icon>
                          <Icon v-else type="eye"></Icon>
                        </a>
                      </span>
                    </div>
                    <div v-show="errors.has('password_pegawai')" class="invalid-feedback invalid-custom">
                      {{ errors.first('password_pegawai') }}
                    </div>
                  </div>
                </div>
                <div class="form-group row">
                  <label class="col-sm-3">Nama</label>
                  <div class="col-sm-9">
                    <input
                    v-model="register.pegawai.nama"
                    v-validate="'required|alpha_spaces'"
                    data-vv-as="Nama Pegawai"
                    :class="{'form-control': true,'is-invalid': errors.has('nama_pegawai')}"
                    name="nama_pegawai"
                    type="text"
                    placeholder="Masukkan Nama">
                    <div v-show="errors.has('nama_pegawai')" class="invalid-feedback">
                      {{ errors.first('nama_pegawai') }}
                    </div>
                  </div>
                </div>
                <div class="form-group row">
                  <label class="col-sm-3">NIP</label>
                  <div class="col-sm-9">
                    <input
                    v-model="register.pegawai.nip"
                    v-validate="'numeric'"
                    type="number"
                    name="nip"
                    :class="{'form-control': true,'is-invalid':  errors.has('nip')}"
                    placeholder="Masukkan NIP">
                    <div v-show="errors.has('nip')" class="invalid-feedback">
                      {{ errors.first('nip') }}
                    </div>
                  </div>
                </div>
                <div class="form-group row">
                  <label class="col-sm-3">Bidang Minat</label>
                  <div class="col-sm-9">
                    <select v-model="register.pegawai.bidang_minat" v-validate="'required'" data-vv-as="Bidang Minat" :class="{'form-control': true,'is-invalid': errors.has('bidang_minat')}" name="bidang_minat">
                      <option value="" disabled>Pilih Bidang Minat</option>
                      <option value="1" >Pemasaran</option>
                      <option value="2" >Keuangan</option>
                      <option value="3" >Manajemen Sumber Daya Manusia</option>
                      <option value="4" >Sistem Informasi Manajemen</option>
                      <option value="5" >Pariwisata</option>
                    </select>
                    <div v-show="errors.has('bidang_minat')" class="invalid-feedback">
                      {{ errors.first('bidang_minat') }}
                    </div>
                  </div>
                </div>
              </div>
            </transition>
            <Button type="primary" @click="submit_register" class="login-button" long>Register</Button>
            <div class="toggle-wrap">
              <a @click.prevent="toggle_auth('login')" href="" class="toggle">Login</a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import md5 from 'md5'
import _ from 'lodash'
export default {
  name: 'Login',
  data: function() {
    return {
      toggle: 'login',
      show_password: false,
      login: {
        email: 'afifnandya@gmail.com',
        password: 'bumiasri'
      },
      register: {
        tipe: 'mahasiswa',
        mahasiswa: {
          nim: '',
          nama: '',
          email: {
            data: '',
            error: '',
          },
          telp: '',
          password: ''
        },
        pegawai: {
          nip: '',
          email: {
            data: '',
            error: '',
          },
          password: '',
          nama: '',
          bidang_minat: '',
        }
      },
      reset_pass: {
        email: '',
      },
      form_login: true,
      form_register: false,
      ajax_check: false,
    }
  },
  created: function(){

  },
  computed: {
    tipe_mahasiswa: function(){
      if(this.register.tipe == 'mahasiswa'){
        return 'primary'
      }
      else{
        return 'ghost'
      }
    },
    tipe_pegawai: function(){
      if(this.register.tipe == 'pegawai'){
        return 'primary'
      }
      else{
        return 'ghost'
      }
    },
    error_email_pegawai: function(){
      if(this.errors.has('email_pegawai') || this.register.pegawai.email.error){
        return true;
      }
      else{
        return false;
      }
    },
    error_email_pegawai_pesan: function(){
      if(this.errors.has('email_pegawai')){
        return this.errors.first('email_pegawai');
      }
      else if(this.register.pegawai.email.error){
        return "Email sudah terdaftar";
      }
    },
    error_email_mahasiwa: function(){
      if(this.errors.has('email_mahasiswa') || this.register.mahasiswa.email.error){
        return true;
      }
      else{
        return false;
      }
    },
    error_email_mahasiwa_pesan: function(){
      if(this.errors.has('email_mahasiswa')){
        return this.errors.first('email_mahasiswa');
      }
      else if(this.register.mahasiswa.email.error){
        return "Email sudah terdaftar";
      }
    }
  },
  methods: {
    tes: function(){
      alert('a')
    },
    ganti_tipe: function(tipe){
      this.register.tipe = tipe;
    },
    toggle_auth: function(tipe){
      if(tipe == 'login'){
        this.toggle = 'login';
        this.form_register = false;
        this.form_login = true;
      }
      else if(tipe == 'register'){
        this.toggle = 'register';
        this.form_login = false;
        this.form_register = true;
      }
      else if(tipe == 'reset'){
        this.toggle = 'reset';
      }
    },
    change_show_password: function(){
      if(this.show_password){
        return this.show_password = false
      }
      else{
        return this.show_password = true;
      }
    },
    cek_email: _.debounce(function(){
      var self = this;
      var data = {};
      if(this.register.tipe == 'mahasiswa'){
        data.mahasiswa = self.register.mahasiswa;
        $.ajax({
          type: 'POST',
          url: "https://simt.000webhostapp.com/index.php/Login/check_email",
          data: {data: data},
          beforeSend: function(){
            self.ajax_check = true;
          },
          success: function(response){
            if(response.status == 'Gagal'){
              self.register.mahasiswa.email.error = true;
            }
            else{
              self.register.mahasiswa.email.error = false;
            }
            console.log(response);
            self.ajax_check = false;
          }
        })
      }
      else{
        data.pegawai = self.register.pegawai;
        $.ajax({
          type: 'POST',
          url: "https://simt.000webhostapp.com/index.php/Login/check_email",
          data: {data: data},
          beforeSend: function(){
            self.ajax_check = true;
          },
          success: function(response){
            if(response.status == 'Gagal'){
              self.register.pegawai.email.error = true;
            }
            else{
              self.register.pegawai.email.error = false;
            }
            console.log(response);
            self.ajax_check = false;
          }
        })
      }
    },1000),
    submit_register: function(){
      var self = this;
      var data = {};
      if(this.register.tipe == 'mahasiswa'){
         data.mahasiswa = self.register.mahasiswa;
         data.mahasiswa.password = md5(self.register.mahasiswa.password);
      }
      else{
        data.pegawai = self.register.pegawai;
        data.pegawai.password = md5(self.register.pegawai.password);
      }
      this.$validator.validateAll().then((result) => {
        if (result) {
          $.ajax({
            type: 'POST',
            url: "https://simt.000webhostapp.com/index.php/Login/register",
            data: {data: data},
            success: function(response){
              console.log(response);
              if(response.status != 'Gagal'){
                // localStorage.setItem('token',html.response);
                self.toggle = 'login';
                self.reset_form();
                self.$Message.success('Registrasi berhasil');
              }
              else{
                self.$Message.error('Gagal Registrasi. Cek kembali inputan anda');
              }
            }
          })
          return;
        }
        self.$Message.error('Harap lengkapi form regsiter')
      });
    },
    submit_login: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Login/",
        data: {email: self.login.email, password: md5(self.login.password)},
        success: function(html){
          if(html.status != 'Gagal'){
            self.$store.commit('set_token',html.response);
            self.$Message.success('Berhasil Login');
            self.$router.push('/');
          }
          else{
            self.$Message.error('Gagal Login. Cek kembali inputan anda');
          }
        }
      })
    },
    submit_reset: function(){
      var self = this;
      if(this.reset_pass.email != ''){
        $.ajax({
          type: 'POST',
          url: "https://simt.000webhostapp.com/index.php/Login/reset_pass",
          data: {email: self.reset_pass.email},
          success: function(html){
            if(html.status != 'Gagal'){
              self.$Message.success('Reset Password berhasil, harap cek email anda');
              self.$router.push('/');
            }
            else{
              self.$Message.error('Reset password gagal, email tidak terdaftar');
            }
          }
        })
      }
      else{
        self.$Message.error('Harap isi email anda');
      }
    },
    reset_form: function(){
      this.register.mahasiswa.nim = '';
      this.register.mahasiswa.nama = '';
      this.register.mahasiswa.email.data = '';
      this.register.mahasiswa.email.error = '';
      this.register.mahasiswa.telp = '';
      this.register.mahasiswa.password = '';
      this.register.pegawai.nip = '';
      this.register.pegawai.email.data = '';
      this.register.pegawai.email.error = '';
      this.register.pegawai.password = '';
      this.register.pegawai.nama = '';
      this.register.pegawai.bidang_minat = '';
    }
  }
}
</script>
<style lang="css" scoped>
.input-loader{
  background-color: #ffffff;
  background-image: url("http://loadinggif.com/images/image-selection/3.gif");
  background-size: 25px 25px;
  background-position:right center;
  background-repeat: no-repeat;
}
.row{
  height: 100%;
}
.css_col{
  transition: all .2s;
}
form{
  margin-top: 1rem;
  margin-bottom: 1rem;
}
h3{
  color: black;
  text-align: center;
  font-weight: 500;
  margin-top: 10px;
  margin-bottom: 15px;
}
label{
  color: #495060;
}
h5{
  color: #495060;
  text-align: center;
}
h5.title-1{
  margin-bottom: 5px;
  font-size: 16px;
}
h5.title-2{
  margin-bottom: 25px;
  font-size: 16px;
}
.login-wrap{
  height: 100%;
  /*background-image:linear-gradient(-225deg, #2CD8D5 0%, #6B8DD6 48%, #8E37D7 100%);*/
  background-image:linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}
.login-container{
  border-radius: 4px;
  border: 1px solid white;
  background-color: white;
  color: white;
  padding: 20px;
}
.login-button{
  margin-top: 1rem;
  font-size: 18px;
}
.login-type-wrap{
  text-align: center;
  margin-bottom: 20px;
}
.invalid-feedback{

}
.invalid-custom{
  display: block;
}
.toggle-wrap{
  margin-top: 10px;
  position: relative;
}
.toggle{
  color: #80848f;
}
.reset{
  color: #80848f;
  position: absolute;
  right: 0;
}
.fade-enter-active, .fade-leave-active {
  transition: all .5s
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0
}
.register-enter-active, .register-leave-active{
  transition: all .5s;
}
.register-enter{
  transform: translateY(20px);
  /*transform: scale(0,0);*/
  opacity: 0;
}
.register-enter-to{
  /*opacity: 0;*/
}
.register-leave{
  transform: translateY(-20px);
  opacity: 0;
  /*transform: scale(0,0);*/
}
.register-leave-to{
  /*opacity: 0;*/
}
/*end*/
</style>



