<template lang="html">
  <input type="text" name="" value="" placeholder="Pilih Jam..">
</template>

<script>
import flatpickr from 'flatpickr';
import 'flatpickr/dist/flatpickr.min.css';
import { Indonesian } from 'flatpickr/dist/l10n/id.js';
import 'flatpickr/dist/themes/airbnb.css';
export default {
  name: 'PilihJam',
  props: {
    
  },
  methods: {
    selected_time: function(jam){
      this.$emit('pilih_jam',jam);
    }
  },
  mounted: function(){
    var self = this;
    flatpickr(this.$el, {
      "locale": Indonesian,
      "enableTime": true,
      "noCalendar": true,
      "altInput": true,
      "time_24hr": true,
      "defaultHour": 12,
      "defaultMinute": 0,
      // "minDate": "7:00 AM",
	    // "maxDate": "4:00 PM",
      onClose: function(selectedDates, dateStr, instance) {
        self.selected_time(dateStr);
      },
    });
  }
}
</script>

<style lang="css">
  .form-control:disabled, .form-control[readonly]{
    background-color: white !important;
  }
</style>



