<template lang="html">
  <div class="layout-content-main">
    <div class="">
      <Card :bordered="false">
        <p slot="title">Ujian Proposal Tesis</p>
        <span slot="extra">
          <router-link v-if="bisa_ujian_proposal" to="/ujian-proposal/Pengajuan">
            <Button type="primary" icon="plus">
              Ajukan Ujian Proposal Tesis
            </Button>
          </router-link>
        </span>
        <Spin v-if="ajax_fetch_data"></Spin>
        <template v-else>
          <div v-if="tesis.length == 0" class="tidak-ada-data">
            <div style="text-align:center">
              <Icon type="document-text"></Icon>
              <br>
              <span>Data Tesis tidak ditemukan,<br> Ujian Proposal Belum Bisa Diajukan</span>
            </div>
          </div>
          <div v-else-if="ujian_proposal.length == 0" class="tidak-ada-data">
            <div style="text-align:center">
              <Icon type="document-text"></Icon>
              <br>
              <span>Data Ujian Proposal tidak ditemukan</span>
            </div>
          </div>
          <card-tesis
          v-else
          v-for="(item,index) in tesis"
          key="index"
          :prop_data="item"
          :prop_ujian="ujian_proposal"
          prop_tipe="ujian-proposal">
          </card-tesis>
        </template>
        </Card>
    </div>
  </div>
</template>

<script>
import CardTesis from './Card_Tesis.vue';
import ModalPilihDosen from './Modal-pilih-dosen.vue';
export default {
  name: 'ujian_proposal_list',
  components: {
    CardTesis,
    ModalPilihDosen
  },
  data:function(){
    return{
      ajax_fetch_data: '',
      ajax_fetch_bisa_ujian_proposal: '',
      tesis: '',
      ujian_proposal: '',
      status: this.$store.state.profile.status_ujian_proposal,
    }
  },
  created: function(){
    // this.cek_bisa_ujian_proposal();
    this.get_tesis_by_nim();
  },
  computed: {
    bisa_ujian_proposal: function(){
      if(this.status == 'pengajuan baru'){
        return true;
      }
      else{
        return false;
      }
    }
  },
  methods: {
    get_tesis_by_nim: function(){
      console.log('get tesis from ajax');
      let nim = this.$store.state.profile.mahasiswa.nim_mahasiswa;
      this.ajax_fetch_data = true;
      var self = this;
      this.$store.dispatch('get_tesis_by_nim',nim).then((tesis) => {
        this.tesis = tesis;
        if(this.tesis.length != 0){
          this.get_ujian_proposal();
        }
        this.ajax_fetch_data = false;
      });
    },
    get_ujian_proposal: function(){
      var self = this;
      this.tesis.forEach(function(val,index,item){
        var args = {};
        args.id_tesis = item[index].id_tesis;
        args.tipe_ujian = 'UP'
        self.$store.dispatch('get_ujian_by_id_tesis',args).then((ujian_proposal) => {
          self.ujian_proposal = ujian_proposal;
        });
      });
    },
  }
}
</script>

<style lang="css">
.ivu-btn a{
  color: white !important;
}
.tidak-ada-data{
  height: 150px;
  display:flex;
  justify-content:center;
  align-items:center;
  font-size: 16px;
}
.tidak-ada-data i{
  font-size: 35px;
}
</style>



