<template lang="html">
  <div>
<table style="width:100%">
  <tr>
    <th>Tanggal</th>
    <th>Jam</th>
    <th>Jenis Ujian</th>
  </tr>
  <tr v-for="item in ujian" v-if="item.tgl_ujian != '0000-00-00'">
    <td>{{ item.tgl_ujian }}</td>
    <td>{{ item.waktu_mulai_ujian+" - "+item.waktu_selesai_ujian }}</td>
    <td><Tag type="dot" color="blue">{{ item.tipe_ujian }}</Tag></td>
  </tr>
</table>
  </div>
</template>

<script>
export default {
  name: "ModalJadwalUjianProposal",
  props: {

  },
  //variable
  data: function(){
    return{
      ujian: ''
    }
  },
  computed: {

  },
  components: {

  },
  //methods
  watch: {

  },
  methods: {
    get_ujian: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ujian/get_jadwal_ujian",
        data: {token: self.$store.state.token},
        success: function(response){
          if(response.ujian.length == 0){

          }
          else{
            self.ujian = response.ujian;
          }
        }
      })
    }
  },
  //components life cycle hook
  created: function(){
    this.get_ujian();
  }
}
</script>

<style lang="css" scoped>
tr,td,th {
  padding: 5px;
  border: 1px solid black;
}
</style>



