// import Login from './components/Login.vue'
// import Admin from './components/Admin.vue'

import Index from './components/Index.vue';
import Dashboard from './components/Dashboard.vue';
import Pengajuan_Proposal_Index from './components/Pengajuan_Proposal_Index.vue';
import Pengajuan_Proposal_Form from './components/Pengajuan_Proposal_Form.vue';
import Pengajuan_Proposal_Progress from './components/Pengajuan_Proposal_Progress.vue';
import Pengajuan_Proposal_List from './components/Pengajuan_Proposal_List.vue';
import Login from './components/Login.vue';
import Reset_Pass from './components/Reset_Pass.vue'
import Profile from './components/Profile.vue'

import Ujian_Proposal_Index from './components/Ujian_proposal_Index.vue';
import Ujian_Proposal_List from './components/Ujian_proposal_list.vue';
import Ujian_Proposal_Form from './components/Ujian_proposal_form.vue';
import Ujian_Proposal_Progress from './components/Ujian_Proposal_Progress.vue';
import Ujian_Proposal_Persetujuan from './components/Ujian_Proposal_Persetujuan.vue';
import Ujian_Proposal_Pengajuan from './components/Ujian_Proposal_Pengajuan.vue';
import Ujian_Proposal_Pelaksanaan from './components/Ujian_Proposal_Pelaksanaan.vue';

import Seminar_Hasil_Index from './components/Seminar_Hasil_Index.vue';
import Seminar_Hasil_List from './components/Seminar_Hasil_List.vue';
import Seminar_Hasil_Form from './components/Seminar_Hasil_Form.vue';
import Seminar_Hasil_Progress from './components/Seminar_Hasil_Progress.vue';
import Seminar_Hasil_Persetujuan from './components/Seminar_Hasil_Persetujuan.vue';
import Seminar_Hasil_Pengajuan from './components/Seminar_Hasil_Pengajuan.vue';
import Seminar_Hasil_Pelaksanaan from './components/Seminar_Hasil_Pelaksanaan.vue';

import Ujian_Tesis_Index from './components/Ujian_Tesis_Index.vue';
import Ujian_Tesis_List from './components/Ujian_Tesis_List.vue';
import Ujian_Tesis_Form from './components/Ujian_Tesis_Form.vue';
import Ujian_Tesis_Progress from './components/Ujian_Tesis_Progress.vue';
import Ujian_Tesis_Persetujuan from './components/Ujian_Tesis_Persetujuan.vue';
import Ujian_Tesis_Pengajuan from './components/Ujian_Tesis_Pengajuan.vue';
import Ujian_Tesis_Pelaksanaan from './components/Ujian_Tesis_Pelaksanaan.vue';

import Mahasiswa_Index from './components/Mahasiswa_Index.vue';
import Mahasiswa_Profile_Index from './components/Mahasiswa_Profile_Index.vue';
import Mahasiswa_Profile from './components/Mahasiswa_Profile.vue';
import Mahasiswa_Pengajuan_Judul from './components/Mahasiswa_Pengajuan_Judul.vue';
import Mahasiswa_Ujian_Proposal from './components/Mahasiswa_Ujian_Proposal.vue';
import Mahasiswa_Tesis from './components/Mahasiswa_Tesis.vue';
import Mahasiswa_Detail_Tesis from './components/Mahasiswa_Detail_Tesis.vue';
import Mahasiswa_Detail_Proposal from './components/Mahasiswa_Detail_Proposal.vue';

import Dosen_Index from './components/Dosen_Index.vue';
import Dosen_Profile_Index from './components/Dosen_Profile_Index.vue';
import Dosen_Profile from './components/Dosen_Profile.vue';

import Dokumen_BA_Ujian_Proposal from './components/Dokumen_BA_Ujian_Proposal.vue';
import Dokumen_BA_Seminar_Hasil from './components/Dokumen_BA_Seminar_Hasil.vue';
import Dokumen_BA_Ujian_Tesis from './components/Dokumen_BA_Ujian_Tesis.vue';
import Dashboard_Kaprodi from './components/Dashboard_Kaprodi.vue';
// Routes
const routes = [
  {
    path: '/',
    component: Index,
    meta: {
      requiresAuth: true
    },
    children: [
      {
        path: '',
        component: Dashboard,
        meta: {
          requiresAuth: true
        }
      },
      {
        path: 'Tesis',
        component: Mahasiswa_Profile_Index,
        meta: {
          requiresAuth: true
        },
        children: [
          { path: '', component: Mahasiswa_Profile, meta: { requiresAuth: true } },
          { path: ':id_tesis', component: Mahasiswa_Detail_Tesis, meta: { requiresAuth: true } },
          { path: 'pengajuan-judul', component: Mahasiswa_Pengajuan_Judul, meta: { requiresAuth: true } },
          { path: 'ujian-proposal', component: Mahasiswa_Ujian_Proposal, meta: { requiresAuth: true } }
        ]
      },
      {
        path: 'Ujian-Proposal',
        component: Ujian_Proposal_Index,
        meta: {
          requiresAuth: true
        },
        children: [
          { path: '', component: Ujian_Proposal_List, meta: { requiresAuth: true } },
          { path: 'Pengajuan', component: Ujian_Proposal_Form, meta: { requiresAuth: true } },
          { path: 'Persetujuan-Ujian-Proposal', component: Ujian_Proposal_Persetujuan, meta: { requiresAuth: true } },
          { path: 'Pengajuan-Ujian-Proposal', component: Ujian_Proposal_Pengajuan, meta: { requiresAuth: true } },
          { path: 'Pelaksanaan-Ujian-Proposal', component: Ujian_Proposal_Pelaksanaan, meta: { requiresAuth: true } },
          { path: ':id_ujian', component: Ujian_Proposal_Progress, props: true, meta: { requiresAuth: true } },
        ]
      },
      {
        path: 'pengajuan-judul',
        component: Pengajuan_Proposal_Index,
        meta: {
          requiresAuth: true
        },
        children: [
          { path: '', component: Pengajuan_Proposal_List, meta: { requiresAuth: true } },
          { path: 'Pengajuan-Proposal', component: Pengajuan_Proposal_Form, meta: { requiresAuth: true } },
          { path: ':id_proposal', component: Pengajuan_Proposal_Progress, props: true, meta: { requiresAuth: true } },
        ]
      },
      {
        path: 'Seminar-Hasil',
        component: Seminar_Hasil_Index,
        meta: {
          requiresAuth: true
        },
        children: [
          { path: '', component: Seminar_Hasil_List, meta: { requiresAuth: true } },
          { path: 'Pengajuan', component: Seminar_Hasil_Form, meta: { requiresAuth: true } },
          { path: 'Pengajuan-Seminar-Hasil', component: Seminar_Hasil_Pengajuan, meta: { requiresAuth: true } },
          { path: 'Persetujuan-Seminar-Hasil', component: Seminar_Hasil_Persetujuan, meta: { requiresAuth: true } },
          { path: 'Pelaksanaan-Seminar-Hasil', component: Seminar_Hasil_Pelaksanaan, meta: { requiresAuth: true } },
          { path: ':id_ujian', component: Seminar_Hasil_Progress, props: true, meta: { requiresAuth: true } },
        ]
      },
      {
        path: 'Ujian-Tesis',
        component: Ujian_Tesis_Index,
        meta: {
          requiresAuth: true
        },
        children: [
          { path: '', component: Ujian_Tesis_List, meta: { requiresAuth: true } },
          { path: 'Pengajuan', component: Ujian_Tesis_Form, meta: { requiresAuth: true } },
          { path: 'Pengajuan-Ujian-Tesis', component:Ujian_Tesis_Pengajuan, meta: { requiresAuth: true } },
          { path: 'Persetujuan-Ujian-Tesis', component: Ujian_Tesis_Persetujuan, meta: { requiresAuth: true } },
          { path: 'Pelaksanaan-Ujian-Tesis', component: Ujian_Tesis_Pelaksanaan, meta: { requiresAuth: true } },
          { path: ':id_ujian', component: Ujian_Tesis_Progress, props: true, meta: { requiresAuth: true } },
        ]
      },
      {
        path: 'Mahasiswa',
        component: Mahasiswa_Index,
        meta: {
          requiresAuth: true
        },
      },
      {
        path: 'Mahasiswa/:nim',
        component: Mahasiswa_Profile_Index,
        meta: {
          requiresAuth: true
        },
        children: [
          { path: '', component: Mahasiswa_Profile, meta: { requiresAuth: true } },
          { path: 'tesis', component: Mahasiswa_Tesis, meta: { requiresAuth: true } },
          { path: 'tesis/:id_tesis', component: Mahasiswa_Detail_Tesis, meta: { requiresAuth: true } },
          { path: 'pengajuan-judul', component: Mahasiswa_Pengajuan_Judul, meta: { requiresAuth: true } },
          { path: 'pengajuan-judul/:id_pengajuan', component: Mahasiswa_Detail_Proposal, meta: { requiresAuth: true } },
          { path: 'ujian-proposal', component: Mahasiswa_Ujian_Proposal, meta: { requiresAuth: true } }
        ]
      },
      {
        path: 'Dosen',
        component: Dosen_Index,
        meta: {
          requiresAuth: true
        },
      },
      {
        path: 'Dosen/:nip',
        component: Dosen_Profile_Index,
        meta: {
          requiresAuth: true
        },
        children: [
          { path: '', component: Dosen_Profile, meta: { requiresAuth: true } },
        ]
      },
      {
        path: 'tes',
        component: Dashboard_Kaprodi,
        meta: {
          requiresAuth: true
        },
      },
      {
        path: 'profile',
        component: Profile,
        meta: {
          requiresAuth: true
        },
      },
      {
        path: 'reset_pass',
        component: Reset_Pass,
        meta: {
          requiresAuth: true
        },
      }
    ]
  },
  {
    path: '/Dokumen/Ujian-Proposal/:id_tesis',
    component: Dokumen_BA_Ujian_Proposal,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/Dokumen/Seminar-Hasil/:id_tesis',
    component: Dokumen_BA_Seminar_Hasil,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/Dokumen/Ujian-Tesis/:id_tesis',
    component: Dokumen_BA_Ujian_Tesis,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/Login',
    component: Login
  }
]

export default routes



