<template lang="html">
  <transition name="fade" mode="out-in">
    <Spin size="large" v-if="ajax_fetch_data"></Spin>
    <Row :class="[progress_visibility, progress_class]" v-else>
      <!-- item header -->
      <Col span="18">
        <router-link :to="link_detail_seminar_hasil">
          <p class="judul">
            {{ tesis_mhs.judul_tesis }}
          </p>
        </router-link>
        <p class="nama-mhs">
          <Avatar class="avatar-mahasiswa" size="small">{{ inisial_nama_mhs }}</Avatar>
          {{ tesis_mhs.nama_mahasiswa }}
          <span class="pengajuan-nim-mhs">({{ tesis_mhs.nim_mahasiswa }})</span>
        </p>
        <p>
          <span>Status : </span>
          <Alert :type="tipe_alert_status_progress" show-icon class="status-sekarang">{{ status_progress_sekarang }}</Alert>
          <Alert v-if="pengajuan_seminar_hasil_ditolak" show-icon class="status-sekarang">{{ status_terakhir.ekstra_detail_status }}</Alert>
        </p>
      </Col>
      <!-- item button group -->
      <Col span="5" offset="1" style="text-align:right">
        <Button-group>
          <Button type="info" icon="chevron-down" size="small" @click="toggle_detail()">Detail</Button>
          <Button v-if="kelola_setujui" type="success" icon="checkmark-round" size="small" @click="toggle_setujui()">Setujui</Button>
          <Button v-if="kelola_tolak" type="error" icon="android-cancel" size="small" @click="toggle_tolak()">Tolak</Button>
          <Button v-if="kelola_input_nilai" type="primary" icon="document-text" size="small" @click="show_modal_seminar_hasil()">Isi Nilai</Button>
          <Button v-if="kelola_input_surat" type="success" icon="checkmark-round" size="small" @click="konfirmasi_akademik()">Setujui</Button>
        </Button-group>
      </Col>
      <!-- bagian detail progress -->
      <Row :class="[detail_visibility, detail_class]">
        <Col span="22" offset="1" class="confirm-box">
          <Col span="24" class="confirm-box-section">
            <p class="confirm-box-section-label">Informasi Tesis</p>
          </Col>
          <Col span="24">
            <Col span="9" class="col-calon-dospem">
              <p class="progress-bidang-minat">Judul Tesis</p>
              <p>{{ tesis_mhs.judul_tesis }}</p>
            </Col>
            <Col span="5" class="col-bidang-minat">
              <p class="progress-bidang-minat">Bidang Minat</p>
              <p> <Tag color="yellow">{{ tesis_mhs.nama_bidang_minat }}</Tag></p>
            </Col>
            <Col span="5" class="col-tgl-progress">
              <p class="progress-tgl">Tgl. Pengajuan</p>
              <p>{{ data_progress.tgl_progress }}</p>
            </Col>
            <Col span="5" class="col-status">
              <p class="progress-calon-dospem">Dospem</p>
              <ul>
                <li>
                  <Avatar class="avatar-dosen" size="small">{{ inisial_nama(tesis_mhs.nama_dospem_1) }}</Avatar>
                  {{ tesis_mhs.nama_dospem_1 }}
                </li>
                <li>
                  <Avatar class="avatar-dosen" size="small">{{ inisial_nama(tesis_mhs.nama_dospem_2) }}</Avatar>
                  {{ tesis_mhs.nama_dospem_2 }}
                </li>
              </ul>
            </Col>
          </Col>
          <Col span="24" class="confirm-box-section">
            <p class="confirm-box-section-label">Informasi Seminar Hasil</p>
          </Col>
          <Col span="24">
            <template v-if="ajax_fetch_data">
              <Spin size="large"></Spin>
            </template>
            <template v-else>
              <Col span="9" class="col-calon-dospem">
                <p class="progress-bidang-minat">Tanggal Ujian</p>
                <p>{{ seminar_hasil.tgl_ujian }}</p>
              </Col>
              <Col span="5" class="col-bidang-minat">
                <p class="progress-bidang-minat">Jam Ujian</p>
                <p>{{ seminar_hasil.waktu_mulai_ujian }} - {{ seminar_hasil.waktu_selesai_ujian }}</p>
              </Col>
              <Col span="10" class="col-tgl-progress">
                <p class="progress-tgl">Ruang Ujian</p>
                <p>{{ seminar_hasil.ruang_ujian | is_kosong }}</p>
              </Col>
            </template>
          </Col>
        </Col>
      </Row>
      <!-- bagian setujui progress -->
      <Row type="flex" justify="center" align="middle" :class="[setujui_visibility, detail_class]">
        <Col span="20" class="confirm-box">
          <Col span="24" class="confirm-box-inner" id="setujui">
            <div>
              <h5>Setujui Pengajuan Seminar Hasil ini ?</h5>
              <Button size="large" type="primary" :loading="ajax_fetch_submit" @click="setujui_progress()">Ok</Button>
            </div>
          </Col>
        </Col>
      </Row>
      <!-- bagian tolak progress -->
      <Row type="flex" justify="center" align="middle" :class="[tolak_visibility, detail_class]">
        <Col span="20">
          <Col span="24" class="confirm-box">
            <div class="confirm-box-inner" id="tolak">
              <h5>Tolak Pengajuan Seminar Hasil ini ?</h5>
              <div class="form-group">
                <label for="alasan_tolak">Alasan Penolakan</label>
                <textarea
                  v-model="alasan_tolak"
                  v-validate="'required'"
                  name="alasan_tolak"
                  id="alasan_tolak"
                  :class="{'form-control': true,'is-invalid': errors.has('alasan_tolak')}"
                  >
                </textarea>
                <div v-show="errors.has('alasan_tolak')" class="invalid-feedback">
                  {{ errors.first('alasan_tolak') }}
                </div>
                <Button size="large" type="error" @click="tolak_progress()">Ok</Button>
              </div>
            </div>
          </Col>
        </Col>
      </Row>
      <!-- modal insert nilai seminar hasil -->
      <Modal
        v-if="seminar_hasil"
        v-model="modal_seminar_hasil.visible"
        width="50"
        :styles	="modal_seminar_hasil.styles"
        title="Input Nilai Seminar Hasil"
        @on-ok="">
        <form>
          <!-- Pembimbing 1 -->
          <p>{{ tesis_mhs.nama_dospem_1 }} <span>(Pemibimbing 1)</span></p>
          <div class="form-group row">
            <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Draf Naskah</label>
            <div class="col-sm-7">
              <InputNumber
                v-model="modal_seminar_hasil.nilai_draf_pembimbing_1"
                :max="100"
                :min="0">
              </InputNumber>
            </div>
          </div>
          <div class="form-group row">
            <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Penyajian Naskah</label>
            <div class="col-sm-7">
              <InputNumber
                v-model="modal_seminar_hasil.nilai_penyajian_pembimbing_1"
                :max="100"
                :min="0">
              </InputNumber>
            </div>
          </div>
          <div class="form-group row">
            <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Diskusi</label>
            <div class="col-sm-7">
              <InputNumber
                v-model="modal_seminar_hasil.nilai_diskusi_pembimbing_1"
                :max="100"
                :min="0">
              </InputNumber>
            </div>
          </div>
          <div class="form-group row">
            <label for="pembimbing_1" class="col-sm-5 col-form-label">Saran Perbaikan</label>
            <div class="col-sm-7">
              <Input v-model="modal_seminar_hasil.saran_pembimbing_1" placeholder="Masukkan Saran Perbaikan"></Input>
            </div>
          </div>
          <!-- Pembimbing 2 -->
          <p>{{ tesis_mhs.nama_dospem_2 }} <span>(Pemibimbing 2)</span></p>
          <div class="form-group row">
            <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Draf Naskah</label>
            <div class="col-sm-7">
              <InputNumber
                v-model="modal_seminar_hasil.nilai_draf_pembimbing_2"
                :max="100"
                :min="0">
              </InputNumber>
            </div>
          </div>
          <div class="form-group row">
            <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Penyajian Naskah</label>
            <div class="col-sm-7">
              <InputNumber
                v-model="modal_seminar_hasil.nilai_penyajian_pembimbing_2"
                :max="100"
                :min="0">
              </InputNumber>
            </div>
          </div>
          <div class="form-group row">
            <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Diskusi</label>
            <div class="col-sm-7">
              <InputNumber
                v-model="modal_seminar_hasil.nilai_diskusi_pembimbing_2"
                :max="100"
                :min="0">
              </InputNumber>
            </div>
          </div>
          <div class="form-group row">
            <label for="pembimbing_1" class="col-sm-5 col-form-label">Saran Perbaikan</label>
            <div class="col-sm-7">
              <Input v-model="modal_seminar_hasil.saran_pembimbing_2" placeholder="Masukkan Saran Perbaikan"></Input>
            </div>
          </div>
        </form>
        <div slot="footer">
          <Button type="primary" size="large" long :loading="ajax_fetch_submit" @click="insert_nilai_seminar_hasil">Submit</Button>
        </div>
      </Modal>
    </Row>
  </transition>
</template>
<script>
export default {
  name: 'SeminarHasilCard',
  props: {
    prop_data_progress: '',
    prop_filter: '',
    prop_index: '',
  },
  data: function(){
    return {
      ajax_fetch_data: '',
      ajax_fetch_submit: false,
      data_progress: this.prop_data_progress,
      data_setujui_ubah_progress: {},//set_data_setujui_progress
      detail_class: 'progress-item-detail',
      detail_visibility: 'hide',
      formValidate: {
        alasan_tolak: '',
      },
      inisial_nama_mhs: '',
      kelola_setujui: '',
      kelola_tolak: '',
      kelola_input_surat: '',
      kelola_input_nilai: '',
      progress_class: 'progress-item',
      progress_visibility: 'show',
      mahasiswa: '',
      alasan_tolak: '',
      status_terakhir: '', //status_terakhir dari progress
      setujui_visibility: 'hide',
      tesis_mhs: '',
      tipe_progress: '', //tipe_progress
      tolak_visibility: 'hide',
      url_ajax_ubah_status: '',// url ajax tertentu (tiap proses beda)
      seminar_hasil: {},
      modal_seminar_hasil: {
        visible: false,

        nilai_draf_pembimbing_1: 0,
        nilai_penyajian_pembimbing_1: 0,
        nilai_diskusi_pembimbing_1: 0,
        saran_pembimbing_1: '',

        nilai_draf_pembimbing_2: 0,
        nilai_penyajian_pembimbing_2: 0,
        nilai_diskusi_pembimbing_2: 0,
        saran_pembimbing_2: '',
        styles: {
          top: '20px'
        }
      },
    }
  },
  computed: {
    link_detail_seminar_hasil: function(){
      return '/seminar-hasil/'+this.seminar_hasil.id_ujian
    },
    status_progress_sekarang: function(){
      if(this.status_terakhir.ekstra_detail_status != ''){
        return 'Telah '+this.status_terakhir.nama_status_progress;
      }
      else{
        if(this.status_terakhir.is_selesai == 1){
          if(this.status_terakhir.id_status_progress == 'SSH10' || this.status_terakhir.id_status_progress == 'SSH16'){
            return this.status_terakhir.nama_status_progress;
          }
          else{
            return 'Sudah '+this.status_terakhir.nama_status_progress;
          }
        }
        else{
          return 'Menunggu '+this.status_terakhir.nama_status_progress;
        }
      }
    },
    tipe_alert_status_progress: function(){
      if(this.status_terakhir.ekstra_detail_status != ''){
        return 'error';
      }
      else{
        if(this.status_terakhir.is_selesai == 1){
          return 'success';
        }
        else{
          return 'warning';
        }
      }
    },
    pengajuan_seminar_hasil_ditolak: function(){
      if(this.status_terakhir.ekstra_detail_status != ''){
        return true;
      }
      else{
        return false;
      }
    },
    status_progress_baru: function(){
      if(this.tipe_progress == 'PRG05'){
        var split_status_progress = this.status_terakhir.id_status_progress.split("");
        if(split_status_progress[3] == "0"){
          var temp = parseInt(split_status_progress[4])+1
          return "SSH0"+temp;
        }
        else{
          var temp = parseInt(split_status_progress[4])+1+10;
          return "SSH"+temp;
        }
      }
      else if(this.tipe_progress == 'PRG06' || this.tipe_progress == 'PRG07'){
        var split_status_progress = this.status_terakhir.id_status_progress.split("");
        if(split_status_progress[3] == "0"){
          if(split_status_progress[4] == "9"){
            var temp = parseInt(split_status_progress[4])+1;
            return "SSH"+temp;
          }
          else{
            var temp = parseInt(split_status_progress[4])+1;
            return "SSH0"+temp;
          }
        }
        else{
          var temp = parseInt(split_status_progress[4])+1+10;
          return "SSH"+temp;
        }
      }
      else{
        return 'aw';
      }
    },
  },
  components: {

  },
  watch: {

  },
  filters: {
    is_kosong(val) {
      if(val == ''){
        return "Tidak Ada";
      }
      else{
        return val;
      }
    }
  },
  methods: {
    inisial_nama: function(nama){
      var split_nama = nama.split(" ");
      if(split_nama.length > 1){
        return split_nama[0].charAt(0)+split_nama[1].charAt(0);
      }
      else{
        return split_nama[0].charAt(0)
      }
    },
    kelola_data_progress: function(){
      if(this.$store.state.profile.pegawai){
        if(this.$store.state.profile.pegawai.id_pegawai == this.status_terakhir.id_pegawai && this.status_terakhir.is_selesai == 0){
          //untuk akademik insert nilai
          if(this.status_terakhir.id_status_progress == 'SSH14'){
            this.kelola_setujui = false;
            this.kelola_input_nilai = true;
          }
          else if(this.status_terakhir.id_status_progress == 'SSH15') {
            this.kelola_setujui = false;
            this.kelola_input_surat = true;
          }
          else{
            this.kelola_setujui = true;
            this.kelola_input_nilai = false;
            this.kelola_input_surat = false;
          }
          //untuk dosen pembimbing menolak seminar hasil
          if(this.status_terakhir.id_status_progress == 'SSH01' || this.status_terakhir.id_status_progress == 'SSH02' || this.status_terakhir.id_status_progress == 'SSH03'){
            this.kelola_tolak = true;
          }
          else{
            this.kelola_tolak = false;
          }
        }
        else{
          this.kelola_setujui = false;
          this.kelola_tolak = false;
          this.kelola_input_nilai = false;
          this.kelola_input_surat = false;
        }
      }
      else{
        this.kelola_setujui = false;
        this.kelola_tolak = false;
      }
    },
    get_mahasiswa: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Mahasiswa/get_mahasiswa",
        data: {token: self.$store.state.token,nim_mahasiswa: self.data_progress.nim_mahasiswa},
        beforeSend: function(){
          this.ajax_fetch_data = true;
        },
        success: function(response){
          self.mahasiswa = response.mahasiswa[0];
          self.set_inisial_nama();
          self.get_tesis();
          console.log(response);
          this.ajax_fetch_data = false;
        }
      })
    },
    get_status_progress: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ubah_Status_Progress/get_ubah_status_progress",
        data: {token: self.$store.state.token,id_progress: self.data_progress.id_progress},
        beforeSend: function(){
          self.ajax_fetch_data = true;
        },
        success: function(response){
          var ubah_status_progress = response.ubah_status_progress;
          ubah_status_progress.forEach(function(val,index,item){
            if(ubah_status_progress[index].is_selesai == 0){
              if(!self.status_terakhir){
                self.status_terakhir = ubah_status_progress[index];
              }
            }
            else if(ubah_status_progress[index].is_selesai == 1){
              if(!self.status_terakhir){
                self.status_terakhir = ubah_status_progress[index];
              }
            }
            else if(ubah_status_progress[index].is_selesai == 2){
              self.status_terakhir = ubah_status_progress[index];
            }
          });
          self.ajax_fetch_data = false;
          self.kelola_data_progress();
          console.log(response);
        }
      })
    },
    get_tesis: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Tesis/get_tesis",
        data: {token: self.$store.state.token,nim_mahasiswa: self.mahasiswa.nim_mahasiswa},
        success: function(response){
          self.tesis_mhs = response.tesis[0];
          console.log(response);
          self.get_seminar_hasil();
        }
      })
    },
    get_seminar_hasil: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ujian/get_ujian",
        data: {token: self.$store.state.token,id_tesis: self.tesis_mhs.id_tesis,tipe_ujian: 'SH'},
        beforeSend: function(){
          self.ajax_fetch_data = true;
        },
        success: function(response){
          self.seminar_hasil = response.ujian[0];
          self.ajax_fetch_data = false;
        }
      })
    },
    konfirmasi_akademik: function(){
      this.data_setujui_ubah_progress.tipe = 'seminar-hasil';
      this.data_setujui_ubah_progress.token = this.$store.state.token;
      this.data_setujui_ubah_progress.id_progress = this.data_progress.id_progress;
      this.data_setujui_ubah_progress.id_status_progress_baru = this.status_progress_baru;
      this.data_setujui_ubah_progress.id_ubah_status_progress = this.status_terakhir.id_ubah_status_progress;
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ujian/konfirmasi_akademik",
        data: self.data_setujui_ubah_progress,
        beforeSend: function(){
          self.ajax_submit = true;
        },
        success: function(response){
          if(response.status == 'berhasil'){
            self.$Notice.success({
              title: 'Konfirmasi Akademik Berhasil',
            });
            // self.setujui_visibility = 'hide';
            self.status_terakhir= '';
            self.get_status_progress();
            self.kelola_setujui = false;
            self.kelola_tolak = false;
            self.kelola_input_nilai = false;
            self.kelola_input_surat = false;
            self.ajax_submit = false;
          }
        }
      })
    },
    show_modal_seminar_hasil: function(){
      this.modal_seminar_hasil.visible = true;
    },
    insert_nilai_seminar_hasil: function(){
      if(this.modal_seminar_hasil.saran_pembimbing_1 != '' &&
         this.modal_seminar_hasil.saran_pembimbing_2 != ''
       ){
         this.modal_seminar_hasil.nilai_akhir = (this.modal_seminar_hasil.nilai_diskusi_pembimbing_1+
                                                 this.modal_seminar_hasil.nilai_diskusi_pembimbing_1+
                                                 this.modal_seminar_hasil.nilai_draf_pembimbing_1+
                                                 this.modal_seminar_hasil.nilai_draf_pembimbing_2+
                                                 this.modal_seminar_hasil.nilai_penyajian_pembimbing_1+
                                                 this.modal_seminar_hasil.nilai_penyajian_pembimbing_2)/6;
         var self = this;
         $.ajax({
           type: 'POST',
           url: "https://simt.000webhostapp.com/index.php/Ujian/insert_nilai_ujian",
           data: {
             token: this.$store.state.token,
             tipe: 'seminar-hasil',
             nilai: this.modal_seminar_hasil,
             pembimbing_1: this.tesis_mhs.id_dospem_1,
             pembimbing_2: this.tesis_mhs.id_dospem_2,
             id_ujian: this.seminar_hasil.id_ujian
           },
           beforeSend: function(){
             self.ajax_fetch_submit = true;
           },
           success: function(response){
             self.konfirmasi_akademik();
             self.ajax_fetch_submit = false;
             self.modal_seminar_hasil.visible = false;
             if(response.status == 'berhasil'){
               self.$Notice.success({
                 title: 'Insert Nilai Seminar Hasil Berhasil',
               });
               self.get_seminar_hasil();
             }
           }
         })
       }
       else{
         this.$Message.error('Harap Isi seluruh inputan form');
       }
    },
    setujui_progress: function(index){
      this.set_data_setujui_ubah_progress();
      var self = this;
      $.ajax({
        type: 'POST',
        url: self.url_ajax_ubah_status,
        beforeSend: function(){
          self.ajax_fetch_submit = true;
        },
        data: self.data_setujui_ubah_progress,
        success: function(response){
          console.log(response);
          if(response.status == 'berhasil'){
            self.$Notice.success({
              title: 'Progress Seminar Hasil Telah Disetujui',
            });
            self.toggle_setujui();
            // self.setujui_visibility = 'hide';
            self.status_terakhir= '';
            self.get_status_progress();
            self.kelola_setujui = false;
            self.kelola_tolak = false;
            self.ajax_fetch_submit = false;
          }
        }
      })
    },
    tolak_progress: function(){
      this.set_data_setujui_ubah_progress();
      this.data_setujui_ubah_progress.alasan_tolak = this.alasan_tolak;
      this.data_setujui_ubah_progress.id_status_progress = this.status_terakhir.id_status_progress;
      var self = this;
      if(this.alasan_tolak != '') {
        $.ajax({
          type: 'POST',
          url: "https://simt.000webhostapp.com/index.php/Ujian/tolak_persetujuan_ujian/",
          data: self.data_setujui_ubah_progress,
          success: function(response){
            console.log(response);
            if(response.status == 'berhasil'){
              self.$Notice.success({
                title: 'Progress Seminar Hasil Telah Ditolak',
              });
              self.tolak_visibility = 'hide';
              self.status_terakhir= '';
              self.get_status_progress();
              self.kelola_setujui = false;
              self.kelola_tolak = false;
            }
          }
        })
      }
      else {
          this.$Message.error('harap isi alasan penolakan');
      }
    },
    //set
    set_data_setujui_ubah_progress: function(){
      if(this.data_progress.id_tipe_progress == 'PRG06'){
        this.data_setujui_ubah_progress.id_tesis = this.tesis_mhs.id_tesis;
      }
      this.data_setujui_ubah_progress.tipe = 'seminar-hasil';
      this.data_setujui_ubah_progress.token = this.$store.state.token;
      this.data_setujui_ubah_progress.id_progress = this.data_progress.id_progress;
      this.data_setujui_ubah_progress.id_status_progress_baru = this.status_progress_baru;
      this.data_setujui_ubah_progress.id_ubah_status_progress = this.status_terakhir.id_ubah_status_progress;
      this.data_setujui_ubah_progress.id_dospem_1 = this.tesis_mhs.id_dospem_1;
      this.data_setujui_ubah_progress.id_dospem_2 = this.tesis_mhs.id_dospem_2;
      this.data_setujui_ubah_progress.nim_mahasiswa = this.tesis_mhs.nim_mahasiswa;
    },
    set_inisial_nama: function(){
      var split_nama = this.mahasiswa.nama_mahasiswa.split(" ");
      if(split_nama[1]){
        this.inisial_nama_mhs = split_nama[0].charAt(0)+split_nama[1].charAt(0);
      }
      else{
        this.inisial_nama_mhs = split_nama[0].charAt(0);
      }
    },
    set_tipe_progress: function(){
      if(this.data_progress.id_tipe_progress == "PRG05"){
        this.tipe_progress = 'PRG05';
        this.url_ajax_ubah_status = 'https://simt.000webhostapp.com/index.php/Ujian/setujui_persetujuan_ujian';
      }
      else if(this.data_progress.id_tipe_progress == "PRG06"){
        this.tipe_progress = 'PRG06';
        this.url_ajax_ubah_status = 'https://simt.000webhostapp.com/index.php/Ujian/setujui_pengajuan_ujian';
      }
      else if(this.data_progress.id_tipe_progress == "PRG07"){
        this.tipe_progress = 'PRG07';
      }
    },
    //fungsi toggle animasi hide/show
    toggle_progress_seminar_hasil: function(){
      if(this.prop_filter == 'all'){
        this.progress_seminar_hasil_visibility = 'show';
      }
      else if(this.prop_filter == 'new'){
        if(this.status_terakhir.is_selesai == 0){
          this.progress_seminar_hasil_visibility = 'show';
        }
        else{
          this.progress_seminar_hasil_visibility = 'hide';
        }
      }
    },
    toggle_detail: function(){
      if(this.detail_visibility == 'show'){
        this.detail_visibility = 'hide';
      }
      else{
        this.tolak_visibility = 'hide';
        this.setujui_visibility = 'hide';
        this.detail_visibility = 'show';
      }
    },
    toggle_setujui(){
      if(this.setujui_visibility == 'show'){
        this.setujui_visibility = 'hide'
      }
      else{
        this.tolak_visibility = 'hide';
        this.detail_visibility = 'hide';
        this.setujui_visibility = 'show';
      }
    },
    toggle_tolak(){
      if(this.tolak_visibility == 'show'){
        this.tolak_visibility = 'hide';
      }
      else{
        this.setujui_visibility = 'hide';
        this.detail_visibility = 'hide';
        this.tolak_visibility = 'show';
      }
    }
  },
  created: function(){
    this.get_mahasiswa();
    this.get_status_progress();
    this.set_tipe_progress();
  },
}
</script>
<style lang="css" scoped>
