  height: 50px;
  line-height: 40px;
}
.item-progress-tgl{
  padding-bottom: 5px;
  padding-top: 5px;
  padding-left: 10px;
  border-right: 1px solid #dddee1;
  height: 50px;
  line-height: 40px;
}
.item-progress-aksi{
  padding-bottom: 5px;
  padding-top: 5px;
  padding-left: 10px;
  border-right: 1px solid #dddee1;
  height: 50px;
}
.confirm-box-section-label{
  border-bottom: 2px solid #e9eaec;
}
/*end dipakai*/
.progress-item{
  padding-top: 15px;
  padding-bottom: 15px;
  padding-right: 10px;
  border-bottom: 1px solid #e9eaec;
}
.progress-item-detail{
  background-color: #e9eaec;
  width: 100%;
}

.progress-nama-mhs{
  margin-top: 10px;
  margin-bottom: 10px;
}
.col-calon-dospem,.col-tgl-progress,.col-bidang-minat,.col-status{
  border-right: 1px solid #e9eaec;
  padding-left: 10px;
  padding-right: 10px;
}
.progress-calon-dospem,.progress-bidang-minat,.progress-status,.progress-tgl{
  padding-top: 5px;
