<template lang="html">
  <transition name="fade" mode="out-in">
    <Spin size="large" v-if="ajax_fetch_data"></Spin>
    <Row type="flex" :class="[pengajuan_visibility, pengajuan_class]" v-else>
      <!-- pengajuan item header -->
      <Col span="18" style="padding-bottom:15px">
        <router-link :to="link_detail_pengajuan">
          <p class="pengajuan-judul">
            {{ data_pengajuan.judul_tesis }}
            <Tag color="yellow">{{ data_pengajuan.nama_bidang_minat }}</Tag>
          </p>
        </router-link>
        <p class="pengajuan-nama-mhs">
          <Avatar class="avatar-mahasiswa" size="small">{{ inisial_nama(data_pengajuan.nama_mahasiswa) }}</Avatar>
          <span>{{ data_pengajuan.nama_mahasiswa }}</span>
          <span class="pengajuan-nim-mhs">({{ data_pengajuan.nim_mahasiswa }})</span>
        </p>
        <p class="pengajuan-tgl">Tgl Pengajuan : {{ data_pengajuan.tgl_progress }}</p>
      </Col>
      <!-- pengajuan item button group -->
      <Col span="5" offset="1" style="text-align:right">
        <Button-group>
          <Button type="info" icon="chevron-down" size="small" @click="toggle_detail()">Detail</Button>
          <template v-if="kelola_setujui">
            <Button type="success" icon="checkmark-round" size="small" @click="toggle_setujui()">Setujui</Button>
          </template>
          <template v-else-if="kelola_verifikasi">
            <Button type="warning" icon="android-warning" size="small" @click="toggle_verifikasi()">Verifikasi</Button>
          </template>
          <Button v-if="kelola_tolak" type="error" icon="android-cancel" size="small" @click="toggle_tolak()">Tolak</Button>
        </Button-group>
      </Col>
      <!-- bagian detail pengajuan -->
      <Row type="flex" justify="center" align="middle" :class="[detail_visibility, detail_class]">
        <Col span="22" class="confirm-box">
          <Col span="8" class="col-calon-dospem">
            <template v-if="data_pengajuan.dospem.length != 0">
              <p class="pengajuan-calon-dospem">Dosen Pembimbing</p>
              <ul>
                <li class="pengajuan-dospem" v-for="(item,index) in data_pengajuan.dospem">
                  <Avatar class="avatar-dosen" size="small">{{ inisial_nama(item.nama_pegawai) }}</Avatar>
                  {{ item.nama_pegawai }}
                </li>
              </ul>
            </template>
            <template v-else>
              <p class="pengajuan-calon-dospem">Pilihan Calon Dospem</p>
              <ul>
                <li class="pengajuan-dospem" v-for="(item,index) in data_pengajuan.calon_dospem">
                  <Avatar class="avatar-dosen" size="small">{{ inisial_nama(item.nama_pegawai) }}</Avatar>
                  <span>{{ item.nama_pegawai }}</span>
                </li>
              </ul>
            </template>
          </Col>
          <Col span="10" class="col-bidang-minat">
            <p class="pengajuan-bidang-minat">Linkage Penelitian</p>
            <p>Dosen : {{ data_pengajuan.linkage_dosen }}</p>
            <p>Tema : {{ data_pengajuan.linkage_tema }}</p>
          </Col>
          <Col span="7" class="col-status">
            <p class="pengajuan-status">Status</p>
            <Alert :type="status_pengajuan_alert" show-icon>{{ status_pengajuan_sekarang }}</Alert>
            <Alert v-if="pengajuan_proposal_selesai" show-icon>Menjadi Proposal Tesis</Alert>
            <Alert v-else-if="pengajuan_proposal_ditolak" show-icon>{{ status_terakhir.ekstra_detail_status }}</Alert>
          </Col>
        </Col>
      </Row>
      <!-- bagian verifikasi pengajuan -->
      <Row v-if="kelola_verifikasi" type="flex" justify="center" align="middle" :class="[verifikasi_visibility, detail_class]">
        <Col span="20" class="confirm-box">
          <div class="confirm-box-inner">
            <h5>Setujui Pengajuan Proposal ini ?</h5>
            <div class="text-center" style="margin-top:20px;">
              <div class="checklist">
                <p>Harap Cek apakah mahasiswa sudah memenuhi syarat berikut</p>
                <Checkbox v-model="checklist.matakuliah">Telah menyelesaikan seluruh mata kuliah teori dengan nilai minimal C+</Checkbox>
                <Checkbox v-model="checklist.tpa">Fotokopi Sertifikat TPA OTO-Bappenas</Checkbox>
                <Checkbox v-model="checklist.toefl">Fotokopi Sertifikat TOEFL</Checkbox>
                <Checkbox v-model="checklist.krs">Terdapat Matakuliah Tesis Pada KRS</Checkbox>
                <Checkbox v-model="checklist.spp">Fotokopi 2 Slip pembayaran SPP</Checkbox>
                <Button :disabled="!checklist_akademik_lengkap" :loading="ajax_submit" size="large" type="primary" @click="setujui_pengajuan(1)">
                  <span v-if="!ajax_submit">Ok</span>
                  <span v-else>Loading...</span>
                </Button>
                <Button :disabled="checklist_akademik_lengkap" :loading="ajax_submit" size="large" type="error" @click="verifikasi_tolak()">
                  <span v-if="!ajax_submit">Tolak</span>
                  <span v-else>Loading...</span>
                </Button>
              </div>
            </div>
          </div>
        </Col>
      </Row>
      <!-- bagian setujui pengajuan -->
      <Row v-if="kelola_setujui" type="flex" justify="center" align="middle" :class="[setujui_visibility, detail_class]">
        <Col span="20" class="confirm-box">
          <div class="confirm-box-inner">
            <div v-if="pilih_dospem_visibility">
              <Steps :current="setujui_peganjuan_step">
                <Step title="Pilih Dosen" content="Pilih Dosen Sebagai Pembimbing"></Step>
                <Step title="Konfirmasi" content="Konfirmasi persetujuan"></Step>
              </Steps>
              <transition name="fade" mode="out-in">
                <!-- Step 1 -->
                <Row v-if="setujui_peganjuan_step == 0" class="pilih_dospem_box" key="0">
                  <Col span="12" class="pilih_dospem_box--left">
                    <p class="pengajuan-calon-dospem">Pilihan Calon Dospem Oleh Mahasiswa</p>
                    <ul>
                      <li class="pengajuan-dospem" v-for="(item,index) in data_pengajuan.calon_dospem">
                        <Avatar class="avatar-dosen" size="small">{{ inisial_nama(item.nama_pegawai) }}</Avatar>
                        {{ item.nama_pegawai }}
                      </li>
                    </ul>
                  </Col>
                  <Col span="12" class="pilih_dospem_box--right">
                    <p class="pengajuan-calon-dospem">Pilih Dospem</p>
                    <div>
                      <span>Dospem 1 : </span>
                      <span v-if="dospem_pilihan.dosen_1 != ''">
                        <Avatar class="avatar-dosen" size="small">{{ inisial_nama(dospem_pilihan.dosen_1.nama_pegawai) }}</Avatar>
                        {{ dospem_pilihan.dosen_1.nama_pegawai }}
                      </span>
                      <Button type="primary" icon="ios-search-strong" size="small" @click="pilih_dospem(1)" v-if="dospem_pilihan.dosen_1 == ''">Pilih</Button>
                      <Button type="error" icon="close-round" size="small" @click="hapus_dospem(1)" v-else="dospem_pilihan.dosen_1 != ''">Cancel</Button>
                    </div>
                    <div>
                      <span>Dospem 2 : </span>
                      <span v-if="dospem_pilihan.dosen_2 != ''">
                        <Avatar class="avatar-dosen" size="small">{{ inisial_nama(dospem_pilihan.dosen_2.nama_pegawai) }}</Avatar>
                        {{ dospem_pilihan.dosen_2.nama_pegawai }}
                      </span>
                      <Button type="primary" icon="ios-search-strong" size="small" @click="pilih_dospem(2)" v-if="dospem_pilihan.dosen_2 == ''">Pilih</Button>
                      <Button type="error" icon="close-round" size="small" @click="hapus_dospem(2)" v-else="dospem_pilihan.dosen_2 != ''">Cancel</Button>
                    </div>
                    <div>
                      <Button long :disabled="submit_dospem" type="primary" @click="setujui_pengajuan(0)">Next</Button>
                    </div>
                  </Col>
                </Row>
                <!-- Step 2 -->
                <Row v-if="setujui_peganjuan_step == 1" class="pilih_dospem_confirm" key="1">
                  <h5>Setujui Pengajuan Proposal ini ?</h5>
                  <Card class="text-left">
                    <span class="pengajuan-judul">{{ data_pengajuan.judul_tesis }}</span>
                    <span><Tag color="yellow">{{ data_pengajuan.nama_bidang_minat }}</Tag></span>
                    <br>
                    <Avatar class="avatar-mahasiswa" size="small">{{ inisial_nama(data_pengajuan.nama_mahasiswa) }}</Avatar>
                    <span>{{ data_pengajuan.nama_mahasiswa }}</span>
                    <span class="pengajuan-nim-mhs">({{ data_pengajuan.nim_mahasiswa }})</span>
                    <br>
                    <p>Pembimbing : </p>
                    <ul>
                      <li class="pengajuan-dospem" v-for="(item,index) in dospem_pilihan">
                        <Avatar class="avatar-dosen" size="small">{{ inisial_nama(item.nama_pegawai) }}</Avatar>
                        {{ item.nama_pegawai }}</li>
                    </ul>
                    <div class="text-right">
                      <Button size="large" type="ghost" @click="back_setujui_pengajuan">Back</Button>
                      <Button  :loading="ajax_submit" size="large" type="primary" @click="setujui_pengajuan(1)">
                        <span v-if="!ajax_submit">Ok</span>
                        <span v-else>Loading...</span>
                      </Button>
                    </div>
                  </Card>
                </Row>
              </transition>
            </div>
            <div v-else>
              <h5>Setujui Pengajuan Proposal ini ?</h5>
              <div class="text-center" style="margin-top:20px;">
                <div class="checklist" v-if="$store.state.profile.pegawai.tipe_pegawai == 'Akademik'">
                  <p>Harap Cek apakah mahasiswa sudah memenuhi syarat berikut</p>
                  <Checkbox v-model="checklist.matakuliah">Telah menyelesaikan seluruh mata kuliah teori dengan nilai minimal C+</Checkbox>
                  <Checkbox v-model="checklist.tpa">Fotokopi Sertifikat TPA OTO-Bappenas</Checkbox>
                  <Checkbox v-model="checklist.toefl">Fotokopi Sertifikat TOEFL</Checkbox>
                  <Checkbox v-model="checklist.krs">Terdapat Matakuliah Tesis Pada KRS</Checkbox>
                  <Checkbox v-model="checklist.spp">Fotokopi 2 Slip pembayaran SPP</Checkbox>
                  <Button :disabled="!checklist_akademik_lengkap" :loading="ajax_submit" size="large" type="primary" @click="setujui_pengajuan(1)">
                    <span v-if="!ajax_submit">Ok</span>
                    <span v-else>Loading...</span>
                  </Button>
                </div>
                <Button v-else :loading="ajax_submit" size="large" type="primary" @click="setujui_pengajuan(1)">
                  <span v-if="!ajax_submit">Ok</span>
                  <span v-else>Loading...</span>
                </Button>
              </div>
            </div>
          </div>
        </Col>
      </Row>
      <!-- bagian tolak pengajuan -->
      <Row v-if="kelola_setujui" type="flex" justify="center" align="middle" :class="[tolak_visibility, detail_class]">
        <Col span="15" class="confirm-box">
          <div class="confirm-box-inner">
            <h5>Tolak Pengajuan ini ?</h5>
            <div class="form-group">
              <label for="alasan_tolak">Alasan Penolakan</label>
              <textarea
                v-model="alasan_tolak"
                v-validate="'required'"
                name="alasan_tolak"
                id="alasan_tolak"
                :class="{'form-control': true,'is-invalid': errors.has('alasan_tolak')}"
                >
              </textarea>
              <div v-show="errors.has('alasan_tolak')" class="invalid-feedback">
                {{ errors.first('alasan_tolak') }}
              </div>
            </div>
            <Button size="large" type="error" @click="tolak_pengajuan" icon="checkmark">Ok</Button>
          </div>
        </Col>
      </Row>
    </Row>
  </transition>
</template>
<script>
// import ModalPilihDosen from './Modal-pilih-dosen.vue';
export default {
  name: 'PengajuanProposalCard',
  props: {
    prop_data_pengajuan: '',
    prop_filter: '',
    prop_tipe: '',
    prop_index: ''
  },
  data: function(){
    return {
      data_pengajuan: this.prop_data_pengajuan,
      pengajuan_class: 'pengajuan-item',
      pengajuan_visibility: 'show',
      detail_class: 'pengajuan-item-detail',
      detail_visibility: 'hide',
      setujui_visibility: 'hide',
      verifikasi_visibility: 'hide',
      tolak_visibility: 'hide',
      kelola_setujui: '',
      kelola_verifikasi: '',
      kelola_tolak: '',
      status_terakhir: '',
      checklist: {
        matakuliah: '',
        tpa: '',
        toefl: '',
        krs: '',
        spp: '',
      },
      //dospem
      setujui_peganjuan_step: 0,
      ajax_fetch_data: true,
      ajax_submit: false,
      alasan_tolak: '',
      dospem_pilihan: {
        dosen_1: '',
        dosen_2: '',
      },
    }
  },
  computed: {
    link_detail_pengajuan: function(){
      return '/pengajuan-judul/'+this.data_pengajuan.id_pengajuan
    },
    watch_me: function(){
      return this.$store.state.modal_pilih_pembimbing.dosen_pilihan[this.prop_index];
    },
    submit_dospem: function(){
      if(this.dospem_pilihan.dosen_1 != '' && this.dospem_pilihan.dosen_2 != ''){
        return false;
      }
      else{
        return true;
      }
    },
    pilih_dospem_visibility: function(){
      if(this.$store.state.profile.pegawai){
        if(this.$store.state.profile.pegawai.tipe_pegawai == 'Kaprodi' && this.status_terakhir.id_status_progress != "SP05"){
          return true;
        }
        else{
          return false;
        }
      }
      else{
        return false;
      }
    },
    dospem: function(){
      if(this.data_pengajuan.dospem.length){
        var dospem = {};
        dospem.id_pembimbing_1 = this.data_pengajuan.dospem[0].id_pegawai;
        dospem.id_pembimbing_2 = this.data_pengajuan.dospem[1].id_pegawai;
        return dospem;
      }
      else{
        return '';
      }
    },
    status_pengajuan_sekarang: function(){
      if(this.status_terakhir.ekstra_detail_status != ''){
        return this.status_terakhir.nama_status_progress;
      }
      else{
        if(this.status_terakhir.is_selesai == 1){
          return 'Sudah '+this.status_terakhir.nama_status_progress;
        }
        else{
          return 'Menunggu '+this.status_terakhir.nama_status_progress;
        }
      }
    },
    status_pengajuan_alert: function(){
      if(this.status_terakhir.ekstra_detail_status != ''){
        return 'error';
      }
      else{
        if(this.status_terakhir.is_selesai == 1){
          return 'success';
        }
        else{
          return 'warning';
        }
      }
    },
    status_pengajuan_baru: function(){
      var split_status_progress = this.status_terakhir.id_status_progress.split("");
      var temp = parseInt(split_status_progress[3])+1;
      return "SP0"+temp;
    },
    pengajuan_proposal_selesai: function(){
      if(this.status_terakhir.id_status_progress == "SP06"){
        return true;
      }
      else{
        return false;
      }
    },
    pengajuan_proposal_ditolak: function(){
      if(this.status_terakhir.ekstra_detail_status != ''){
        return true;
      }
      else{
        return false;
      }
    },
    checklist_akademik_lengkap: function(){
      if(this.checklist.matakuliah && this.checklist.tpa && this.checklist.toefl && this.checklist.krs && this.checklist.spp){
        return true;
      }
      else{
        return false;
      }
    }
  },
  components: {},
  watch: {
    prop_filter: function(){
      this.toggle_pengajuan();
    },
    watch_me: {
      handler: function(after,before){
        this.dospem_pilihan.dosen_1 = this.$store.state.modal_pilih_pembimbing.dosen_pilihan[this.prop_index].dosen_1;
        this.dospem_pilihan.dosen_2 = this.$store.state.modal_pilih_pembimbing.dosen_pilihan[this.prop_index].dosen_2;
      },
      deep: true,
    }
  },
  methods: {
    inisial_nama: function(nama){
      var split_nama = nama.split(" ");
      if(split_nama.length > 1){
        return split_nama[0].charAt(0)+split_nama[1].charAt(0);
      }
      else{
        return split_nama[0].charAt(0)
      }
    },
    set_row_dosen: function(){
      this.$store.state.dosen.forEach(function(val,index,item){
        val.rekomendasi = '';
      });
      if(this.$store.state.profile.pegawai){
        var self = this;
        this.data_pengajuan.calon_dospem.forEach(function(val,index,item){
          var rekomendasi_dospem1 = item[0].id_pegawai;
          var rekomendasi_dospem2 = item[1].id_pegawai;
          self.$store.state.dosen.find(function(element){
            if(element.id_pegawai == rekomendasi_dospem1){
              element.rekomendasi = "rekomendasi dospem 1";
            }
            else if(element.id_pegawai == rekomendasi_dospem2){
              element.rekomendasi = "rekomendasi dospem 2";
            }
            else if(element.id_pegawai == val.id_pegawai){
              element.rekomendasi = "alternatif_dospem";
            }
          });
        });
      }
    },
    back_setujui_pengajuan: function(){
      this.setujui_peganjuan_step = 0;
    },
    kelola_data_pengajuan: function(){
      if(this.$store.state.profile.pegawai && this.prop_tipe == 'full mode'){
        if(this.$store.state.profile.pegawai.id_pegawai == this.status_terakhir.id_pegawai && this.status_terakhir.is_selesai == 0){
          if(this.$store.state.profile.pegawai.tipe_pegawai == 'Akademik'){
            this.kelola_verifikasi = true;
          }
          else if(this.$store.state.profile.pegawai.tipe_pegawai == 'Kaprodi'){
            this.kelola_setujui = true;
            this.kelola_tolak = true;
          }
          else if(this.$store.state.profile.pegawai.tipe_pegawai == 'Dosen'){
            this.kelola_setujui = true;
            this.kelola_tolak = false;
          }
        }
        else{
          this.kelola_setujui = false;
          this.kelola_tolak = false;
          this.kelola_verifikasi = false;
        }
      }
      else{
        this.kelola_setujui = false;
        this.kelola_tolak = false;
      }
    },
    get_status_pengajuan: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Pengajuan_Proposal/get_status_pengajuan",
        data: {token: self.$store.state.token,id_pengajuan: self.data_pengajuan.id_pengajuan},
        beforeSend: function(){
          self.ajax_fetch_data = true;
        },
        success: function(response){
          var status_pengajuan = response.status_progress;
          status_pengajuan.forEach(function(val,index,item){
            if(status_pengajuan[index].is_selesai == 0){
              if(!self.status_terakhir){
                self.status_terakhir = status_pengajuan[index];
              }
            }
            //status terakhir dari pengajuan proposal (tolak/terima)
            else if(status_pengajuan[index].id_status_progress == "SP06" || status_pengajuan[index].id_status_progress == "SP07" || status_pengajuan[index].id_status_progress == "SP08"){
              self.status_terakhir = status_pengajuan[index];
            }
          });
          self.ajax_fetch_data = false;
          self.kelola_data_pengajuan();
          console.log(status_pengajuan);
        }
      })
    },
    setujui_pengajuan: function(index){
      if(index == 0){
        this.setujui_peganjuan_step = 1;
      }
      else if(index == 1){
        var self = this;
        $.ajax({
          type: 'POST',
          url: "https://simt.000webhostapp.com/index.php/Pengajuan_Proposal/setujui_pengajuan_proposal",
          data: {
            token: self.$store.state.token,
            id_progress: self.data_pengajuan.id_pengajuan,
            id_status_progress_baru: self.status_pengajuan_baru,
            id_ubah_status_progress: self.status_terakhir.id_ubah_status_progress,
            dospem_pilihan: self.dospem_pilihan,
            dospem: self.dospem
          },
          beforeSend: function(){
            self.ajax_submit = true;
          },
          success: function(response){
            console.log(response);
            if(response.status == 'berhasil'){
              self.$Notice.success({
                title: 'Pengajuan Proposal berhasil disetujui',
              });
              self.ajax_submit = false;
              self.toggle_setujui();
              self.setujui_visibility = 'hide';
              self.verifikasi_visibility = 'hide';
              self.kelola_setujui = false;
              self.kelola_tolak = false;
              self.status_terakhir = '';
              self.get_status_pengajuan();
            }
          }
        })
      }
    },
    tolak_pengajuan: function(){
      var self = this;
      this.$validator.validateAll().then((result) => {
        if (result) {
          $.ajax({
            type: 'POST',
            url: "https://simt.000webhostapp.com/index.php/Pengajuan_Proposal/tolak_pengajuan_proposal/",
            data: {
              token: self.$store.state.token,
              id_progress: self.data_pengajuan.id_pengajuan,
              id_ubah_status_progress: self.status_terakhir.id_ubah_status_progress,
              alasan_tolak: self.alasan_tolak
            },
            success: function(response){
              console.log(response);
              if(response.status == 'berhasil'){
                self.$Notice.success({
                  title: 'Pengajuan Proposal telah ditolak',
                });
                self.toggle_tolak();
                self.setujui_visibility = 'hide';
                self.verifikasi_visibility = 'hide';
                self.kelola_setujui = false;
                self.kelola_tolak = false;
                self.get_status_pengajuan();
              }
            }
          })
          return;
        }
        self.$Message.error('Harap lengkapi form alasan penolakan')
      });
    },
    verifikasi_tolak: function(){
      var self = this;
      var i = 0;
      for(const prop in this.checklist){
        if(this.checklist[prop] == false){
          i=i+1;
          this.alasan_tolak = this.alasan_tolak+` ${i}`+". Syarat "+prop+" tidak memenuhi,";
        }
      }
      this.tolak_pengajuan();
    },
    pilih_dospem: function(index){
      this.$store.commit('set_index_modal',this.prop_index);
      this.$store.commit('pilih_dosen_pembimbing_ke',index);
      this.$store.commit('open_modal_pilih_pembimbing');
      this.set_row_dosen();
    },
    hapus_dospem: function(index){
      this.$store.commit('pilih_dosen_pembimbing_ke',index);
      this.$store.commit('hapus_dosen_pembimbing');
    },
    toggle_pengajuan: function(){
      if(this.prop_filter == 'all'){
        this.pengajuan_visibility = 'show';
      }
      else if(this.prop_filter == 'new'){
        if(this.status_terakhir.is_selesai == 0){
          this.pengajuan_visibility = 'show';
        }
        else{
          this.pengajuan_visibility = 'hide';
        }
      }
    },
    toggle_detail: function(){
      if(this.detail_visibility == 'show'){
        this.detail_visibility = 'hide';
      }
      else{
        this.tolak_visibility = 'hide';
        this.setujui_visibility = 'hide';
        this.verifikasi_visibility = 'hide';
        this.detail_visibility = 'show';
      }
    },
    toggle_verifikasi: function(){
      if(this.verifikasi_visibility == 'show'){
        this.verifikasi_visibility = 'hide';
      }
      else{
        this.verifikasi_visibility = 'show';
        this.detail_visibility = 'hide';
      }
    },
    toggle_setujui: function(){
      if(this.setujui_visibility == 'show'){
        this.setujui_visibility = 'hide';
      }
      else{
        this.tolak_visibility = 'hide';
        this.detail_visibility = 'hide';
        this.setujui_visibility = 'show';
      }
    },
    toggle_tolak: function(){
      if(this.tolak_visibility == 'show'){
        this.tolak_visibility = 'hide';
      }
      else{
        this.setujui_visibility = 'hide';
        this.detail_visibility = 'hide';
        this.tolak_visibility = 'show';
      }
    }
  },
  created: function(){
    this.get_status_pengajuan();
    // this.set_row_dosen();
  },
}
</script>
