<template lang="html">
  <div class="layout-content-main">
    <div class="">
      <Card :bordered="false">
        <p slot="title">Ujian Proposal Tesis</p>
        <a slot="extra">tes</a>
        <ujian-proposal-card v-for="(item,index) in progress" :key="item.id_progress" :prop_data_progress="item"></ujian-proposal-card>
        </Card>
    </div>
  </div>
</template>

<script>
import UjianProposalCard from './Ujian_Proposal_Card.vue';
export default {
  name: "MahasiswaUjianProposal",
  props: {

  },
  //variable
  data: function(){
    return{
      progress: ''
    }
  },
  computed: {

  },
  components: {
    UjianProposalCard
  },
  //methods
  watch: {

  },
  methods: {
    get_progress: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ujian_Proposal/get_progress_ujian_proposal",
        data: {token: self.$store.state.token,id_tipe_progress: 'PRG03', nim_mahasiswa: this.$route.params.nim},
        success: function(response){
          self.progress = response.progress;
          console.log(response);
        }
      })
    },
  },
  //components life cycle hook
  created: function(){
    this.get_progress();
  }
}
</script>

<style lang="css" scoped>
</style>



