<template lang="html">
  <transition name="fade" mode="out-in">
    <Spin v-if="ajax_fetch_data" size="large"></Spin>
    <div v-else class="layout-content-main">
      <div class="">
        <Card :bordered="false">
          <p slot="title">Data Tesis</p>
          <a slot="extra">tes</a>
          <card-tesis v-for="(item,index) in tesis" :key="item.id_tesis" :prop_data="item"></card-tesis>
          </Card>
      </div>
    </div>
  </transition>
</template>

<script>
import CardTesis from './Card_Tesis.vue';
export default {
  name: "MahasiswaTesis",
  props: {

  },
  //variable
  data: function(){
    return{
      tesis: '',
      ajax_fetch_data: ''
    }
  },
  computed: {

  },
  components: {
    CardTesis
  },
  //methods
  watch: {

  },
  methods: {
    get_tesis: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Tesis/get_tesis",
        data: {token: self.$store.state.token,nim_mahasiswa: this.$route.params.nim},
        beforeSend: function(){
          self.ajax_fetch_data = true;
        },
        success: function(response){
          self.tesis = response.tesis;
          console.log(response);
          self.ajax_fetch_data = false;
        }
      })
    },
  },
  //components life cycle hook
  created: function(){
    this.get_tesis();
  }
}
</script>

<style lang="css" scoped>
</style>



