<template lang="html">
  <transition name="fade" mode="out-in">
    <Spin size="large" v-if="ajax_fetch_data"></Spin>
    <Row :class="[progress_visibility, progress_class]" v-else>
      <!-- item header -->
      <Col span="18">
        <router-link :to="link_detail_ujian_proposal">
          <p class="judul">
            {{ tesis_mhs.judul_tesis }}
            <Tag color="yellow">{{ tesis_mhs.nama_bidang_minat }}</Tag>
          </p>
        </router-link>
        <p class="nama-mhs">
          <Avatar class="avatar-mahasiswa" size="small">{{ inisial_nama_mhs }}</Avatar>
          {{ tesis_mhs.nama_mahasiswa }}
          <span class="pengajuan-nim-mhs">({{ tesis_mhs.nim_mahasiswa }})</span>
        </p>
        <p>
          <span>Status : </span>
          <Alert :type="tipe_alert_status_progress" show-icon class="status-sekarang">{{ status_progress_sekarang }}</Alert>
          <Alert v-if="pengajuan_ujian_proposal_ditolak" show-icon class="status-sekarang">{{ status_terakhir.ekstra_detail_status }}</Alert>
        </p>
      </Col>
      <!-- item button group -->
      <Col span="5" offset="1" style="text-align:right">
        <Button-group>
          <Button type="info" icon="chevron-down" size="small" @click="toggle_detail()">Detail</Button>
          <Button v-if="kelola_setujui" type="success" icon="checkmark-round" size="small" @click="toggle_setujui()">Setujui</Button>
          <Button v-if="kelola_verifikasi" type="warning" icon="android-warning" size="small" @click="toggle_verifikasi()">Verifikasi</Button>
          <Button v-if="kelola_tolak" type="error" icon="android-cancel" size="small" @click="toggle_tolak()">Tolak</Button>
          <Button v-if="kelola_input_nilai" type="primary" icon="document-text" size="small" @click="show_modal_ujian_proposal()">Isi Nilai</Button>
          <Button v-if="kelola_input_surat" type="success" icon="checkmark-round" size="small" @click="konfirmasi_akademik()">Setujui</Button>
        </Button-group>
      </Col>
      <!-- bagian detail progress -->
      <Row :class="[detail_visibility, detail_class]">
        <Col span="22" offset="1" class="confirm-box">
          <Col span="24" class="confirm-box-section">
            <p class="confirm-box-section-label">Informasi Tesis</p>
          </Col>
          <Col span="24">
            <Col span="9" class="col-calon-dospem">
              <p class="progress-bidang-minat">Judul Tesis</p>
              <p>{{ tesis_mhs.judul_tesis }}</p>
            </Col>
            <Col span="5" class="col-bidang-minat">
              <p class="progress-bidang-minat">Bidang Minat</p>
              <p> <Tag color="yellow">{{ tesis_mhs.nama_bidang_minat }}</Tag></p>
            </Col>
            <Col span="5" class="col-tgl-progress">
              <p class="progress-tgl">Tgl. Pengajuan</p>
              <p>{{ data_progress.tgl_progress }}</p>
            </Col>
            <Col span="5" class="col-status">
              <p class="progress-calon-dospem">Dospem</p>
              <ul>
                <li style="margin-bottom:5px;">
                  <Avatar class="avatar-dosen" size="small">{{ inisial_nama(tesis_mhs.nama_dospem_1) }}</Avatar>
                  {{ tesis_mhs.nama_dospem_1 }}
                </li>
                <li style="margin-bottom:5px;">
                  <Avatar class="avatar-dosen" size="small">{{ inisial_nama(tesis_mhs.nama_dospem_2) }}</Avatar>
                  {{ tesis_mhs.nama_dospem_2 }}
                </li>
              </ul>
            </Col>
          </Col>
          <Col span="24" class="confirm-box-section">
            <p class="confirm-box-section-label">Informasi Ujian Proposal Tesis</p>
          </Col>
          <Col span="24">
            <template v-if="ajax_fetch_data">
              <Spin size="large"></Spin>
            </template>
            <template v-else>
              <Col span="9" class="col-calon-dospem">
                <p class="progress-bidang-minat">Tanggal Ujian</p>
                <p>{{ ujian_proposal.tgl_ujian | is_kosong }}</p>
              </Col>
              <Col span="5" class="col-bidang-minat">
                <p class="progress-bidang-minat">Jam Ujian</p>
                <p>{{ ujian_proposal.waktu_mulai_ujian | is_kosong }} - {{ ujian_proposal.waktu_selesai_ujian }}</p>
              </Col>
              <Col span="5" class="col-tgl-progress">
                <p class="progress-tgl">Ruang Ujian</p>
                <p>{{ ujian_proposal.ruang_ujian | is_kosong }}</p>
              </Col>
              <Col span="5" class="col-status">
                <p class="progress-calon-dospem">Dosen Penguji</p>
                <ul>
                  <li style="margin-bottom:5px;">
                    <Avatar class="avatar-dosen" size="small">{{ inisial_nama(ujian_proposal.penguji_1) }}</Avatar>
                    {{ ujian_proposal.penguji_1 | is_kosong }}
                  </li>
                  <li style="margin-bottom:5px;">
                    <Avatar class="avatar-dosen" size="small">{{ inisial_nama(ujian_proposal.penguji_2) }}</Avatar>
                    {{ ujian_proposal.penguji_2 | is_kosong }}
                  </li>
                </ul>
              </Col>
            </template>
          </Col>
        </Col>
      </Row>
      <!-- bagian verifikasi pengajuan -->
      <Row v-if="kelola_verifikasi" type="flex" justify="center" align="middle" :class="[verifikasi_visibility, detail_class]">
        <Col span="20" class="confirm-box">
          <div class="confirm-box-inner">
            <h5>Setujui Pengajuan Proposal ini ?</h5>
            <div class="text-center" style="margin-top:20px;">
              <div class="checklist">
                <p>Harap Cek apakah mahasiswa sudah memenuhi syarat berikut</p>
                <Checkbox v-model="checklist.sertifikat">Menyerahkan foto copy sertifikat TPA dan TOEFL</Checkbox>
                <Checkbox v-model="checklist.spp">Fotokopi bukti pembayaran spp semester terakhir</Checkbox>
                <Checkbox v-model="checklist.seminar">Lembar mengikuti seminar (yang terisi minimal 10 kali keikutsertaan</Checkbox>
                <Button :disabled="!checklist_akademik_lengkap" :loading="ajax_submit" size="large" type="primary" @click="setujui_progress(1)">
                  <span v-if="!ajax_submit">Ok</span>
                  <span v-else>Loading...</span>
                </Button>
                <Button :disabled="checklist_akademik_lengkap" :loading="ajax_submit" size="large" type="error" @click="verifikasi_tolak()">
                  <span v-if="!ajax_submit">Tolak</span>
                  <span v-else>Loading...</span>
                </Button>
              </div>
            </div>
          </div>
        </Col>
      </Row>
      <!-- bagian setujui progress -->
      <Row type="flex" justify="center" align="middle" :class="[setujui_visibility, detail_class]">
        <Col span="20" class="confirm-box">
          <Col span="24" class="confirm-box-inner" id="setujui">
            <h5>Setujui Pengajuan Ujian Proposal ini ?</h5>
            <div v-if="pilih_dosen_visibility">
              <Steps :current="setujui_peganjuan_step">
                <Step title="Pilih Dosen" content="Pilih Dosen Sebagai Pengji"></Step>
                <Step title="Konfirmasi" content="Konfirmasi persetujuan"></Step>
              </Steps>
              <transition name="fade" mode="out-in">
                <!-- Step 1 -->
                <Row v-if="setujui_peganjuan_step == 0" class="pilih_dospem_box" key="0">
                  <Col span="24" class="pilih_dospem_box--right">
                    <p class="progress-calon-dospem">Pilih Dosen Penguji</p>
                    <div>
                      <span>Penguji 1 : </span>
                      <span v-if="dosen_pilihan.dosen_1 != ''">
                        <Avatar class="avatar-dosen" size="small">{{ inisial_nama(dosen_pilihan.dosen_1.nama_pegawai) }}</Avatar>
                        {{ dosen_pilihan.dosen_1.nama_pegawai }}
                      </span>
                      <Button type="primary" icon="ios-search-strong" size="small" @click="pilih_penguji(1)" v-if="dosen_pilihan.dosen_1 == ''">Pilih</Button>
                      <Button type="error" icon="close-round" size="small" @click="hapus_dosen(1)" v-else="dosen_pilihan.dosen_1 != ''">Cancel</Button>
                    </div>
                    <div>
                      <span>Penguji 2 : </span>
                      <span v-if="dosen_pilihan.dosen_2 != ''">
                        <Avatar class="avatar-dosen" size="small">{{ inisial_nama(dosen_pilihan.dosen_2.nama_pegawai) }}</Avatar>
                        {{ dosen_pilihan.dosen_2.nama_pegawai }}
                      </span>
                      <Button type="primary" icon="ios-search-strong" size="small" @click="pilih_penguji(2)" v-if="dosen_pilihan.dosen_2 == ''">Pilih</Button>
                      <Button type="error" icon="close-round" size="small" @click="hapus_dosen(2)" v-else="dosen_pilihan.dosen_2 != ''">Cancel</Button>
                    </div>
                    <div>
                      <Button long :disabled="submit_dosen_pilihan" type="primary" @click="setujui_progress(0)">Next</Button>
                    </div>
                  </Col>
                </Row>
                <!-- Step 2 -->
                <Row v-if="setujui_peganjuan_step == 1" class="pilih_dospem_confirm" key="1">
                  <h5>Setujui progress Proposal ini ?</h5>
                  <Card class="text-left">
                    <p class="judul">{{ tesis_mhs.judul_tesis }}</p>
                    <span><Tag color="yellow">{{ tesis_mhs.nama_bidang_minat }}</Tag></span>
                    <p class="nama-mhs">
                      <Avatar class="avatar-mahasiswa" size="small">{{ inisial_nama_mhs }}</Avatar>
                      {{ tesis_mhs.nama_mahasiswa }}
                      <span class="pengajuan-nim-mhs">({{ tesis_mhs.nim_mahasiswa }})</span>
                    </p>
                    <ul>
                      <li class="progress-dospem">Penguji 1 :
                        <Avatar class="avatar-dosen" size="small">{{ inisial_nama(dosen_pilihan.dosen_1.nama_pegawai) }}</Avatar>
                        {{ dosen_pilihan.dosen_1.nama_pegawai }}
                      </li>
                      <li class="progress-dospem">Penguji 2 :
                        <Avatar class="avatar-dosen" size="small">{{ inisial_nama(dosen_pilihan.dosen_2.nama_pegawai) }}</Avatar>
                        {{ dosen_pilihan.dosen_2.nama_pegawai }}
                      </li>
                    </ul>
                    <div class="text-right">
                      <Button size="large" type="ghost" @click="back_setujui_progress_ujian()">Back</Button>
                      <Button :loading="ajax_submit" size="large" type="primary" @click="setujui_progress(1)">
                        <span v-if="ajax_submit">Loading..</span>
                        <span v-else>Ok</span>
                      </Button>
                    </div>
                  </Card>
                </Row>
              </transition>
            </div>
            <div v-else>
              <div class="text-center" style="margin-top:20px;">
                <Button :loading="ajax_submit" size="large" type="primary" @click="setujui_progress(1)">
                  <span v-if="!ajax_submit">Ok</span>
                  <span v-else>Loading...</span>
                </Button>
              </div>
            </div>
          </Col>
        </Col>
      </Row>
      <!-- bagian tolak progress -->
      <Row type="flex" justify="center" align="middle" :class="[tolak_visibility, detail_class]">
        <Col span="15" class="confirm-box">
          <div class="confirm-box-inner">
            <h5>Tolak Pengajuan Ujian Proposal ini ?</h5>
            <div class="form-group">
              <label for="alasan_tolak">Alasan Penolakan</label>
              <textarea
                v-model="alasan_tolak"
                v-validate="'required'"
                name="alasan_tolak"
                id="alasan_tolak"
                :class="{'form-control': true,'is-invalid': errors.has('alasan_tolak')}"
                >
              </textarea>
              <div v-show="errors.has('alasan_tolak')" class="invalid-feedback">
                {{ errors.first('alasan_tolak') }}
              </div>
            </div>
            <Button size="large" type="error" @click="tolak_progress()">Ok</Button>
          </div>
        </Col>
      </Row>
      <Modal-Pilih-Dosen
        tipe="ujian_proposal"
        :dospem_1="tesis_mhs.id_dospem_1"
        :dospem_2="tesis_mhs.id_dospem_2"
        :modal_dosen="$store.state.modal_pilih_pembimbing.show"
        :dosen="$store.state.modal_pilih_pembimbing.dosen_pilihan"
        :row_dosen="$store.state.modal_pilih_pembimbing.row_dosen"
        v-on:pilih_dosen='pilih_dosen_pembimbing'
        v-on:modal_close="modal_close">
        </Modal-Pilih-Dosen>

        <!-- modal insert nilai ujian proposal -->
        <Modal
          v-if="ujian_proposal"
          v-model="modal_ujian_proposal.visible"
          width="50"
          :styles	="modal_ujian_proposal.styles"
          title="Input Nilai Ujian Proposal"
          @on-ok="insert_nilai_ujian_proposal"
          >
          <form>
            <!-- Pembimbing 1 -->
            <p>{{ tesis_mhs.nama_dospem_1 }} <span>(Pemibimbing 1)</span></p>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Materi</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_proposal.nilai_materi_pembimbing_1"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Penalaran</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_proposal.nilai_penalaran_pembimbing_1"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Saran Perbaikan</label>
              <div class="col-sm-7">
                <Input v-model="modal_ujian_proposal.saran_pembimbing_1" placeholder="Masukkan Saran Perbaikan"></Input>
              </div>
            </div>
            <!-- Pembimbing 2 -->
            <p>{{ tesis_mhs.nama_dospem_2 }} <span>(Pemibimbing 2)</span></p>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Materi</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_proposal.nilai_materi_pembimbing_2"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Penalaran</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_proposal.nilai_penalaran_pembimbing_2"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Saran Perbaikan</label>
              <div class="col-sm-7">
                <Input v-model="modal_ujian_proposal.saran_pembimbing_2" placeholder="Masukkan Saran Perbaikan"></Input>
              </div>
            </div>
            <!-- Penguji 1 -->
            <p>{{ ujian_proposal.penguji_1 }} <span>(Penguji 1)</span></p>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Materi</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_proposal.nilai_materi_penguji_1"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Penalaran</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_proposal.nilai_penalaran_penguji_1"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Saran Perbaikan</label>
              <div class="col-sm-7">
                <Input v-model="modal_ujian_proposal.saran_penguji_1" placeholder="Masukkan Saran Perbaikan"></Input>
              </div>
            </div>
            <!-- Penguji 2 -->
            <p>{{ ujian_proposal.penguji_2 }} <span>(Penguji 2)</span></p>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Materi</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_proposal.nilai_materi_penguji_2"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Nilai Penalaran</label>
              <div class="col-sm-7">
                <InputNumber
                  v-model="modal_ujian_proposal.nilai_penalaran_penguji_2"
                  :max="100"
                  :min="0">
                </InputNumber>
              </div>
            </div>
            <div class="form-group row">
              <label for="pembimbing_1" class="col-sm-5 col-form-label">Saran Perbaikan</label>
              <div class="col-sm-7">
                <Input v-model="modal_ujian_proposal.saran_penguji_2" placeholder="Masukkan Saran Perbaikan"></Input>
              </div>
            </div>
          </form>
          <div slot="footer">
            <Button type="primary" size="large" long :loading="ajax_submit" @click="insert_nilai_ujian_proposal">Submit</Button>
          </div>
        </Modal>
    </Row>

  </transition>
</template>
<script>
import ModalPilihDosen from './Modal-pilih-dosen.vue';
export default {
  name: 'UjianProposalCard',
  props: {
    prop_data_progress: '',
    prop_filter: '',
    prop_index: '',
  },
  data: function(){
    return {
      ajax_fetch_data: '',
      ajax_submit: false,
      data_progress: this.prop_data_progress,
      data_setujui_ubah_progress: {},//set_data_setujui_progress
      dosen_pilihan: {
        dosen_1: '',
        dosen_2: '',
      },
      checklist: {
        spp: '',
        sertifikat: '',
        seminar: '',
      },
      detail_class: 'progress-item-detail',
      detail_visibility: 'hide',
      alasan_tolak: '',
      inisial_nama_mhs: '',
      kelola_setujui: '',
      kelola_tolak: '',
      kelola_verifikasi: '',
      kelola_input_nilai: '',
      kelola_input_surat: '',
      nomer_dosen: '', //dospem
      progress_class: 'progress-item',
      progress_visibility: 'show',
      verifikasi_visibility: 'hide',
      mahasiswa: '',
      status_terakhir: '', //status_terakhir dari progress
      show_modal_dosen: false,
      setujui_peganjuan_step: 0,
      setujui_visibility: 'hide',
      tesis_mhs: '',
      tipe_progress: '', //tipe_progress
      tolak_visibility: 'hide',
      url_ajax_ubah_status: '',// url ajax tertentu (tiap proses beda)
      ujian_proposal: {},
      modal_ujian_proposal: {
        visible: false,
        nilai_materi_pembimbing_1: 50,
        nilai_penalaran_pembimbing_1: 50,
        saran_pembimbing_1: 'a',

        nilai_materi_pembimbing_2: 50,
        nilai_penalaran_pembimbing_2: 50,
        saran_pembimbing_2: 'a',

        nilai_materi_penguji_1: 50,
        nilai_penalaran_penguji_1: 5,
        saran_penguji_1: 'a',

        nilai_materi_penguji_2: 4,
        nilai_penalaran_penguji_2: 4,
        saran_penguji_2: 'a',

        nilai_akhir: '',
        styles: {
          top: '20px'
        }
      }
    }
  },
  computed: {
    checklist_akademik_lengkap: function(){
      if(this.checklist.seminar && this.checklist.sertifikat && this.checklist.spp){
        return true;
      }
      else{
        return false;
      }
    },
    link_detail_ujian_proposal: function(){
      return '/ujian-proposal/'+this.ujian_proposal.id_ujian
    },
    submit_dosen_pilihan: function(){
      if(this.dosen_pilihan.dosen_1.id_pegawai && this.dosen_pilihan.dosen_2.id_pegawai){
        return false;
      }
      else{
        return true;
      }
    },
    pilih_dosen_visibility: function(){
      if(this.$store.state.profile.pegawai){
        if(this.$store.state.profile.pegawai.tipe_pegawai == 'Kaprodi' && this.status_terakhir.id_status_progress == "SP05" || this.status_terakhir.id_status_progress == 'SUP07'){
          return true;
        }
        else{
          return false;
        }
      }
      else{
        return false;
      }
    },
    status_progress_sekarang: function(){
      if(this.status_terakhir.is_selesai == 1){
        if(this.status_terakhir.id_status_progress == 'SUP04' || this.status_terakhir.id_status_progress == 'SUP12' || this.status_terakhir.id_status_progress == 'SUP17'){
          return this.status_terakhir.nama_status_progress;
        }
        else{
          return 'Sudah '+this.status_terakhir.nama_status_progress;
        }

      }
      else if(this.status_terakhir.is_selesai == 0){
        return 'Menunggu '+this.status_terakhir.nama_status_progress;
      }
      else if(this.status_terakhir.is_selesai == 2){
        return 'Telah  '+this.status_terakhir.nama_status_progress;
      }
    },
    tipe_alert_status_progress: function(){
      if(this.status_terakhir.ekstra_detail_status != ''){
        return 'error';
      }
      else{
        if(this.status_terakhir.is_selesai == 1){
          return 'success';
        }
        else{
          return 'warning';
        }
      }
    },
    pengajuan_proposal_selesai: function(){
      if(this.status_terakhir.id_status_progress == "SUP12"){
        return true;
      }
      else{
        return false;
      }
    },
    pengajuan_ujian_proposal_ditolak: function(){
      if(this.status_terakhir.ekstra_detail_status != ''){
        return true;
      }
      else{
        return false;
      }
    },
    status_progress_baru: function(){
      if(this.tipe_progress == 'PRG02'){
        var split_status_progress = this.status_terakhir.id_status_progress.split("");
        if(split_status_progress[3] == "0"){
          var temp = parseInt(split_status_progress[4])+1
          return "SUP0"+temp;
        }
        else{
          var temp = parseInt(split_status_progress[4])+1+10;
          return "SUP"+temp;
        }
      }
      else if(this.tipe_progress == 'PRG03' || this.tipe_progress == 'PRG04'){
        var split_status_progress = this.status_terakhir.id_status_progress.split("");
        if(split_status_progress[3] == "0"){
          if(split_status_progress[4] == "9"){
            var temp = parseInt(split_status_progress[4])+1;
            return "SUP"+temp;
          }
          else{
            var temp = parseInt(split_status_progress[4])+1;
            return "SUP0"+temp;
          }
        }
        else{
          var temp = parseInt(split_status_progress[4])+1+10;
          return "SUP"+temp;
        }
      }
      else{
        return 'aw';
      }
    },
    watch_me: function(){
      return this.$store.state.modal_pilih_pembimbing.dosen_pilihan[this.prop_index];
    },
  },
  components: {
    ModalPilihDosen
  },
  watch: {
    watch_me: {
      handler: function(after,before){
        this.dosen_pilihan.dosen_1 = this.$store.state.modal_pilih_pembimbing.dosen_pilihan[this.prop_index].dosen_1;
        this.dosen_pilihan.dosen_2 = this.$store.state.modal_pilih_pembimbing.dosen_pilihan[this.prop_index].dosen_2;
      },
      deep: true,
    }
  },
  filters: {
    is_kosong(val) {
      if(val == ''){
        return "Tidak Ada";
      }
      else{
        return val;
      }
    }
  },
  methods: {
    back_setujui_progress_ujian: function(){
      this.setujui_peganjuan_step = 0;
    },
    modal_close: function(){
      this.show_modal_dosen = false;
    },
    inisial_nama: function(nama){
      var split_nama = nama.split(" ");
      if(split_nama.length > 1){
        return split_nama[0].charAt(0)+split_nama[1].charAt(0);
      }
      else{
        return split_nama[0].charAt(0)
      }
    },
    kelola_data_progress: function(){
      if(this.$store.state.profile.pegawai){
        if(this.$store.state.profile.pegawai.id_pegawai == this.status_terakhir.id_pegawai && this.status_terakhir.is_selesai == 0){
          //untuk akademik insert nilai
          if(this.$store.state.profile.pegawai.tipe_pegawai == 'Akademik'){
            if(this.status_terakhir.id_status_progress == 'SUP16'){
              this.kelola_setujui = false;
              this.kelola_input_nilai = true;
            }
            // else if(this.status_terakhir.id_status_progress == 'SUP17') {
            //   this.kelola_setujui = false;
            //   this.kelola_input_surat = true;
            // }
            else if(this.status_terakhir.id_status_progress == 'SUP17') {
              this.kelola_setujui = false;
            }
            else{
              this.kelola_verifikasi = true;
              this.kelola_setujui = false;
            }
          }
          else{
            this.kelola_setujui = true;
            this.kelola_input_nilai = false;
            this.kelola_input_surat = false;
            this.kelola_verifikasi = false;
          }
          // dosen pembimbing bisa menolak persetujuan ujian proposal
          if(this.status_terakhir.id_status_progress == 'SUP01' || this.status_terakhir.id_status_progress == 'SUP02' || this.status_terakhir.id_status_progress == 'SUP03'){
            this.kelola_tolak = true;
          }
          else{
            this.kelola_tolak = false;
          }
        }
        else{
          this.kelola_setujui = false;
          this.kelola_tolak = false;
          this.kelola_input_nilai = false;
          this.kelola_input_surat = false;
        }
      }
      else{
        this.kelola_setujui = false;
        this.kelola_tolak = false;
      }
    },
    get_mahasiswa: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Mahasiswa/get_mahasiswa",
        data: {token: self.$store.state.token,nim_mahasiswa: self.data_progress.nim_mahasiswa},
        beforeSend: function(){
          this.ajax_fetch_data = true;
        },
        success: function(response){
          self.mahasiswa = response.mahasiswa[0];
          self.set_inisial_nama();
          self.get_tesis();
          console.log(response);
          this.ajax_fetch_data = false;
        }
      })
    },
    get_status_progress: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ubah_Status_Progress/get_ubah_status_progress",
        data: {token: self.$store.state.token,id_progress: self.data_progress.id_progress},
        beforeSend: function(){
          self.ajax_fetch_data = true;
        },
        success: function(response){
          var ubah_status_progress = response.ubah_status_progress;
          ubah_status_progress.forEach(function(val,index,item){
            if(ubah_status_progress[index].is_selesai == 0){
              if(!self.status_terakhir){
                self.status_terakhir = ubah_status_progress[index];
              }
            }
            else if(ubah_status_progress[index].is_selesai == 1){
              if(!self.status_terakhir){
                self.status_terakhir = ubah_status_progress[index];
              }
            }
            else if(ubah_status_progress[index].is_selesai == 2){
              self.status_terakhir = ubah_status_progress[index];
            }
          });
          self.ajax_fetch_data = false;
          self.kelola_data_progress();
          console.log(response);
        }
      })
    },
    get_tesis: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Tesis/get_tesis",
        data: {token: self.$store.state.token,nim_mahasiswa: self.mahasiswa.nim_mahasiswa},
        success: function(response){
          self.tesis_mhs = response.tesis[0];
          console.log(response);
          self.get_ujian_proposal();
        }
      })
    },
    get_ujian_proposal: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ujian/get_ujian",
        data: {token: self.$store.state.token,id_tesis: self.tesis_mhs.id_tesis,tipe_ujian: 'UP'},
        beforeSend: function(){
          self.ajax_fetch_data = true;
        },
        success: function(response){
          self.ujian_proposal = response.ujian[0];
          if(self.ujian_proposal.penguji_1 != ''){
            let penguji_1 = self.ujian_proposal.penguji_1[0].nama_pegawai;
            self.ujian_proposal.penguji_1 = penguji_1;
          }
          else{
            self.ujian_proposal.penguji_1 = 'Belum Ada';
          }
          if(self.ujian_proposal.penguji_2 != ''){
            let penguji_2 = self.ujian_proposal.penguji_2[0].nama_pegawai;
            self.ujian_proposal.penguji_2 = penguji_2;
          }
          else{
            self.ujian_proposal.penguji_2 = 'Belum Ada';
          }
          self.ajax_fetch_data = false;
        }
      })
    },
    konfirmasi_akademik: function(){
      this.data_setujui_ubah_progress.tipe = 'ujian-proposal';
      this.data_setujui_ubah_progress.token = this.$store.state.token;
      this.data_setujui_ubah_progress.id_progress = this.data_progress.id_progress;
      this.data_setujui_ubah_progress.id_status_progress_baru = this.status_progress_baru;
      this.data_setujui_ubah_progress.id_ubah_status_progress = this.status_terakhir.id_ubah_status_progress;
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ujian/konfirmasi_akademik",
        data: self.data_setujui_ubah_progress,
        beforeSend: function(){
          self.ajax_submit = true;
        },
        success: function(response){
          if(response.status == 'berhasil'){
            self.$Notice.success({
              title: 'Konfirmasi Akadmeik Telah Disetujui',
            });
            // self.setujui_visibility = 'hide';
            self.status_terakhir= '';
            self.get_status_progress();
            self.kelola_setujui = false;
            self.kelola_tolak = false;
            self.kelola_input_nilai = false;
            self.kelola_input_surat = false;
            self.ajax_submit = false;
          }
        }
      })
    },
    setujui_progress: function(index){
      this.set_data_setujui_ubah_progress();
      if(index == 0){
        this.setujui_peganjuan_step = 1;
      }
      else if(index == 1){
        var self = this;
        $.ajax({
          type: 'POST',
          url: self.url_ajax_ubah_status,
          data: self.data_setujui_ubah_progress,
          beforeSend: function(){
            self.ajax_submit = true;
          },
          success: function(response){
            console.log(response);
            if(response.status == 'berhasil'){
              self.ajax_submit = false;
              self.$Notice.success({
                title: 'Progress Proposal Telah Disetujui',
              });
              self.setujui_visibility = 'hide';
              self.status_terakhir= '';
              self.get_status_progress();
              self.kelola_setujui = false;
              self.kelola_tolak = false;
              self.$router.push('/ujian-proposal/pengajuan-ujian-proposal');
            }
          }
        })
      }
    },
    tolak_progress: function(){
      this.set_data_setujui_ubah_progress();
      this.data_setujui_ubah_progress.alasan_tolak = this.alasan_tolak;
      this.data_setujui_ubah_progress.id_status_progress = this.status_terakhir.id_status_progress;
      var self = this;
      if(this.alasan_tolak != ''){
        $.ajax({
          type: 'POST',
          url: "https://simt.000webhostapp.com/index.php/Ujian/tolak_persetujuan_ujian/",
          data: self.data_setujui_ubah_progress,
          success: function(response){
            console.log(response);
            if(response.status == 'berhasil'){
              self.$Notice.success({
                title: 'Persetujuan Ujian Proposal Telah Ditolak',
              });
              self.tolak_visibility = 'hide';
              self.status_terakhir= '';
              self.get_status_progress();
              self.kelola_setujui = false;
              self.kelola_tolak = false;
            }
          }
        })
      }
      else{
        this.$Message.error('Harap Isi Alasan Penolakan');
      }
    },
    verifikasi_tolak: function(){
      var self = this;
      var i = 0;
      for(const prop in this.checklist){
        if(this.checklist[prop] == false){
          i=i+1;
          this.alasan_tolak = this.alasan_tolak+` ${i}`+". Syarat "+prop+" tidak memenuhi,";
        }
      }
      this.tolak_pengajuan();
    },
    pilih_penguji: function(index){
      this.$store.commit('set_index_modal',this.prop_index);
      //eksperiment
      // this.$store.commit('push_modal_dosen_pembimbing');
      // this.$store.commit('set_tipe_modal','ujian_proposal');
      this.$store.commit('pilih_dosen_pembimbing_ke',index);
      this.$store.commit('open_modal_pilih_pembimbing');
      // this.set_row_dosen();
      // this.show_modal_dosen = true;
      // this.nomer_dosen = index;
    },
    hapus_dosen: function(index){
      this.$store.commit('pilih_dosen_pembimbing_ke',index);
      this.$store.commit('hapus_dosen_pembimbing');
      // eksperiment
      if(index == 1){
        this.dosen_pilihan.dosen_1 = '';
      }
      else if(index == 2){
        this.dosen_pilihan.dosen_2 = '';
      }
      // console.log(this.dosen_pilihan);
    },
    pilih_dosen_pembimbing: function(dosen){
      alert('aaa');
      if(this.nomer_dosen == 1){
        this.dosen_pilihan.dosen_1 = dosen;
      }
      else if(this.nomer_dosen == 2){
        this.dosen_pilihan.dosen_2 = dosen;
      }
      this.show_modal_dosen = false;
      // console.log(this.dosen_pilihan);
    },
    hapus_dosen_pembimbing: function(nomor){
      if(nomor == 1){
        this.dosen_pilihan.dosen_1 = '';
      }
      else if(nomor == 2){
        this.dosen_pilihan.dosen_2 = '';
      }
    },
    //set
    set_data_setujui_ubah_progress: function(){
      if(this.data_progress.id_tipe_progress == 'PRG03'){
        if(this.status_terakhir.id_status_progress == 'SUP07'){
          this.data_setujui_ubah_progress.id_penguji_1 = this.dosen_pilihan.dosen_1.id_pegawai;
          this.data_setujui_ubah_progress.id_penguji_2 = this.dosen_pilihan.dosen_2.id_pegawai;
        }
        else{
          this.data_setujui_ubah_progress.id_penguji_1 = this.ujian_proposal.id_penguji_1;
          this.data_setujui_ubah_progress.id_penguji_2 = this.ujian_proposal.id_penguji_2;
        }
      }
      this.data_setujui_ubah_progress.tipe = 'ujian-proposal';
      this.data_setujui_ubah_progress.id_tesis = this.tesis_mhs.id_tesis;
      this.data_setujui_ubah_progress.token = this.$store.state.token;
      this.data_setujui_ubah_progress.id_progress = this.data_progress.id_progress;
      this.data_setujui_ubah_progress.id_status_progress_baru = this.status_progress_baru;
      this.data_setujui_ubah_progress.id_ubah_status_progress = this.status_terakhir.id_ubah_status_progress;
      this.data_setujui_ubah_progress.id_dospem_1 = this.tesis_mhs.id_dospem_1;
      this.data_setujui_ubah_progress.id_dospem_2 = this.tesis_mhs.id_dospem_2;
      this.data_setujui_ubah_progress.nim_mahasiswa = this.tesis_mhs.nim_mahasiswa;
    },
    set_inisial_nama: function(){
      var split_nama = this.mahasiswa.nama_mahasiswa.split(" ");
      if(split_nama[1]){
        this.inisial_nama_mhs = split_nama[0].charAt(0)+split_nama[1].charAt(0);
      }
      else{
        this.inisial_nama_mhs = split_nama[0].charAt(0);
      }
    },
    set_tipe_progress: function(){
      if(this.data_progress.id_tipe_progress == "PRG02"){
        this.tipe_progress = 'PRG02';
        this.url_ajax_ubah_status = 'https://simt.000webhostapp.com/index.php/Ujian/setujui_persetujuan_ujian';
      }
      else if(this.data_progress.id_tipe_progress == "PRG03"){
        this.tipe_progress = 'PRG03';
        this.url_ajax_ubah_status = 'https://simt.000webhostapp.com/index.php/Ujian/setujui_pengajuan_ujian';
      }
      else if(this.data_progress.id_tipe_progress == "PRG04"){
        this.tipe_progress = 'PRG04';
      }
    },
    show_modal_ujian_proposal: function(){
      this.modal_ujian_proposal.visible = true;
    },
    insert_nilai_ujian_proposal: function(){
      if(this.modal_ujian_proposal.saran_pembimbing_1 != '' &&
         this.modal_ujian_proposal.saran_pembimbing_2 != '' &&
         this.modal_ujian_proposal.saran_penguji_1 != '' &&
         this.modal_ujian_proposal.saran_penguji_2 != ''
       ){
         this.modal_ujian_proposal.nilai_akhir = (this.modal_ujian_proposal.nilai_materi_pembimbing_1+
                                                 this.modal_ujian_proposal.nilai_materi_pembimbing_2+
                                                 this.modal_ujian_proposal.nilai_materi_penguji_1+
                                                 this.modal_ujian_proposal.nilai_materi_penguji_2+
                                                 this.modal_ujian_proposal.nilai_penalaran_pembimbing_1+
                                                 this.modal_ujian_proposal.nilai_penalaran_pembimbing_2+
                                                 this.modal_ujian_proposal.nilai_penalaran_penguji_1+
                                                 this.modal_ujian_proposal.nilai_penalaran_penguji_2)/8;
         var self = this;
         $.ajax({
           type: 'POST',
           url: "https://simt.000webhostapp.com/index.php/Ujian/insert_nilai_ujian",
           data: {
             token: this.$store.state.token,
             tipe: 'ujian-proposal',
             nilai: this.modal_ujian_proposal,
             penguji_1: this.ujian_proposal.id_penguji_1,
             penguji_2: this.ujian_proposal.id_penguji_2,
             pembimbing_1: this.tesis_mhs.id_dospem_1,
             pembimbing_2: this.tesis_mhs.id_dospem_2,
             id_ujian: this.ujian_proposal.id_ujian
           },
           beforeSend: function(){
             self.ajax_submit = true;
           },
           success: function(response){
             self.konfirmasi_akademik();
             self.ajax_submit = false;
             self.modal_ujian_proposal.visible = false;
             if(response.status == 'berhasil'){
               self.$Notice.success({
                 title: 'Insert Nilai Ujian Proposal berhasil',
               });
               self.get_ujian_proposal();
             }
           }
         })
       }
       else{
         this.$Message.error('Harap Isi seluruh inputan form');
       }
    },
    //fungsi toggle animasi hide/show
    toggle_progress_ujian_proposal: function(){
      if(this.prop_filter == 'all'){
        this.progress_ujian_proposal_visibility = 'show';
      }
      else if(this.prop_filter == 'new'){
        if(this.status_terakhir.is_selesai == 0){
          this.progress_ujian_proposal_visibility = 'show';
        }
        else{
          this.progress_ujian_proposal_visibility = 'hide';
        }
      }
    },
    toggle_detail: function(){
      if(this.detail_visibility == 'show'){
        this.detail_visibility = 'hide';
      }
      else{
        this.tolak_visibility = 'hide';
        this.setujui_visibility = 'hide';
        this.verifikasi_visibility = 'hide';
        this.detail_visibility = 'show';
      }
    },
    toggle_setujui(){
      if(this.setujui_visibility == 'show'){
        this.setujui_visibility = 'hide'
      }
      else{
        this.tolak_visibility = 'hide';
        this.detail_visibility = 'hide';
        this.verifikasi_visibility = 'hide';
        this.setujui_visibility = 'show';
      }
    },
    toggle_tolak(){
      if(this.tolak_visibility == 'show'){
        this.tolak_visibility = 'hide';
      }
      else{
        this.setujui_visibility = 'hide';
        this.detail_visibility = 'hide';
        this.verifikasi_visibility = 'hide';
        this.tolak_visibility = 'show';
      }
    },
    toggle_verifikasi: function(){
      if(this.verifikasi_visibility == 'show'){
        this.verifikasi_visibility = 'hide';
      }
      else{
        this.verifikasi_visibility = 'show';
        this.detail_visibility = 'hide';
      }
    },
  },
  created: function(){
    this.get_mahasiswa();
    this.get_status_progress();
    this.set_tipe_progress();
  },
}
</script>
