<template lang="html">
  <transition name="fade" mode="out-in">
    <div class="layout-content-main">
      <Row>
        <Col span="12" class="column">
          <div class="column-inner">
            <Spin v-if="ajax_fetch.pengajuan_proposal" fix size="large"></Spin>
            <div v-else>
              <p class="text-label">Judul Tesis</p>
              <p class="text-content">{{ pengajuan_proposal.judul_tesis }}</p>
              <p class="text-label">Bidang Minat</p>
              <p class="text-content">{{ pengajuan_proposal.nama_bidang_minat }}</p>
              <p class="text-label">Tanggal Pengajuan</p>
              <p class="text-content">{{ pengajuan_proposal.tgl_progress }}</p>
              <p class="text-label">Calon Dosen Pembimbing</p>
              <p v-for="calon_dospem in pengajuan_proposal.calon_dospem" style="margin-bottom:10px;">
                <Avatar class="avatar" size="small">{{ inisial_nama(calon_dospem.nama_pegawai) }}</Avatar>
                <span>{{ calon_dospem.nama_pegawai }}</span>
              </p>
              <p class="text-label">Dosen Pembimbing</p>
              <p v-for="dospem in pengajuan_proposal.dospem" style="margin-bottom:10px;">
                <Avatar class="avatar" size="small">{{ inisial_nama(dospem.nama_pegawai) }}</Avatar>
                <span>{{ dospem.nama_pegawai }}</span>
              </p>
            </div>
          </div>
        </Col>
        <Col span="12" class="column">
          <Col span="24" class="column-inner">
            <Spin v-if="ajax_fetch.pengajuan_proposal" size="large"></Spin>
            <template v-else>
              <div v-if="pengajuan_proposal == ''" class="tidak-ada-data">
                <div style="text-align:center">
                  <Icon type="document-text"></Icon>
                  <br>
                  <span>Data tidak ditemukan</span>
                </div>
              </div>
              <div v-else>
                <Spin v-if="ajax_fetch.timeline_pengajuan_proposal" size="large"></Spin>
                <Timeline v-else>                  
                  <template v-for="(item,index) in timeline_pengajuan_proposal">
                      <TimelineItem>
                        <p class="time">{{ format_tanggal(item.tgl_progress) }}</p>
                        <p class="content">{{ item.nama_status_progress }}</p>
                      </TimelineItem>
                      <TimelineItem v-if="index == timeline_pengajuan_proposal.length-1">
                        <p class="time">{{ format_tanggal(item.tgl_progress) }}</p>
                        <p class="content">Pengajuan Judul dan dosen Pembimbing</p>
                      </TimelineItem>
                  </template>
                </Timeline>
                <!-- <Col span="12">
                  <p class="text-label">Judul Tesis</p>
                  <p class="text-content">{{ pengajuan_proposal.judul_tesis }}</p>
                  <p class="text-label">Bidang Minat</p>
                  <p class="text-content">{{ pengajuan_proposal.nama_bidang_minat }}</p>
                  <p class="text-label">Tanggal Pengajuan</p>
                  <p class="text-content">{{ pengajuan_proposal.tgl_progress }}</p>
                  <p class="text-label">Pilihan Dosen Pembimbing</p>
                  <p v-for="(item,index) in pengajuan_proposal.calon_dospem" style="margin-bottom:10px;">
                    <Avatar class="avatar" size="small">{{ inisial_nama(item.nama_pegawai) }}</Avatar>
                    <span>{{ item.nama_pegawai }}</span>
                  </p>
                </Col>
                <Col span="12">
                  <p class="text-label">Time Line Pengajuan</p>
                  <Spin v-if="ajax_fetch.timeline_pengajuan_proposal" size="large"></Spin>
                  <Timeline v-else>
                    <template v-for="(item,index) in timeline_pengajuan_proposal">
                        <TimelineItem>
                          <p class="time">{{ format_tanggal(item.tgl_progress) }}</p>
                          <p class="content">{{ item.nama_status_progress }}</p>
                        </TimelineItem>
                        <TimelineItem v-if="index == timeline_pengajuan_proposal.length-1">
                          <p class="time">{{ format_tanggal(item.tgl_progress) }}</p>
                          <p class="content">Pengajuan Judul dan dosen Pembimbing</p>
                        </TimelineItem>
                    </template>
                  </Timeline>
                </Col> -->
              </div>
            </template>
          </Col>
        </Col>
      </Row>
    </div>
  </transition>
</template>

<script>
export default {
  name: "MasiswaDetailProposal",
  props: {

  },
  data: function(){
    return{
      ajax_fetch: {
        tesis: '',
        progress: '',
        pengajuan_proposal: '',
        ujian_proposal: '',
        seminar_hasil: '',
        ujian_tesis: '',
        submit: false,
        timeline_pengajuan_proposal: '',
        timeline_persetujuan_ujian_proposal: '',
        timeline_pengajuan_ujian_proposal: '',
      },
      nim: '',
      id_pengajuan_proposal: '',
      pengajuan_proposal: '',
      progress_pengajuan_proposal: '',
      progress_persetujuan_ujian_proposal: '',
      progress_pengajuan_ujian_proposal: '',
      pilih_time_line: 1,
      tab: 0,
      tesis: '',

      timeline_pengajuan_proposal: [],
      timeline_persetujuan_ujian_proposal: [],
      timeline_pengajuan_ujian_proposal: [],
      timeline_persetujuan_seminar_hasil: [],
      timeline_pengajuan_seminar_hasil: [],
      timeline_persetujuan_ujian_tesis: [],
      timeline_pengajuan_ujian_tesis: [],

      hasil_ujian_proposal: [],
      hasil_seminar_hasil: [],
      hasil_ujian_tesis: [],
      seminar_hasil: '',
      ubah_status_progress: '',
      ujian_proposal: '',
      ujian_proposal_step: "1",
      ujian_tesis: '',
      link_ba_ujian_proposal: '/Dokumen/Ujian-Proposal/'+this.$route.params.id_tesis,
      link_ba_seminar_hasil: '/Dokumen/Seminar-Hasil/'+this.$route.params.id_tesis,
      link_ba_ujian_tesis: '/Dokumen/Ujian-Tesis/'+this.$route.params.id_tesis
    }
  },
  computed: {

  },
  components: {

  },
  //methods
  watch: {

  },
  methods: {
    // untuk set nim yg digunakan di request
    set_param: function(){
      if(this.$store.state.profile.mahasiswa){
        this.nim = this.$store.state.profile.mahasiswa.nim_mahasiswa;
      }
      else if(this.$store.state.profile.pegawai){
        this.nim = this.$route.params.nim;
      }
      let a = this.$route.path.split("/");
      if(a[1] == 'mahasiswa'){
        this.id_pengajuan_proposal = a[4];
      }
      console.log(a);
      this.get_pengajuan_proposal();
      this.get_timeline_pengajuan_proposal();
    },

    // get riwayat pengajuan judul dari tesis yang sudah ada
    get_pengajuan_proposal: function(){
      console.log('get pengajuan proposal from ajax');
      this.ajax_fetch.pengajuan_proposal = true;
      var self = this;
      let param = {};
      param.id_pengajuan = this.id_pengajuan_proposal;
      this.$store.dispatch('get_pengajuan_proposal',param).then((pengajuan_proposal) => {
        this.pengajuan_proposal = pengajuan_proposal[0];
        this.ajax_fetch.pengajuan_proposal = false;
      });
    },
    // Time Line Pengajuan Proposal
    get_timeline_pengajuan_proposal: function(){
      var self = this;
      console.log('get timeline pengajuan proposall from ajax');
      this.ajax_fetch.timeline_pengajuan_proposal = true;
      this.$store.dispatch('get_timeline_pengajuan_proposal',this.id_pengajuan_proposal).then((time_line) => {
        time_line.forEach(function(val,index,item){
          self.timeline_pengajuan_proposal.push(item[index]);
        });
        self.ajax_fetch.timeline_pengajuan_proposal = false;
      });
    },

    inisial_nama: function(nama){
      var split_nama = nama.split(" ");
      if(split_nama.length > 1){
        return split_nama[0].charAt(0)+split_nama[1].charAt(0);
      }
      else{
        return split_nama[0].charAt(0)
      }
    },
    format_tanggal: function(tanggal){
      var days = ['Minggu', 'Senin', 'Selasa', 'Rabu', 'Kamis', 'Jumat', 'Sabtu'];
      var date = new Date(tanggal);
      var month = date.getMonth() + 1;
      var day = date.getDate();
      var nama_hari = days[date.getDay()]
      var year = date.getFullYear();
      return nama_hari+", "+day+"-"+month+"-"+year;
    },
    pindah_tab: function(name){
      this.tab = name;
    },
    show_modal: function(nama_modal){
      if(nama_modal == 'ujian-proposal'){
        this.modal.ujian_proposal.visible = true;
      }
      else if(nama_modal == 'seminar-hasil'){
        this.modal.seminar_hasil.visible = true;
      }
      else if(nama_modal == 'ujian-tesis'){
        this.modal.ujian_tesis.visible = true;
      }
    }
  },
  //components life cycle hook
  created: function(){
    this.set_param();
  }
}
</script>

<style lang="css" scoped>
  .ivu-input-number{
    width: 100%;
  }
  .column{
    padding: 5px;
    position: relative;
  }
  .column-inner{
    padding: 15px;
    background-color: white;
  }
  .text-label{
    font-weight: 500;
    font-size: 16px;
    margin-top: 5px;
    margin-bottom: 5px;
  }
  .text-label select{
    font-size: 14px;
    border-top: none;
    border-right: none;
    border-left: none;
    border-bottom-color: #dddee1;
  }
  .text-label select option{
    font-size: 14px;
  }
  .text-content{
    padding: 5px 5px 3px 10px;
    line-height: 1.5;
    border-left: 4px solid #ddd;
    margin-bottom: 15px;
    color: #666;
    font-size: 14px;
  }
  .avatar{
    color: white;
    top: 0px;
    left: 0px;
    margin-right: 5px;
    background-color: #19be6b;
  }
  .time{
    font-size: 14px;
  }
  .content{
    font-weight: 400;
  }
  .tidak-ada-data{
    height: 200px;
    display:flex;
    justify-content:center;
    align-items:center;
    font-size: 16px;
  }
  .tidak-ada-data i{
    font-size: 35px;
  }
  .dokumen-hasil-ujian i{
    font-size: 18px;
  }
  .dokumen-hasil-ujian{
    font-size: 14px;
    color: #2d85e4;
  }
  /*override */
  .ivu-collapse-item{
    font-size: 14px;
  }
  .ivu-form-item{
    margin-bottom: 0px;
  }
  .a .ivu-modal{
    top: 50px !important;
  }
  .ivu-modal-body p{
    font-size: 16px;
  }
  .ivu-modal-body p span{
    font-size: 14px;
    color: #5b6270;
  }
  .ivu-form .ivu-form-item-label{
    font-size: 14px !important;
  }
</style>



