<template lang="html">
  <div class="layout-content-main">
    <div class="">
      <Card :bordered="false">
        <p slot="title">Pengajuan Seminar Hasil</p>        
        <Spin v-if="ajax_fetch_data"></Spin>
        <template v-else>
          <div v-if="seminar_hasil.length == 0" class="tidak-ada-data">
            <div style="text-align:center">
              <Icon type="document-text"></Icon>
              <br>
              <span>Data pengajuan judul tidak ditemukan</span>
            </div>
          </div>
          <seminar-hasil-card
          v-else
          v-for="(item,index) in seminar_hasil"
          :key="item.id_seminar_hasil"
          :prop_index="index"
          :prop_data_progress="item">
          </seminar-hasil-card>
        </template>
        </Card>
    </div>
  </div>
</template>

<script>
import SeminarHasilCard from './Seminar_Hasil_Card.vue';
export default {
  name: 'SeminarHasilPengajuan',
  props: {

  },
  //variable
  data: function(){
    return{
      ajax_fetch_data: '',
      seminar_hasil: [],
    }
  },
  computed: {

  },
  components: {
    SeminarHasilCard,
  },
  //methods
  watch: {

  },
  methods: {
    get_pengajuan_seminar_hasil: function(){
      var self = this;
      $.ajax({
        type: 'POST',
        url: "https://simt.000webhostapp.com/index.php/Ujian/get_progress_ujian",
        data: {token: self.$store.state.token,id_tipe_progress: 'PRG06'},
        success: function(response){
          self.seminar_hasil = response.progress;
          console.log(response);
          self.seminar_hasil.forEach(function(){
            self.$store.commit('push_modal_dosen_pembimbing');
          });
        }
      })
    }
  },
  //components life cycle hook
  created: function(){
    this.get_pengajuan_seminar_hasil();
  }
}
</script>

<style lang="css">
</style>



